import instance from './../instance'
const axios = instance('/c/cm')

export default class ClaimsManagerServices {
  static getBundleCount () {
    return axios({
      url: `/payment-requests/submitted/count`,
      method: 'GET'
    })
  }

  static getSubmittedPaymentRequest (params) {
    return axios({
      url: `/payment-requests/submitted`,
      method: 'GET',
      params
    })
  }

  static getBatch (params) {
    const { bundle_number, batch_number } = params
    return axios({
      url: `/payment-requests/${bundle_number}/${batch_number}`,
      method: 'GET'
    })
  }

  static getApprovedClaims (params) {
    const { bundle_number, batch_number } = params
    return axios({
      url: `/payment-requests/${bundle_number}/${batch_number}/claims`,
      method: 'GET'
    })
  }

  static overrideClaims (params) {
    const { bundle_number, batch_number, ...others } = params
    return axios({
      url: `/payment-requests/${bundle_number}/${batch_number}/claims/override`,
      method: 'POST',
      params: { others }
    })
  }
}
