import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'

// Components
import MemberLookup from './ClientMemberLookup'

// Styles
import classes from './ClientMemberModal.module.scss'

export default function ClientMemberModal (props) {
  // Variables
  const { show, title = '' } = props
  // Services
  // Events
  // Callbacks
  const { onClose } = props
  // Local Variables

  return (
    <Dialog
      fullWidth
      maxWidth='md'
      onClose={onClose}
      aria-labelledby='customized-dialog-title'
      open={show}
      className={classes.root}
      PaperProps={{ style: { display: 'flex' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <Typography variant='h6' className={classes.title}>
            {title}
          </Typography>
          <IconButton aria-label='close' className={classes.button} onClick={onClose}>
            <CloseIcon />
          </IconButton>
        </div>
      </DialogTitle>

      <DialogContent dividers className={classes.content}>
        <MemberLookup {...props} />
      </DialogContent>
    </Dialog>
  )
}
