import constants from './../../contants'

const initialValue = '--'

const utilizationReducer = (state = initialValue, action) => {
  if (action.for !== constants.for.CLAIMS_ANALYSIS) return state

  switch (action.type) {
    case 'SET_LIMIT_TYPE':
      return (state = action.payload)

    case 'RESET_LIMIT_TYPE':
      return initialValue

    default:
      return state
  }
}

export default utilizationReducer
