import React from 'react'
import { Switch, Route } from 'react-router-dom'

// Pages of Batch Encoder
import BundlePage from './../Components/BatchEncoder/BundlePage/BundlePage'
import NewBundlePage from './../Components/BatchEncoder/NewBundlePage/NewBundlePage'
import UploadSoaPage from './../Components/BatchEncoder/UploadSoaPage/UploadSoaPage'
// import FieldMapper from '../Components/FieldMapper/FieldMapper'
import TransmittalReport from './../Components/TransmittalReport/TransmittalReport'
import ClaimsUtilLedgerReport from './../Components/ClaimsUtilLedgerReport/ClaimsUtilLedgerReport'

// Pages of Batch Encoder Supervisor
import Dashboard from './../Components/BatchEncoderSupervisor/Dashboard/Dashboard'
import CreatedBundlesPage from './../Components/BatchEncoderSupervisor/CreatedBundlesPage/CreatedBundlesPage'

// Pages of Recon User
import ClaimsViewer from './../Components/ClaimsViewer'

// Services
import { SideBarService } from 'SharedComponents/SideBarComponent/SideBarComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import ClaimsReconServices from 'Services/Api/ClaimsReconServices'

const sideBarItems = [
  {
    label: 'Dashboard',
    icon: 'home',
    to: '/'
  },
  {
    label: 'Reports',
    icon: 'chart-bar',
    type: 'popover',
    child: [
      { label: 'Generate Utilization Ledger', to: '/util-ledger' },
      { label: 'Transmittal Report', to: '/transmittal-report' }
    ]
  }
]

const breadcrumbs = {
  dashboard: {
    text: 'Dashboard',
    to: '/'
  },
  specificBundle: {
    text: data => `Bundle No. ${data}`,
    to: data => `/bundles/${data}`
  },
  newBundle: {
    text: 'Create new bundle',
    to: '/bundles/create'
  },
  uploadSoa: {
    text: 'Upload SOA',
    to: ''
  },
  createdBundles: {
    text: data => `Created Bundles by Batch Encoder ${data}`,
    to: data => `/batch-encoder-work/${data}`
  },
  claims: {
    text: 'Claims',
    to: '/claims'
  },
  specificClaim: {
    text: data => data,
    to: data => `/claims/${data}`
  },
  recon: {
    text: 'Recon',
    to: ''
  },
  reports: {
    text: 'Reports'
  },
  transmittal: {
    text: 'Transmittal Report'
  }
}

const permissions = {
  base: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  diagnosis: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  procedures: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  hospital_items: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  professional_fees: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  tabs: {
    benefits: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    partner: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    utilization: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    contact: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    comments: {
      create: false,
      read: true,
      update: false,
      delete: false
    }
  },
  footer: {
    visible: true,
    enabled: true,
    reassign: {
      visible: true,
      enabled: true,
      options: { roles: ['CLAIMS_VERIFIER_SUPERVISOR'] }
    },
    analysis_sheet: { visible: true, enabled: true, options: {} },
    letter_of_advice: { visible: true, enabled: true, options: {} }
  }
}

function BatchEncoderSupervisorRoutes () {
  SideBarService.setItems(sideBarItems)
  BreadcrumbsService.setItems(breadcrumbs)

  return (
    <React.Fragment>
      <Switch>
        <Route path='/' exact component={Dashboard} />
        <Route path='/bundles/:bundle_number/create' exact component={NewBundlePage} />
        <Route
          path='/bundles/upload-soa'
          exact
          render={props => <UploadSoaPage {...props} ApiService={ClaimsReconServices} />}
        />
        <Route path='/bundles/:bundle_number' exact component={BundlePage} />
        <Route path='/transmittal-report' exact render={() => <TransmittalReport />} />
        <Route path='/util-ledger' exact render={() => <ClaimsUtilLedgerReport />} />
        <Route path='/batch-encoder-work/:user_id' exact component={CreatedBundlesPage} />
        <Route path='/batch-encoder-work/:user_id/bundles/:bundle_number' exact component={BundlePage} />
        <Route
          path='/claims/recon/:claim_number'
          exact
          render={props => (
            <ClaimsViewer
              breadCrumbsList={['claims', `specificClaim:${props.match.params.claim_number}`, 'recon']}
              {...props}
              permissions={permissions}
              ApiService={ClaimsReconServices}
            />
          )}
        />
      </Switch>
    </React.Fragment>
  )
}

export default BatchEncoderSupervisorRoutes
