import React from 'react'

// Material Components
import Paper from '@material-ui/core/Paper'
import Table from 'SharedComponents/Table/Table'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'

// Shared Components
import DatePicker from 'SharedComponents/DatePicker/DatePicker'
import TextField from 'SharedComponents/TextField/CustomTextField'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'

// Services
import ArrayToCSV from 'Services/ArrayToCSV'
// TODO
// Change ClaimsService when API for Treasury is available
import ClaimsServices from 'Services/Api/ClaimsServices'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'

// Utility
import { DateFormatter } from 'Utilities/TextFormatter'

// Styles
import classes from './PRCheckReport.module.scss'

const columns = [
  { label: 'Payee Name', data: 'payee_name', search: true, match: 'any' },
  { label: 'Check Voucher No.', data: 'check_voucher_no', search: true, match: 'any' },
  { label: 'Check Voucher Date.', data: 'check_voucher_date', search: true, match: 'date' },
  { label: 'Check No.', data: 'check_no', search: true, match: 'start' },
  { label: 'Check Date', data: 'check_date', search: true, match: 'date' },
  { label: 'Bank Name', data: 'bank_name', search: true, match: 'any' },
  { label: 'Check Amount', data: 'check_amount', search: true, match: 'any' },
  { label: 'Check Release Date', data: 'check_release_date', search: true, match: 'date' },
  { label: 'FPAD Transmittal Date', data: 'fpad_transmittal_date', search: true, match: 'date' },
  { label: 'Date Received (FPAD Delivery)', data: 'date_received_fpad_delivery', search: true, match: 'date' }
]

export default function PRCheckReport (props) {
  const [reports, setReports] = React.useState({
    data: [],
    search: '',
    range: { from: new Date().toISOString(), to: new Date().toISOString(), field: 'status_date' },
    filter: {},
    orderBy: 'date_requested',
    order: true,
    page: 1,
    limit: 10,
    count: 0,
    rowsPerPageOptions: [10, 15, 20]
  })
  const [debounce, setDebounce] = React.useState(null)

  React.useEffect(() => {
    PageTitleService('PR and Check Report')
    BreadcrumbsService.setSelectedItems(['pr'])
  }, [])

  React.useEffect(() => {
    fetchReport()
    // Watch filter items
  }, [reports.search, reports.filter, reports.orderBy, reports.order, reports.limit, reports.page])

  const fetchCSV = () => {
    // TODO:
    // Change service if api becomes available
    ClaimsServices.utilizationLegder({
      search: reports.search,
      range: reports.range,
      filter: reports.filter,
      sort: `${reports.orderBy}|${reports.order ? 'asc' : 'desc'}`,
      page: reports.page,
      limit: reports.count > 0 ? reports.count : reports.rowsPerPageOptions[0]
    }).then(res => {
      const { data } = res.data
      const { results = [] } = data
      const _reports = results.map(report => ({
        ...report,
        date_requested: DateFormatter(report.date_requested),
        check_date: DateFormatter(report.check_date),
        date_received: DateFormatter(report.date_received)
      }))

      const rows = _reports.map(report => {
        return columns.map(col => {
          if (col.type === 'array') console.log(formatData({ type: col.type, source: report, field: col.data }))
          return formatData({ type: col.type, source: report, field: col.data })
        })
      })

      const header = columns.map(c => c.label)

      ArrayToCSV({ fileName: 'Treasury Transmittal Report', header, rows })
    })
  }

  const fetchReport = () => {
    // TODO:
    // Change service if api becomes available
    ClaimsServices.utilizationLegder({
      search: reports.search,
      range: reports.range,
      filter: reports.filter,
      sort: `${reports.orderBy}|${reports.order ? 'asc' : 'desc'}`,
      page: reports.page,
      limit: reports.limit
    }).then(res => {
      const { data } = res.data
      const { results = [], count = 0 } = data
      const _reports = results.map(report => ({
        ...report,
        date_requested: DateFormatter(report.date_requested),
        check_date: DateFormatter(report.check_date),
        date_received: DateFormatter(report.date_received)
      }))
      setReports(state => ({ ...state, data: _reports, count }))
    })
  }

  const formatData = ({ type = null, source, field }) => {
    let output = ''
    if (type === 'date') {
      output = DateFormatter(source[field])
    } else if (type === 'array') {
      const [root, property] = field.split('.')
      output = source[root] ? source[root].map(f => f[property]).join(`${'\r\n'}${'\r\n'}`) : ''
    } else {
      output = source[field] ? source[field] : ''
    }

    return output && output !== '' && output !== `${'\r\n'}${'\r\n'}` ? output : 'N/A'
  }

  const handleOnFilter = args => {
    const { field, value, match } = args
    setReports(state => ({ ...state, filter: { ...state.filter, [field]: { value, match } } }))
  }

  const handleOnPaginate = args => {
    setReports(state => ({ ...state, ...args }))
  }

  const handleSearchChange = search => {
    /*
     * Debounce for searching through
     * SearchTextComponent
     */
    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        setReports(state => ({ ...state, search }))
      }, 500)
    )
  }

  return (
    <Paper className={classes.root}>
      <div className={classes.header}>
        <div className={classes.content}>
          <div className={classes.filter}>
            <SearchTextComponent
              id='search'
              onChange={e => handleSearchChange(e.target.value)}
              placeholder={`Search for Payee Name`}
            />
          </div>
        </div>
      </div>
      <div className={classes.body}>
        <div className={classes.table}>
          <Table
            TableContainerProps={{
              style: { maxHeight: '60vh', overflow: 'auto', position: 'relative', minHeight: '20vh' }
            }}
            TableProps={{ stickyHeader: true }}
            head={
              <TableRow>
                {columns.map((headCell, index) => (
                  <TableCell key={`table-header-${index}`}>
                    <TableSortLabel
                      onClick={event => handleOnPaginate({ orderBy: headCell.data, order: !reports.order })}
                    >
                      {headCell.label}
                    </TableSortLabel>
                    {headCell.search && (
                      <div className={classes.input}>
                        {headCell.type && headCell.type === 'date' ? (
                          <DatePicker
                            noMargin
                            id={`${headCell.data}`}
                            onChange={event =>
                              handleOnFilter({
                                field: headCell.data,
                                value: DateFormatter(event),
                                match: headCell.match
                              })
                            }
                          />
                        ) : (
                          <TextField
                            noMargin
                            onChange={event =>
                              handleOnFilter({
                                field: headCell.data,
                                value: event.target.value,
                                match: headCell.match
                              })
                            }
                          />
                        )}
                      </div>
                    )}
                  </TableCell>
                ))}
              </TableRow>
            }
            body={
              reports.data &&
              reports.data.map((claim, claimIndex) => (
                <TableRow className={classes['table-row']} key={`table-item-${claimIndex}`}>
                  {columns.map((col, colIndex) => (
                    <TableCell key={`table-cell-${claimIndex}-${colIndex}`}>
                      {formatData({ type: col.type, source: claim, field: col.data })}
                    </TableCell>
                  ))}
                </TableRow>
              ))
            }
            pagination
            count={reports.count}
            page={reports.page}
            limit={reports.limit}
            rowsPerPageOptions={reports.rowsPerPageOptions}
            onChange={args => handleOnPaginate(args)}
          />
        </div>
      </div>
    </Paper>
  )
}
