import React from 'react'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'

// Components
import TextField from 'SharedComponents/TextField/CustomTextField'

// Styles
import classes from './ClaimsRemarks.module.scss'

// Store
import { useDispatch } from 'react-redux'
import { ClaimsEncoding as actions } from 'Pages/Protected/Claims/Store/actions'

const { setClaimBaseDetails } = actions

export default function ClaimsRemarks (props) {
  const { data } = props
  const dispatch = useDispatch()

  return (
    <Paper className={classes.root}>
      <div className={classes['header']}>
        <Typography variant='subtitle2' className={classes['header-text']}>
          Claims Remarks
        </Typography>
      </div>
      <div className={classes['input']}>
        <TextField
          multiline
          rows={8}
          id='claim-remarks'
          value={data}
          onChange={event =>
            dispatch(
              setClaimBaseDetails({
                value: event.target.value,
                field: 'claim_remarks'
              })
            )
          }
        />
      </div>
    </Paper>
  )
}
