import React from 'react'
import Paper from '@material-ui/core/Paper'
import { subscribe } from 'redux-subscriber'

// Components
import TextField from 'SharedComponents/TextField/CustomTextField'
import TextFieldCurrency from 'SharedComponents/TextFieldCurrency/TextFieldCurrency'
import Button from '@material-ui/core/Button'
import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'
import Alert from 'SharedComponents/Alert/Alert'
import ProfessionalFeeFormValidator from './ProfessionalFeeFormValidator'
import DiagnosisValidator from './DiagnosisValidator'
import DropDown from 'SharedComponents/DropDown/DropDown'

// Styles
import classes from './ProfessionalFeeForm.module.scss'

// Services
import ClaimsEncodingServices from 'Services/Api/ClaimsEncodingServices'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { ClaimsEncoding as actions } from 'Pages/Protected/Claims/Store/actions'

const {
  setProfessionalFee,
  addClaimProfessionalFee,
  updateClaimProfessionalFee,
  removeClaimProfessionalFee,
  resetSuggestionObject,
  setSuggestionObject
} = actions

export default function ProfessionalFeeForm (props) {
  const { diagnosis } = props
  const dispatch = useDispatch()

  const [openDialog, setOpenDialog] = React.useState(false)

  const professionalFee = useSelector(({ ClaimsEncoding: state }) => state.professionalFee)
  const pfSuggestions = useSelector(({ ClaimsEncoding: state }) => state.suggestions)

  const [debounce, setDebounce] = React.useState({
    pfDoctor: null
  })

  const [errors, setErrors] = React.useState({})

  React.useEffect(() => {
    const unsubscribe = subscribe('professionalFee.show', state => {
      setErrors({})
      setOpenDialog(false)
    })
    return () => unsubscribe()
  }, [])

  React.useEffect(() => {
    if (diagnosis) {
      const professionalFeeICD = professionalFee.details.diagnosis_code
      const existing = diagnosis.find(item => item.code === professionalFeeICD)
      if (!existing) dispatch(setProfessionalFee({ details: { diagnosis_code: '', diagnosis_description: '' } }))
    }
  }, [diagnosis])

  //* Doctor
  const handleOnChangeDoctor = ({ value, field, isSuggestionClick }) => {
    dispatch(setProfessionalFee({ details: { [field]: value } }))
    if (isSuggestionClick) {
      for (let i of pfSuggestions.pfDoctor.data) {
        if (value === i.id || value === i.full_name) {
          dispatch(
            setProfessionalFee({
              details: {
                doctor_id: i.id,
                doctor_name: i.full_name,
                specialization: i.specialization.main + ', ' + i.specialization.major
              }
            })
          )
          dispatch(resetSuggestionObject({ field: 'pfDoctor' }))
          return
        }
      }
    } else {
      dispatch(resetSuggestionObject({ field: 'pfDoctor' }))
      const { 1: by } = field.split('_')
      let _by = by === 'id' ? 'id' : 'full_name'
      getDoctorSuggestions({ value, by: _by })
    }
  }

  const getDoctorSuggestions = ({ value = '', by }) => {
    if (value.trim().length < 3) return

    clearTimeout(debounce.pfDoctor)
    setDebounce(state => ({
      ...state,
      pfDoctor: setTimeout(() => {
        if (value) {
          ClaimsEncodingServices.lookups({
            on: 'doctor-partner',
            params: { query: value, by }
          }).then(res => {
            const { data } = res.data
            let _items = []
            for (let d of data) _items.push(d[by])
            dispatch(
              setSuggestionObject({
                field: 'pfDoctor',
                suggestion: { [by]: _items, data }
              })
            )
          })
        }
      }, 500)
    }))
  }

  const handleOnChangeProfessionalFeeForm = args => {
    const { value, field } = args
    dispatch(setProfessionalFee({ details: { [field]: value } }))
  }

  const handleOnChangeDiagnosis = ({ value, field }) => {
    const _field = field.split('_')[1]
    const diagnosi = diagnosis.find(d => d[_field] === value)
    dispatch(
      setProfessionalFee({ details: { diagnosis_code: diagnosi.code, diagnosis_description: diagnosi.description } })
    )
  }

  const validate = () => {
    const baseError = ProfessionalFeeFormValidator({
      doctor_id: professionalFee.details.doctor_id,
      doctor_name: professionalFee.details.doctor_name,
      specialization: professionalFee.details.specialization,
      gross_amount: professionalFee.details.gross_amount,
      vat_amount: professionalFee.details.vat_amount,
      amount_paid_by_member: professionalFee.details.amount_paid_by_member,
      philhealth_amount_paid: professionalFee.details.philhealth_amount_paid
    })
    let diagnosisError = {}
    if (diagnosis) {
      diagnosisError = DiagnosisValidator({
        diagnosis_description: professionalFee.details.diagnosis_description || '',
        diagnosis_code: professionalFee.details.diagnosis_code || ''
      })
    }
    const error = { ...baseError.error, ...diagnosisError.error }
    console.log(error)
    setErrors(error || {})
    return !!Object.keys(error).length
  }

  const handleOnSaveProfessionalFee = () => {
    if (validate()) return

    dispatch(addClaimProfessionalFee(professionalFee.details))
    dispatch(setProfessionalFee({ show: false }))
  }

  const handleOnUpdateProfessionalFee = index => {
    if (validate()) return

    dispatch(updateClaimProfessionalFee({ index, details: professionalFee.details }))
    dispatch(setProfessionalFee({ show: false }))
  }

  const handleOnRemoveProfessionalFee = index => {
    dispatch(removeClaimProfessionalFee({ index }))
    dispatch(setProfessionalFee({ show: false }))
    setOpenDialog(false)
  }

  return (
    professionalFee.show && (
      <React.Fragment>
        <Paper className={classes.root}>
          <div className={classes.groups}>
            {diagnosis && (
              <React.Fragment>
                <div className={classes.input}>
                  <DropDown
                    id='icd10-description'
                    required
                    label='ICD10 Description'
                    items={diagnosis.map(({ description }) => ({ text: description, value: description }))}
                    value={professionalFee.details.diagnosis_description}
                    onChange={event =>
                      handleOnChangeDiagnosis({
                        field: 'diagnosis_description',
                        value: event.target.value
                      })
                    }
                    error={errors.diagnosis_description}
                  />
                </div>
                <div className={classes.input}>
                  <DropDown
                    id='icd10-code'
                    required
                    label='ICD10 Code'
                    items={diagnosis.map(({ code }) => ({ text: code, value: code }))}
                    value={professionalFee.details.diagnosis_code}
                    onChange={event =>
                      handleOnChangeDiagnosis({
                        field: 'diagnosis_code',
                        value: event.target.value
                      })
                    }
                    error={errors.diagnosis_code}
                  />
                </div>
              </React.Fragment>
            )}
          </div>
          <div className={`${classes.groups} `}>
            <div className={classes.input}>
              <AutoComplete
                id='professional-fee-medical-partner-id'
                required
                label='Medical Partner ID'
                value={professionalFee.details.doctor_id || ''}
                suggestions={pfSuggestions.pfDoctor.id}
                onChange={(value, isSuggestionClick) =>
                  handleOnChangeDoctor({ field: 'doctor_id', value, isSuggestionClick })
                }
                error={errors.doctor_id}
              />
            </div>

            <div className={classes.input}>
              <AutoComplete
                id='professional-fee-doctor-name'
                required
                label='Doctor Name'
                value={professionalFee.details.doctor_name || ''}
                suggestions={pfSuggestions.pfDoctor.full_name}
                onChange={(value, isSuggestionClick) =>
                  handleOnChangeDoctor({ field: 'doctor_name', value, isSuggestionClick })
                }
                error={errors.doctor_name}
              />
            </div>

            <div className={classes.input}>
              <TextField
                id='professional-fee-specialization'
                required
                label='Specialization'
                value={professionalFee.details.specialization}
                onChange={event =>
                  handleOnChangeProfessionalFeeForm({
                    field: 'specialization',
                    value: event.target.value
                  })
                }
                error={errors.specialization}
              />
            </div>
          </div>

          <div className={classes.groups}>
            <div className={classes.input}>
              <TextFieldCurrency
                id='professional-fee-gross-amount'
                label='Gross Amount'
                value={professionalFee.details.gross_amount}
                onChange={event =>
                  handleOnChangeProfessionalFeeForm({
                    field: 'gross_amount',
                    value: event.target.value
                  })
                }
                error={errors.gross_amount}
              />
            </div>

            <div className={classes.input}>
              <TextFieldCurrency
                id='professional-fee-vat-amount'
                label='VAT Amount'
                value={professionalFee.details.vat_amount}
                onChange={event =>
                  handleOnChangeProfessionalFeeForm({
                    field: 'vat_amount',
                    value: event.target.value
                  })
                }
                error={errors.vat_amount}
              />
            </div>

            <div className={classes.input}>
              <TextFieldCurrency
                id='professional-fee-amount-paid-by-member'
                label='Amount Paid by Member'
                value={professionalFee.details.amount_paid_by_member}
                onChange={event =>
                  handleOnChangeProfessionalFeeForm({
                    field: 'amount_paid_by_member',
                    value: event.target.value
                  })
                }
                error={errors.amount_paid_by_member}
              />
            </div>
          </div>

          <div className={classes.groups}>
            <div className={classes.input}>
              <TextFieldCurrency
                id='philhealth-amount-paid'
                label='PhilHealth Amount Paid'
                value={professionalFee.details.philhealth_amount_paid}
                onChange={event =>
                  handleOnChangeProfessionalFeeForm({
                    field: 'philhealth_amount_paid',
                    value: event.target.value
                  })
                }
                error={errors.philhealth_amount_paid}
              />
            </div>

            <div className={classes.input}>
              <TextField
                multiline
                rows={3}
                id='philhealth-remarks'
                label='PhilHealth Remarks'
                value={professionalFee.details.philhealth_remarks}
                onChange={event =>
                  handleOnChangeProfessionalFeeForm({
                    field: 'philhealth_remarks',
                    value: event.target.value
                  })
                }
              />
            </div>
          </div>

          <div className={`${classes.groups} ${classes.buttons}`}>
            {professionalFee.mode === 'add' && (
              <React.Fragment>
                <Button
                  id='professional-fee-save-button'
                  variant='contained'
                  color='primary'
                  className='button-positive'
                  onClick={handleOnSaveProfessionalFee}
                >
                  Save
                </Button>
                <Button
                  id='professional-fee-cancel-button'
                  variant='contained'
                  color='secondary'
                  onClick={() => dispatch(setProfessionalFee({ show: false }))}
                >
                  Cancel
                </Button>
              </React.Fragment>
            )}

            {professionalFee.mode !== 'add' && (
              <React.Fragment>
                <Button
                  id='professional-fee-update-button'
                  variant='contained'
                  color='primary'
                  className='button-positive'
                  onClick={() => handleOnUpdateProfessionalFee(professionalFee.index)}
                >
                  Update
                </Button>
                <Button
                  id='professional-fee-delete-button'
                  variant='contained'
                  color='secondary'
                  onClick={() => setOpenDialog(true)}
                >
                  Delete
                </Button>
              </React.Fragment>
            )}
          </div>
        </Paper>
        <Alert
          open={openDialog}
          message='Are you sure you want to delete this professional fee?'
          cancelLabel='No'
          okLabel='Yes'
          onCancel={() => setOpenDialog(false)}
          onOk={() => handleOnRemoveProfessionalFee(professionalFee.index)}
        />
      </React.Fragment>
    )
  )
}
