import React from 'react'
import { Provider } from 'react-redux'
import { Switch, Route } from 'react-router-dom'

// Pages of Medical Consultant
import Dashboard from './../Components/ClaimsReconciliation/Dashboard/Dashboard'
import ClaimsViewer from './../Components/ClaimsViewer'

// Components
import UtilizationReport from './../Components/UtilizationReport/UtilizationReport'
import PRCheckReport from './../Components/PRCheckReport/PRCheckReport'
import TreasuryTransmittalReport from './../Components/TreasuryTransmittalReport/TreasuryTransmittalReport'

// Services
import { SideBarService } from 'SharedComponents/SideBarComponent/SideBarComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import ClaimsReconServices from 'Services/Api/ClaimsReconServices'

const sideBarItems = [
  {
    label: 'Dashboard',
    icon: 'home',
    to: '/'
  },
  {
    label: 'Claims',
    icon: 'file-alt',
    to: '/claims'
  },
  {
    label: 'Reports',
    icon: 'chart-bar',
    type: 'popover',
    child: [
      { label: 'View PR and Check Details', to: '/pr-reports' },
      { label: 'View Treasury Transmittal', to: '/treasury-reports' },
      { label: 'View Account & Member Utilization', to: '/utilization' }
    ]
  }
]

const breadcrumbs = {
  dashboard: {
    text: 'Dashboard',
    to: '/'
  },
  claims: {
    text: 'Claims',
    to: '/claims'
  },
  specificClaim: {
    text: data => data,
    to: data => `/claims/${data}`
  },
  recon: {
    text: 'Recon',
    to: ''
  },
  treasury: {
    text: 'Treasury Transmittal Reports'
  },
  pr: {
    text: 'PR and Check Report'
  },
  utilization: {
    text: 'Utilization Report'
  }
}

const permissions = {
  base: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  diagnosis: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  procedures: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  hospital_items: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  professional_fees: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  tabs: {
    benefits: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    partner: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    utilization: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    contact: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    comments: {
      create: false,
      read: true,
      update: false,
      delete: false
    }
  },
  footer: {
    visible: true,
    enabled: true,
    reassign: {
      visible: true,
      enabled: true,
      options: { roles: ['CLAIMS_VERIFIER_SUPERVISOR'] }
    },
    analysis_sheet: { visible: true, enabled: true, options: {} },
    letter_of_advice: { visible: true, enabled: true, options: {} },
    remove: { visible: true }
  }
}

function ClaimsReconcilationRoutes () {
  SideBarService.setItems(sideBarItems)
  BreadcrumbsService.setItems(breadcrumbs)

  return (
    <Switch>
      <Route path='/' exact component={Dashboard} />
      <Route path='/pr-reports' exact render={() => <PRCheckReport />} />
      <Route path='/treasury-reports' exact render={() => <TreasuryTransmittalReport />} />
      <Route path='/utilization' exact render={() => <UtilizationReport />} />
      <Route
        path='/claims/recon/:claim_number'
        exact
        render={props => (
          <ClaimsViewer
            breadCrumbsList={['claims', `specificClaim:${props.match.params.claim_number}`, 'recon']}
            {...props}
            permissions={permissions}
            ApiService={ClaimsReconServices}
          />
        )}
      />
    </Switch>
  )
}

export default ClaimsReconcilationRoutes
