import { DateFormatter } from 'Utilities/TextFormatter'

export const mapData = ({ data }) => {
  return data.map(i => {
    return {
      claim_no: i.claim_no || '',
      diagnosis: i.diagnosis || i.diagnoses || [{ diagnosis: i.primary_diagnosis || '', icd10_code: i.primary_icd10_code || '' }],
      procedures: i.procedures || [], // partner_doctor_name
      provider: { facility: [i.partner_name] || '', doctor: i.doctors ? setDoctors(i.doctors, i.partner_doctor_name) : i.partner_doctor_name ? [i.partner_doctor_name] : [] },
      amount: i.total_amount || i.grand_total || 0,
      session: i.session || 1,
      date: DateFormatter(i.date_admitted) || DateFormatter(i.date_issued),
      type: i.type,
      status: i.status
    }
  })
}

const setDoctors = (doctors = [], partner_name) => {
  if (partner_name == undefined) return doctors.map(i => (i.name))
  return doctors.map(i => (i.name)).concat([partner_name])
}
