import constants from './../../contants'

export const initDiscrepancy = payload => {
  return {
    for: constants.for.CLAIMS_ANALYSIS,
    type: 'INIT_DISCREPANCY',
    payload
  }
}

export const setDiscrepancy = payload => {
  return {
    for: constants.for.CLAIMS_ANALYSIS,
    type: 'SET_DISCREPANCY',
    payload
  }
}
