import React from 'react'

// Material Components
import Alert from 'SharedComponents/Alert/Alert'
import Button from '@material-ui/core/Button'
import Paper from '@material-ui/core/Paper'
import Checkbox from '@material-ui/core/Checkbox'
import Typography from '@material-ui/core/Typography'
import FormControlLabel from '@material-ui/core/FormControlLabel'
// Shared Components
import TextField from 'SharedComponents/TextField/CustomTextField'
import DropDown from 'SharedComponents/DropDown/DropDown'
import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'

import DataPairContainer from './DataPairs/DataPairs'

// Styles
import classes from './FieldMapper.module.scss'

const claim_sources = [
  { value: 'Hospital', text: 'Hospital' },
  { value: 'Clinic', text: 'Clinic' }
]

const dropdown_source = [
  { value: 'OP', text: 'OP' },
  { value: 'PF', text: 'PF' },
  { value: 'IP', text: 'IP' }
]

const dropdown_target = [
  { value: 'In Patient', text: 'In Patient' },
  { value: 'Out Patient Procedure', text: 'Out Patient Procedure' },
  { value: 'Out Patient Consultation', text: 'Out Patient Consultation' },
  { value: 'Reimbursement', text: 'Reimbursement' },
  { value: 'Emergency', text: 'Emergency' },
  { value: 'Dental', text: 'Dental' },
  { value: 'Medicine Allowance', text: 'Medicine Allowance' },
  { value: 'APE', text: 'APE' },
  { value: 'ECU', text: 'ECU' },
  { value: 'Pharmacy', text: 'Pharmacy' },
  { value: 'PPE', text: 'PPE' },
  { value: 'Medical Supplies', text: 'Medical Supplies' },
  { value: 'Clinic Management', text: 'Clinic Management' }
]

export default function FieldMapper (props) {
  // Variables
  const { sources = dropdown_source, targets = dropdown_target, claimSources = claim_sources } = props

  // Services
  const { ApiService } = props

  // Callbacks
  const { onSaveSuccess } = props
  const { onChangeHeaderInputs } = props
  const { onChangeClaimTypeDataMapping } = props

  // Local Variables
  const [facilityTemplate, setFacilityTemplate] = React.useState({
    facility_id: '',
    facility_name: '',
    claim_source: ''
  })
  const [template, setTemplate] = React.useState({
    id: '',
    name: '',
    isDefault: false,
    fields: []
  })

  const [templates, setTemplates] = React.useState([])

  const [suggestions, setSuggestions] = React.useState({
    facility: {}
  })

  const [debounce, setDebounce] = React.useState({
    facility: null
  })

  const [dialog, setDialog] = React.useState({
    show: false,
    name: '',
    isDefault: false,
    error: {}
  })

  const [alertDialog, setAlertDialog] = React.useState({
    show: false,
    mesage: '',
    okLabel: 'Okay'
  })

  // Listeners
  React.useEffect(() => {
    getTemplates()
  }, [])

  React.useEffect(() => {
    onChangeHeaderInputs(facilityTemplate)
  }, [facilityTemplate])

  React.useEffect(() => {
    onChangeClaimTypeDataMapping(template.fields)
  }, [template])

  // Fetchers
  const getFacilitySuggestions = ({ value = '', by }) => {
    if (value.trim().length < 3) return

    clearTimeout(debounce.facility)
    setDebounce(state => ({
      ...state,
      facility: setTimeout(() => {
        if (value) {
          ApiService.lookups({
            on: 'medical-partner',
            params: { query: value, by }
          }).then(res => {
            const { data } = res.data
            let _items = []
            for (let d of data) _items.push(d[by])
            setSuggestions(state => ({ ...state, facility: { [by]: _items, data } }))
          })
        }
      }, 500)
    }))
  }

  const getTemplates = () => {
    ApiService.getTemplates().then(res => {
      const { data } = res.data
      setTemplates(data.map(data => ({ ...data, value: data.name, text: data.name })))
      const _default = data.find(d => d.is_default === true)
      if (_default) {
        setTemplate({ id: _default._id, name: _default.name, fields: _default.fields, isDefault: _default.is_default })
      }
    })
  }

  // Events
  const handleOnChangeFacility = ({ value, field, isSuggestionClick }) => {
    setFacilityTemplate(state => ({ ...state, [field]: value }))
    if (isSuggestionClick) {
      for (let i of suggestions.facility.data) {
        if (value === i.id || value === i.name) {
          setFacilityTemplate(state => ({ ...state, facility_id: i.id, facility_name: i.name }))
        }
      }
    } else {
      setSuggestions(state => ({ ...state, facility: [] }))
      const { 1: by } = field.split('_')
      getFacilitySuggestions({ value, by })
    }
  }

  const handleOnClickAddNewTemplate = () => {
    setDialog({ show: true, name: '', isDefault: false, error: {} })
  }

  const handleOnChangeTemplate = ({ value, text }) => {
    const _template = templates.find(t => t.value === value)
    setTemplate({ id: _template._id, name: _template.name, fields: _template.fields, isDefault: _template.is_default })
  }

  const handleOnChangeDialog = ({ field, value }) => {
    setDialog(state => ({ ...state, [field]: value }))
  }

  const onClickAddField = () => {
    const _fields = template.fields
    const last = _fields[_fields.length - 1]
    if (last && (last.source === '' || last.target === '')) return
    _fields.push({ source: '', target: '' })
    setTemplate(state => ({ ...state, fields: _fields }))
  }

  const onRemoveField = ({ index }) => {
    const _fields = template.fields
    _fields.splice(index, 1)
    setTemplate(state => ({ ...state, fields: _fields }))
  }

  const onChangeSource = ({ index, value }) => {
    const _fields = template.fields
    _fields[index] = { ..._fields[index], source: value }
    setTemplate(state => ({ ...state, fields: _fields }))
  }

  const onChangeTarget = ({ index, value }) => {
    const _fields = template.fields
    _fields[index] = { ..._fields[index], target: value }
    setTemplate(state => ({ ...state, fields: _fields }))
  }

  const doValidateTemplate = () => {
    if (dialog.name === '') return false

    return true
  }

  const doValidateFacilityTemplate = () => {
    if (template.fields.length < 1) return false
    template.fields.forEach(field => {
      if (field.source === '' || field.target === '') {
        return false
      }
    })

    return true
  }

  const handleOnSaveTemplateDialog = () => {
    if (!doValidateTemplate()) {
      setAlertDialog({
        show: true,
        message: 'Enter Template Name',
        okLabel: 'Okay'
      })
      return
    }

    ApiService.storeTemplate({
      data: { is_default: dialog.isDefault, fields: [] },
      template_name: dialog.name
    }).then(res => {
      const { data } = res.data
      setDialog({
        show: false,
        name: '',
        isDefault: false,
        error: {}
      })
      getTemplates()
      setTemplate({ id: data._id, name: data.name, isDefault: data.is_default, fields: data.fields })
    })
  }

  const handleOnSaveFacilityTemplate = () => {
    if (!doValidateFacilityTemplate()) {
      setAlertDialog({
        show: true,
        message:
          'SOA details and mapping of data is required. Please complete first the SOA details and mapping of data to continue.',
        okLabel: 'Okay'
      })
      return
    }

    ApiService.storeTemplate({
      data: { is_default: template.isDefault, fields: template.fields },
      template_name: template.name
    }).then(res => {
      const { data } = res.data
      ApiService.storeFacilityTemplate({
        data: {
          facility_name: facilityTemplate.facility_name,
          claim_source: facilityTemplate.claim_source,
          template_id: data._id
        },
        facility_id: facilityTemplate.facility_id
      }).then(res => {
        setAlertDialog({
          show: true,
          message: 'SOA details mapping of data successfully saved.',
          okLabel: 'Okay'
        })
        onSaveSuccess({ template, facility: facilityTemplate })
      })
    })
  }

  return (
    <Paper className={classes.root}>
      <div className={classes.section}>
        <div className={classes.groups}>
          <div className={classes['input']}>
            <AutoComplete
              id='medical-partner-id'
              required
              label='Medical Partner ID'
              value={facilityTemplate.facility_id}
              suggestions={suggestions.facility.id}
              onChange={(value, isSuggestionClick) =>
                handleOnChangeFacility({
                  field: 'facility_id',
                  value,
                  isSuggestionClick
                })
              }
            />
          </div>
          <div className={classes['input']}>
            <AutoComplete
              id='medical-partner-facility-name'
              required
              label='Medical Partner (Facility) Name'
              value={facilityTemplate.facility_name}
              suggestions={suggestions.facility.name}
              onChange={(value, isSuggestionClick) =>
                handleOnChangeFacility({
                  field: 'facility_name',
                  value,
                  isSuggestionClick
                })
              }
            />
          </div>
          <div className={classes['input']}>
            <DropDown
              id='claim-source'
              label='Claim Source'
              required
              items={claimSources}
              value={facilityTemplate.claim_source}
              onChange={event => setFacilityTemplate(state => ({ ...state, claim_source: event.target.value }))}
            />
          </div>
        </div>
      </div>
      <div className={classes.section}>
        <div className={classes.groups}>
          <Typography variant='h6' className={classes.title}>
            Data Values
          </Typography>
        </div>
        <div className={classes.groups}>
          <div className={classes['input']}>
            <DropDown
              id='data-templates'
              label='Data Mapping Template'
              required
              items={templates}
              value={template.name}
              onChange={event =>
                event.target.value !== 'add-new'
                  ? handleOnChangeTemplate({ value: event.target.value, text: event.target.text })
                  : handleOnClickAddNewTemplate()
              }
              addNew={{
                show: true,
                text: '+ Add New Data Map',
                value: 'add-new'
              }}
            />
          </div>
        </div>
      </div>
      <div className={classes.section}>
        <div className={classes.groups}>
          <div className={classes.field}>
            <div className={classes.label}>
              <Typography>Claim Type</Typography>
            </div>
            <div className={classes.table}>
              <DataPairContainer
                headers={['Values', 'Data Column']}
                sources={sources}
                targets={targets}
                fields={template.fields}
                onClickAddNew={() => onClickAddField()}
                onClickRemove={({ index }) => onRemoveField({ index })}
                onChangeSource={({ index, value }) => onChangeSource({ index, value })}
                onChangeTarget={({ index, value }) => onChangeTarget({ index, value })}
              />
            </div>
          </div>
        </div>
        <div className={classes.groups}>
          <Button
            id='save'
            variant='contained'
            color='primary'
            className='button-positive'
            onClick={handleOnSaveFacilityTemplate}
          >
            Save
          </Button>
        </div>
      </div>
      <Dialog
        fullWidth
        maxWidth='sm'
        onClose={() => setDialog(state => ({ ...state, show: false }))}
        aria-labelledby='customized-dialog-title'
        open={dialog.show}
        className={classes.dialog}
        PaperProps={{ style: { height: 'auto', display: 'flex' } }}
      >
        <DialogTitle disableTypography className={classes['header-container']}>
          <div className={classes.header}>
            <Typography variant='h6' className={classes.title}>
              Define Data Map
            </Typography>
            <IconButton
              aria-label='close'
              className={classes.button}
              onClick={() => setDialog(state => ({ ...state, show: false }))}
            >
              <CloseIcon />
            </IconButton>
          </div>
        </DialogTitle>
        <DialogContent dividers className={classes.content}>
          <div className={classes.input}>
            <TextField
              className={classes.name}
              id='data-map-name'
              value={dialog.name}
              autoFocus
              fullWidth
              placeholder='Template Name'
              onChange={event => handleOnChangeDialog({ field: 'name', value: event.target.value })}
              error={dialog.error.name}
            />
          </div>
          <div className={classes.input}>
            <FormControlLabel
              control={
                <Checkbox
                  className={classes.default}
                  id='data-map-default'
                  checked={dialog.isDefault}
                  value={dialog.isDefault}
                  onChange={event => handleOnChangeDialog({ field: 'isDefault', value: event.target.checked })}
                />
              }
              label={'Set as default template'}
            />
          </div>
          <div className={classes.footer}>
            <div className={classes.action}>
              <Button
                onClick={() => setDialog(state => ({ ...state, show: false }))}
                variant='contained'
                color='primary'
                className='button-negative'
              >
                Cancel
              </Button>
              <Button
                onClick={handleOnSaveTemplateDialog}
                variant='contained'
                color='primary'
                className='button-positive'
              >
                Save
              </Button>
            </div>
          </div>
        </DialogContent>
      </Dialog>

      <Alert
        open={alertDialog.show}
        message={alertDialog.message}
        okLabel={alertDialog.okLabel}
        onOk={() => {
          setAlertDialog(state => ({ ...state, show: false }))
        }}
      />
    </Paper>
  )
}
