import React from 'react'
import Typography from '@material-ui/core/Typography'
import { withStyles } from '@material-ui/core/styles'
import LinearProgress from '@material-ui/core/LinearProgress'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import Parser from './../../Parser'

// Style
import classes from './UploadSpace.module.scss'

const Progress = withStyles({
  root: {
    height: 15,
    borderRadius: 20,
    backgroundColor: '#c7c7c7'
  },
  bar: {
    borderRadius: 20,
    backgroundColor: '#00ab00'
  }
})(LinearProgress)

export default function UploadSpace (props) {
  const { onSelectFile } = props
  const dialogRef = React.useRef()

  const [dragged, setDragged] = React.useState(false)
  const [fileLoaded, setFileLoaded] = React.useState()
  const [progress, setProgress] = React.useState(0)

  const handleOnClick = event => {
    event.stopPropagation()
    event.preventDefault()
    dialogRef.current.click()
  }

  const handleOnChangeFile = event => {
    event.stopPropagation()
    event.preventDefault()
    const file = event.target.files[0]

    if (!file) return

    handleParse(file)
  }

  const handleOnDrag = state => e => {
    e.preventDefault()
    e.stopPropagation()
    setDragged(state)
  }

  const handleDrop = e => {
    setDragged(false)
    e.preventDefault()
    e.stopPropagation()

    // TODO if multiple file and invalid file type
    const [file] = e.dataTransfer.files

    handleParse(file)
  }

  const handleParse = file => {
    setFileLoaded(true)

    console.log(file)
    Parser({
      file,
      onComplete: ({ results, headers }) => {
        setProgress(100)
        setTimeout(() => onSelectFile({ items: results, headers, fileName: file.name }), 1000)
      }
    })
  }

  return (
    <React.Fragment>
      {!fileLoaded && (
        <div
          className={`${classes.root} ${dragged && classes.dragged}`}
          onDrop={handleDrop}
          onDragOver={handleOnDrag(true)}
          onDragLeave={handleOnDrag(false)}
        >
          <div className={`${classes.container} ${classes.clickable}`} onClick={handleOnClick}>
            <FontAwesomeIcon icon='file-upload' className={classes.icon} />
            <Typography variant='subtitle2' className={classes.label}>
              Click or drag/drop filte to preview
            </Typography>

            <div className={classes.warning}>
              <Typography variant='subtitle2' className={classes.label}>
                Make sure the dates are in the format where the
              </Typography>
              <Typography variant='subtitle2' className={classes.label}>
                <span>month is written first</span> (ex. MM/DD/YYYY or YYYY/MM/DD)
              </Typography>
            </div>
          </div>

          <input
            id='file-dialog'
            type='file'
            ref={dialogRef}
            style={{ display: 'none' }}
            accept='.xlsx, .xls, .csv'
            onChange={handleOnChangeFile}
          />
        </div>
      )}

      {fileLoaded && (
        <div className={classes.root}>
          <div className={classes.container}>
            <div className={classes['progress-container']}>
              <Progress variant='determinate' value={progress} />
            </div>
          </div>
        </div>
      )}
    </React.Fragment>
  )
}
