import React from 'react'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'
import Radio from '@material-ui/core/Radio'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'

// Components
import Table from 'SharedComponents/Table/Table'
import TextField from 'SharedComponents/TextField/CustomTextField'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import Alert from 'SharedComponents/Alert/Alert'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

// Services
import ClaimsMaintainerServices from 'Services/Api/ClaimsMaintainerServices'

// Styles
import classes from './MaintenancePage.module.scss'

// Constants
const lookups = {
  hospital_items: { on: 'hospital-item-list', field: 'hospital-item-list' },
  reason_covered: { on: 'maintenance', field: 'covered-reason' },
  reason_charging: { on: 'maintenance', field: 'charging-reason' },
  reason_return: { on: 'maintenance', field: 'return-reason' },
  reason_reject: { on: 'maintenance', field: 'reject-reason' },
  claims_source: { on: 'maintenance', field: 'claims-source' },
  claim_sub_type: { on: 'maintenance', field: 'claims-sub-type' },
  claim_sub_sub_type: { on: 'maintenance', field: 'claims-sub-sub-type' }
}
const initialValue = {
  show: false,
  title: '',
  count: 0,
  search: '',
  limit: 10,
  page: 1,
  data: [],
  orderBy: '',
  order: true,
  header: 'Hospital Item List',
  filter: {description: {value: ''}, status: {value: ''}}
}
const initialItem = {
  description: '',
  status: 'Active',
  code: '',
  active: true,
  deactivate: false
}
let columns = [
  { name: 'Description', sortField: 'description', id: 'description', match: 'any' },
  { name: 'Status', sortField: 'status', id: 'status', match: 'start' }
]
export default function MaintenancePage (props) {
  const [data, setData] = React.useState(initialValue)
  const [lookup, setLookup] = React.useState(lookups.hospital_items)
  const [debounce, setDebounce] = React.useState(null)
  const [item, setItem] = React.useState(initialItem)
  const [showAddItem, setShowAddItem] = React.useState({
    show: false,
    mode: 'add'
  })
  const [showAlert, setShowAlert] = React.useState({
    open: false,
    message: '',
    onOk: '',
    okLabel: 'Okay',
    cancelLabel: ''
  })

  React.useEffect(() => {
    PageTitleService('Claims Maintenance')
    BreadcrumbsService.setSelectedItems(['maintenance'])
  }, [])

  React.useEffect(() => {
    ClaimsMaintainerServices.lookups({ on: lookup.on, params: {...data, field: lookup.field} }).then(({ data: res }) => {
      const { results, count } = res.data
      setData(state => ({...state, data: results, count}))
    })
  }, [lookup])

  const handleOnChange = params => {
    const _params = {
      limit: data.limit,
      page: data.page,
      search: data.search,
      sort: `${data.orderBy}|${data.order ? 'desc' : 'asc'}`,
      filter: data.filter,
      field: lookup.field,
      ...params
    }

    ClaimsMaintainerServices.lookups({ on: lookup.on, params: _params }).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setData(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const isActive = args => {
    return (lookup && lookup.field && args === lookup.field ? 'active' : '')
  }

  const onClickHospitalItem = () => {
    setLookup(lookups.hospital_items)
    setData({...data, header: 'Hospital Item List', filter: {description: {value: ''}, status: {value: ''}}})
  }
  const onClickNotCoveredReason = () => {
    setLookup(lookups.reason_covered)
    setData({...data, header: 'Not Covered Reasons', filter: {description: {value: ''}, status: {value: ''}}})
  }

  const onClickChargingReason = () => {
    setLookup(lookups.reason_charging)
    setData({...data, header: 'Reasons For Charging Member', filter: {description: {value: ''}, status: {value: ''}}})
  }

  const onClickClaimSource = () => {
    setLookup(lookups.claims_source)
    setData({...data, header: 'Claim Source', filter: {description: {value: ''}, status: {value: ''}}})
  }

  const onClickReturnReason = () => {
    setLookup(lookups.reason_return)
    setData({...data, header: 'Reimbursement Return Reasons', filter: {description: {value: ''}, status: {value: ''}}})
  }

  const onClickRejectReason = () => {
    setLookup(lookups.reason_reject)
    setData({...data, header: 'Reimbursement Reject Reasons', filter: {description: {value: ''}, status: {value: ''}}})
  }

  const onClickClaimSubType = () => {
    setLookup(lookups.claim_sub_type)
    setData({...data, header: 'Claim Sub-Type', filter: {description: {value: ''}, status: {value: ''}}})
  }

  const onClickClaimSubSubType = () => {
    setLookup(lookups.claim_sub_sub_type)
    setData({...data, header: 'Claim Sub Sub-Type', filter: {description: {value: ''}, status: {value: ''}}})
  }

  const handleOnSort = orderBy => e => {
    setData(p => ({ ...p, orderBy, order: !data.order }))
    handleOnChange({ sort: `${orderBy}|${data.order ? 'asc' : 'desc'}` })
  }
  const handleColumnFiltering = args => {
    const { field, value, match } = args
    setData(p => ({ ...p, filter: { ...p.filter, [field]: { value, match } } }))
    handleOnChange({ filter: { ...data.filter, [field]: { value, match } }, page: 1 })
  }

  const handleOnSaveItem = () => {
    LoadingService.loading(true)
    const initialLookup = lookup
    const _params = {
      limit: 1,
      page: 1,
      search: {
        description: item.description,
        code: item.code
      },
      field: lookup.field
    }
    const handleOnOk = () => {
      setShowAlert(state => ({ ...state, open: false }))
    }

    if (!item.description.trim()) {
      setShowAlert(state =>
        ({
          ...state,
          open: true,
          message: 'Description details is empty. Please input the description details.',
          onOk: handleOnOk,
          okLabel: 'Okay',
          cancelLabel: ''
        }))
      LoadingService.loading(false)
    } else {
      ClaimsMaintainerServices.lookups({ on: lookup.on, params: _params }).then(({ data: res }) => {
        const { results } = res.data
        if (results.length > 0) {
          setShowAlert(state =>
            ({
              ...state,
              open: true,
              message: 'Description details already exists. Please change the description details encoded.',
              onOk: handleOnOk,
              okLabel: 'Okay',
              cancelLabel: ''
            }))
        } else {
          if (showAddItem.mode === 'add') {
            ClaimsMaintainerServices.storeLookups({ on: lookup.on, data: item, params: { field: lookup.field } }).then(res => {
              setItem(initialItem)
              setLookup(false)
              setLookup(initialLookup)
              setShowAddItem({...showAddItem, show: false})
              LoadingService.loading(false)
            })
          } else if (showAddItem.mode === 'edit') {
            ClaimsMaintainerServices.updateLookups({ on: lookup.on, data: item, code: item.code, params: { field: lookup.field } }).then(res => {
              setItem(initialItem)
              setLookup(false)
              setLookup(initialLookup)
              setShowAddItem({...showAddItem, show: false})
              LoadingService.loading(false)
            })
          }
        }
        LoadingService.loading(false)
      })
    }
  }

  const handleOnEditItem = item => {
    setShowAddItem({...showAddItem, show: true, mode: 'edit'})
    setItem({
      description: item.description,
      code: item.code,
      status: item.status,
      active: item.status === 'Active',
      deactivate: item.status === 'Inactive'
    })
  }

  const handleOnCancelItem = () => {
    if (item.description) {
      setShowAlert(state =>
        ({
          ...state,
          open: true,
          message: 'Are you sure you want to discard your changes?',
          onOk: () => {
            setShowAlert(alert => ({...alert, open: false}))
            setShowAddItem(show => ({...show, show: false}))
            setItem(item => ({...item, description: ''}))
          },
          okLabel: 'Yes',
          cancelLabel: 'No'
        }))
    } else {
      setShowAddItem({...showAddItem, show: false})
    }
  }

  return (
    <div className={classes.root}>
      <div className={classes['button-parent']}>
        <Button disabled={showAddItem.show} variant='contained' color='primary' id='hospital-item-list' className={`button-positive ${classes[isActive('hospital-item-list')]}`} onClick={onClickHospitalItem} >
          Hospital Item List
        </Button>

        <Button disabled={showAddItem.show} variant='contained' color='primary' id='not-covered-reason' className={`button-positive ${classes[isActive('covered-reason')]}`} onClick={onClickNotCoveredReason} >
          Not Covered Reason
        </Button>

        <Button disabled={showAddItem.show} variant='contained' color='primary' id='reason-for-charging' className={`button-positive ${classes[isActive('charging-reason')]}`} onClick={onClickChargingReason} >
          Reason for Charging Member
        </Button>

        <Button disabled={showAddItem.show} variant='contained' color='primary' id='claim-source' className={`button-positive ${classes[isActive('claims-source')]}`} onClick={onClickClaimSource} >
          Claim Source
        </Button>
      </div>

      <div className={classes['button-parent']}>
        <Button disabled={showAddItem.show} variant='contained' color='primary' id='reimbursement-return-reason' className={`button-positive ${classes[isActive('return-reason')]}`} onClick={onClickReturnReason} >
          Reimbursement Return Reason
        </Button>

        <Button disabled={showAddItem.show} variant='contained' color='primary' id='reimbursement-reject-reason' className={`button-positive ${classes[isActive('reject-reason')]}`} onClick={onClickRejectReason} >
          Reimbursement Reject Reason
        </Button>

        <Button disabled={showAddItem.show} variant='contained' color='primary' id='claim-sub-type' className={`button-positive ${classes[isActive('claims-sub-type')]}`} onClick={onClickClaimSubType} >
          Claim Sub-Type
        </Button>

        <Button disabled={showAddItem.show} variant='contained' color='primary' id='claim-sub-sub-type' className={`button-positive ${classes[isActive('claims-sub-sub-type')]}`} onClick={onClickClaimSubSubType} >
          Claim Sub Sub-Type
        </Button>
      </div>

      <div className={classes.header}>
        <div className={classes['header-wrapper']}>
          <span className={classes['header-text']}> {data.header} </span>
          <Button disabled={showAddItem.show} variant='contained' color='primary' id='add-item' className='button-positive' onClick={() => setShowAddItem({...showAddItem, show: true, mode: 'add'})} >
           + Add
          </Button>
        </div>
      </div>

      {showAddItem.show && <div className={classes['add-item-root']}>
        <div className={classes.wrapper}>
          <span className={classes['header-text']}>
            Description:
          </span>
          <div className={classes['text-wrapper']}>
            <TextField
              id={'description-field'}
              value={item.description}
              onChange={event => { setItem({...item, description: event.target.value}) }}
            />
          </div>
          <div className={classes['radio-buttons']}>
            <div className={classes.activate}>
              Active:
              <Radio
                color='primary'
                id={'activate'}
                checked={item.active}
                onClick={() =>
                  setItem(
                    {...item,
                      status: 'Active',
                      active: true,
                      deactivate: false})} />
            </div>
            <div className={classes.deactivate}>
              Deactivate:
              <Radio
                color='primary'
                id={'deactivate'}
                checked={item.deactivate}
                onClick={() =>
                  setItem(
                    {...item,
                      status: 'Inactive',
                      active: false,
                      deactivate: true})} />
            </div>
          </div>
          <div className={classes['button-wrapper']}>
            <Button variant='contained' color='primary' id='add-item' className='button-positive' onClick={handleOnSaveItem} >
              Save
            </Button>
            <Button variant='contained' color='primary' id='add-item' className='button-negative' onClick={handleOnCancelItem} >
              Cancel
            </Button>
          </div>
        </div>
      </div>}

      {!showAddItem.show && <div className={classes.table}>
        <Table
          TableContainerProps={{ style: { height: '100%', overflow: 'auto', position: 'relative' } }}
          TableProps={{ stickyHeader: true }}
          head={
            <React.Fragment>
              <TableRow>
                {columns.map(headCell => (
                  <TableCell key={headCell.sortField} id={`sort-${headCell.id}`}>
                    <TableSortLabel
                      active={data.orderBy === headCell.sortField}
                      direction={data.order ? 'asc' : 'desc'}
                      onClick={handleOnSort(headCell.sortField)}
                    >
                      {headCell.name}
                    </TableSortLabel>
                    <TextField
                      value={data.filter[headCell.sortField].value || ''}
                      onChange={event => {
                        const search = event.target.value
                        setData(p => ({ ...p, filter: { ...p.filter, [headCell.sortField]: { value: search, match: headCell.match } } }))
                        clearTimeout(debounce)
                        setDebounce(
                          setTimeout(() => {
                            handleColumnFiltering({
                              field: headCell.sortField,
                              value: search,
                              match: headCell.match
                            })
                          }, 500)
                        )
                      }}
                    />
                  </TableCell>
                ))}
                <TableCell key={'action'} id={`sort-action`} />
              </TableRow>
            </React.Fragment>

          }
          body={data && data.data.map((item, index) => (
            <TableRow key={index}>
              <TableCell id={`item-description-${index}`}>{item.description}</TableCell>
              <TableCell id={`item-status-${index}`}>{item.status}</TableCell>
              <TableCell align={'center'}>
                <Typography variant='caption' className={`text-link underline`} id={`submmited-pr-${index}`} onClick={() => handleOnEditItem(item)} >
                  Edit
                </Typography>
              </TableCell>
            </TableRow>
          ))}
          count={data && data.count}
          page={data && data.page}
          limit={data && data.limit}
          rowsPerPageOptions={[10, 15, 20]}
          onChange={handleOnChange}
        />
      </div>}
      <Alert
        open={showAlert.open}
        message={showAlert.message}
        okLabel={showAlert.okLabel}
        cancelLabel={showAlert.cancelLabel}
        onCancel={() => setShowAlert(alert => ({...alert, open: false}))}
        onOk={showAlert.onOk}
      />
    </div>
  )
}
