import React from 'react'
import { Button, Typography } from '@material-ui/core'

// Components
import ChatBox from '../ChatBox/ChatBox'

import CircularProgress from '@material-ui/core/CircularProgress'
import { CustomTabs, CustomTab } from 'SharedComponents/CustomTabs/CustomTabs'

// Compare Tabs
import Benefits from '../Benefits/Benefits'
import Exclusions from '../Exclusions/Exclusions'
import PartnerNetwork from '../PartnerNetwork/PartnerNetwork'
import Utilization from '../Utilization/Utilization'
import Contract from '../Contract/Contract'

// Utilities
import { SafeAccess } from 'Utilities/SafeAccess'

// Store
import { useSelector } from 'react-redux'

// Styles
import classes from './RightPanelTabs.module.scss'

export default function RightPanelTabs (props) {
  // Events
  const { onReturnEscalate, onReturnInquire = () => {} } = props
  // Variables
  const { claim, permissions, isMember, member, memberFetchStatus, client, clientFetchStatus, utilizationLoa } = props
  // Services
  const { getUtilizations, getPartners, getDoctors, getMemberContract, getClientContract } = props

  const escalations = useSelector(({ ClaimsAnalysis: state }) => state.escalations)
  const inquiries = useSelector(({ ClaimsAnalysis: state }) => state.inquiries)
  const recon = useSelector(({ ClaimsAnalysis: state }) => state.recon)

  return (
    <CustomTabs id='medical-partner-tabs' defaultIndex={0}>
      {SafeAccess(permissions, ['tabs', 'benefits', 'read']) && (
        <CustomTab id='benefits-tab' label='BENEFITS'>
          {(memberFetchStatus === 'fetching' || clientFetchStatus === 'fetching') && (
            <div className={classes.center}>
              <CircularProgress size={50} />
            </div>
          )}
          {memberFetchStatus === '404' && (
            <div className={classes.center}>
              <Typography>{`${isMember ? 'Member' : 'Client'} Not Found`}</Typography>
            </div>
          )}
          {memberFetchStatus === '500' && (
            <div className={classes.center}>
              <Typography>{'Internal Server Error'}</Typography>
            </div>
          )}
          {(memberFetchStatus === 'success' || clientFetchStatus === 'success') && (
            <Benefits isMember={isMember} member={member} client={client} />
          )}
        </CustomTab>
      )}

      {SafeAccess(permissions, ['tabs', 'exclusions', 'read']) && (
        <CustomTab id='exclusions-tab' label='EXCLUSIONS'>
          {(memberFetchStatus === 'fetching' || clientFetchStatus === 'fetching') && (
            <div className={classes.center}>
              <CircularProgress size={50} />
            </div>
          )}
          {memberFetchStatus === '404' && (
            <div className={classes.center}>
              <Typography>{`${isMember ? 'Member' : 'Client'} Not Found`}</Typography>
            </div>
          )}
          {memberFetchStatus === '500' && (
            <div className={classes.center}>
              <Typography>{'Internal Server Error'}</Typography>
            </div>
          )}
          {(memberFetchStatus === 'success' || clientFetchStatus === 'success') && (
            <Exclusions isMember={isMember} member={member} client={client} />
          )}
        </CustomTab>
      )}

      {SafeAccess(permissions, ['tabs', 'partner', 'read']) && (
        <CustomTab id='partner-network-tab' label='PARTNER NETWORK'>
          <PartnerNetwork
            partnerService={getPartners}
            doctorService={getDoctors}
            memberAccountNumber={claim.member_account_number || '-'}
          />
        </CustomTab>
      )}

      {SafeAccess(permissions, ['tabs', 'utilization', 'read']) && (
        <CustomTab id='utilization-tab' label='UTILIZATION'>
          <Utilization
            fetchService={getUtilizations}
            utilizationLoa={utilizationLoa}
            isMember={isMember}
            member={member}
            client={client}
            diagnosis={claim.diagnosis || []}
          />
        </CustomTab>
      )}

      {SafeAccess(permissions, ['tabs', 'communication', 'read']) && (
        <CustomTab id='communication-tab' label='COMMUNICATION'>
          <ChatBox
            messages={inquiries.messages}
            title='Communication Logs'
            onSubmitComment={comment => onReturnInquire({ comment: comment, goBack: true })}
            permissions={permissions.tabs.communication}
          />
        </CustomTab>
      )}

      {SafeAccess(permissions, ['tabs', 'contact', 'read']) && (
        <CustomTab id='contract-tab' label='CONTRACT'>
          {(memberFetchStatus === 'fetching' || clientFetchStatus === 'fetching') && (
            <div className={classes.center}>
              <CircularProgress size={50} />
            </div>
          )}
          {memberFetchStatus === '404' && (
            <div className={classes.center}>
              <Typography>{`${isMember ? 'Member' : 'Client'} Not Found`}</Typography>
            </div>
          )}
          {memberFetchStatus === '500' && (
            <div className={classes.center}>
              <Typography>{'Internal Server Error'}</Typography>
            </div>
          )}
          {(memberFetchStatus === 'success' || clientFetchStatus === 'success') && (
            <Contract
              isMember={isMember}
              member={member}
              client={client}
              getMemberContract={getMemberContract}
              getClientContract={getClientContract}
            />
          )}
        </CustomTab>
      )}

      {SafeAccess(permissions, ['tabs', 'comments', 'read']) && (
        <CustomTab id='comment-tab' label='COMMENTS'>
          <ChatBox
            messages={escalations.messages}
            title='Escalation Logs'
            onSubmitComment={comment => onReturnEscalate({ comment: comment, goBack: true })}
            permissions={permissions.tabs.comments}
          />
        </CustomTab>
      )}

      {SafeAccess(permissions, ['tabs', 'notes', 'read']) && (
        <CustomTab id='notes-tab' label='NOTES'>
          <ChatBox messages={recon.messages} permissions={permissions.tabs.notes} />
        </CustomTab>
      )}
    </CustomTabs>
  )
}
