import constants from './constants'
import { DateFormatter } from 'Utilities/TextFormatter'
import moment from 'moment'

export const staticHeaderFooter = args => {
  const { registered_name, corporate_account_no, contracts = [], amount, ar_amount, username } = args

  const static_header = constants.header.map(i => {
    switch (i) {
      case 'Company:':
        return [i, registered_name, '\r\n']
      case 'Account No.:':
        return [i, corporate_account_no, '\r\n']
      case 'Contract:':
        return [i, contracts.length, '\r\n']
      case 'Date:':
        return [i, `${DateFormatter(new Date())} ${moment().format('LTS')}`, '\r\n']
    }
  })

  const static_footer = constants.footer.map(i => {
    switch (i) {
      case 'Total':
        return ['', '', '', i, amount, ar_amount, '\r\n']
      case 'Transmitted by:':
        return [i, username, '\r\n']
      case 'Noted by:':
        return [i, '--', '\r\n']
      case 'Received by:':
        return [i, '--', '\r\n']
    }
  })

  return [
    [...static_header, '\r\n'],
    ['\r\n', ...static_footer]
  ]
}
