import constants from './../../contants'

export const resetError = payload => {
  return {
    for: constants.for.CLAIMS_ANALYSIS,
    type: 'RESET_ERROR',
    payload
  }
}

export const setError = payload => {
  return {
    for: constants.for.CLAIMS_ANALYSIS,
    type: 'SET_ERROR',
    payload
  }
}
