import React from 'react'

// Components
import TreeView from '@material-ui/lab/TreeView'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import ChevronRightIcon from '@material-ui/icons/ChevronRight'
import TreeItem from '@material-ui/lab/TreeItem'

// Styles
import classes from './SideBar.module.scss'

export default function SideBar (props) {
  const { contractId, nodes = [], onItemClick } = props

  return (
    <div className={classes.root}>
      <div className={classes['contract-id']}>
        <span>CONTRACT ID</span>
        <span className={classes.id}>{contractId}</span>
      </div>
      <div className={classes['plan-types']}>
        <TreeView defaultCollapseIcon={<ExpandMoreIcon />} defaultExpandIcon={<ChevronRightIcon />}>
          {nodes.map(node =>
            node.type === 'item' ? (
              <TreeItem
                id={`${node.id}-control`}
                key={`node-${node.id}`}
                className={`${classes.node}`}
                nodeId={`node-${node.id}`}
                label={node.label}
                onClick={() => onItemClick({ planPackage: node.planPackage, benefitCategory: node.benefitCategory })}
              />
            ) : (
              <TreeItem
                id={`${node.id}-control`}
                key={`node-${node.id}`}
                className={`${classes['benefit-label']}`}
                nodeId={`node-${node.id}`}
                label={node.label}
              >
                {node.benefits &&
                  Object.keys(node.benefits).map(key => (
                    <TreeItem
                      id={`node-coverages-${node.benefits[key].details.tree_id}-control`}
                      key={`node-coverages-${node.benefits[key].details.tree_id}`}
                      className={classes.node}
                      nodeId={`node-coverages-${node.benefits[key].details.tree_id}`}
                      label={node.benefits[key].details.name}
                      onClick={() =>
                        onItemClick({
                          planPackage: node.planPackage,
                          benefitCategory: node.benefits[key].details.code
                        })
                      }
                    />
                  ))}
              </TreeItem>
            )
          )}
        </TreeView>
      </div>
    </div>
  )
}
