import React from 'react'
import Paper from '@material-ui/core/Paper'
import { subscribe } from 'redux-subscriber'

// Components
import { CptTextFields, CptTextField } from '../CptTextField/CptTextField'
import TextField from 'SharedComponents/TextField/CustomTextField'
import TextFieldCurrency from 'SharedComponents/TextFieldCurrency/TextFieldCurrency'
import Button from '@material-ui/core/Button'
import Alert from 'SharedComponents/Alert/Alert'
import ProcedureFormValidator from './ProcedureFormValidator'
import DiagnosisValidator from './DiagnosisValidator'
import DropDown from 'SharedComponents/DropDown/DropDown'

// Styles
import classes from './ProcedureForm.module.scss'

// Services
import IcdCptStorageServices from 'Services/Storage/IcdCptStorageServices'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { ClaimsEncoding as actions } from 'Pages/Protected/Claims/Store/actions'

const {
  setProcedure,
  resetSuggestionObject,
  setSuggestionObject,
  addClaimProcedure,
  updateClaimProcedure,
  removeClaimProcedure
} = actions

export default function ProcedureForm (props) {
  const { showAmountPaidByMember = true, showPhilhealthField = true, diagnosis } = props
  const dispatch = useDispatch()

  const [openDialog, setOpenDialog] = React.useState(false)

  const procedure = useSelector(({ ClaimsEncoding: state }) => state.procedure)
  const suggestions = useSelector(({ ClaimsEncoding: state }) => state.suggestions)
  const [debounce, setDebounce] = React.useState(null)

  const [errors, setErrors] = React.useState({})

  React.useEffect(() => {
    const unsubscribe = subscribe('procedure.show', state => {
      setErrors({})
      setOpenDialog(false)
    })
    return () => unsubscribe()
  }, [])

  React.useEffect(() => {
    if (diagnosis) {
      const procedureICD = procedure.details.diagnosis_code
      const existing = diagnosis.find(item => item.code === procedureICD)
      if (!existing) dispatch(setProcedure({ details: { diagnosis_code: '', diagnosis_description: '' } }))
    }
  }, [diagnosis])

  const handleOnChangeProcedureForm = ({ value, field }) => {
    dispatch(setProcedure({ details: { [field]: value } }))
  }

  const handleOnChangeDiagnosis = ({ value, field }) => {
    const _field = field.split('_')[1]
    const diagnosi = diagnosis.find(d => d[_field] === value)
    dispatch(setProcedure({ details: { diagnosis_code: diagnosi.code, diagnosis_description: diagnosi.description } }))
  }

  const handleOnChangeCpt = ({ value, field, isSuggestionClick, selectedIndex }) => {
    dispatch(setProcedure({ details: { [field]: value } }))

    if (isSuggestionClick) {
      if (!isNaN(selectedIndex)) {
        try {
          const i = suggestions.cpt.data[selectedIndex]
          dispatch(resetSuggestionObject({ field: 'cpt' }))
          return dispatch(
            setProcedure({
              details: { description: i.description, code: i.code.toString() }
            })
          )
        } catch (ex) {}
      }
    } else getCptSuggestions({ value, by: field })

    dispatch(resetSuggestionObject({ field: 'cpt' }))
  }

  const getCptSuggestions = ({ value, by }) => {
    if (value.trim().length < 3) return
    clearTimeout(debounce)

    setDebounce(
      setTimeout(() => {
        if (value) {
          IcdCptStorageServices.lookups({
            on: 'cpt',
            params: { query: value, by }
          }).then(res => {
            const { data } = res.data
            let _items = []
            for (let d of data) _items.push(d[by])
            dispatch(
              setSuggestionObject({
                field: 'cpt',
                suggestion: { [by]: _items, data }
              })
            )
          })
        }
      }, 500)
    )
  }

  const validate = () => {
    const baseError = ProcedureFormValidator({
      description: procedure.details.description,
      code: procedure.details.code.toString(),
      gross_amount: procedure.details.gross_amount,
      vat_amount: procedure.details.vat_amount,
      amount_paid_by_member: procedure.details.amount_paid_by_member,
      philhealth_amount_paid: procedure.details.philhealth_amount_paid
    })
    let diagnosisError = {}
    if (diagnosis) {
      diagnosisError = DiagnosisValidator({
        diagnosis_description: procedure.details.diagnosis_description || '',
        diagnosis_code: procedure.details.diagnosis_code || ''
      })
    }
    const error = { ...baseError.error, ...diagnosisError.error }
    console.log(error)
    setErrors(error || {})
    return !!Object.keys(error).length
  }

  const handleOnSaveProcedure = () => {
    if (validate()) return

    dispatch(addClaimProcedure(procedure.details))
    dispatch(setProcedure({ show: false }))
  }

  const handleOnUpdateProcedure = index => {
    if (validate()) return

    dispatch(updateClaimProcedure({ index, details: procedure.details }))
    dispatch(setProcedure({ show: false }))
  }

  const handleOnRemoveProcedure = index => {
    dispatch(removeClaimProcedure({ index }))
    dispatch(setProcedure({ show: false }))
    setOpenDialog(false)
  }

  return (
    procedure.show && (
      <React.Fragment>
        <Paper className={classes.root}>
          <div className={classes.groups}>
            {diagnosis && (
              <React.Fragment>
                <div className={classes.input}>
                  <DropDown
                    id='icd10-description'
                    required
                    label='ICD10 Description'
                    items={diagnosis.map(({ description }) => ({ text: description, value: description }))}
                    value={procedure.details.diagnosis_description}
                    onChange={event =>
                      handleOnChangeDiagnosis({
                        field: 'diagnosis_description',
                        value: event.target.value
                      })
                    }
                    error={errors.diagnosis_description}
                  />
                </div>
                <div className={classes.input}>
                  <DropDown
                    id='icd10-code'
                    required
                    label='ICD10 Code'
                    items={diagnosis.map(({ code }) => ({ text: code, value: code }))}
                    value={procedure.details.diagnosis_code}
                    onChange={event =>
                      handleOnChangeDiagnosis({
                        field: 'diagnosis_code',
                        value: event.target.value
                      })
                    }
                    error={errors.diagnosis_code}
                  />
                </div>
              </React.Fragment>
            )}
          </div>
          <CptTextFields>
            <CptTextField
              id={`procedure-description-autocomplete`}
              codeId={`procedure-code-autocomplete`}
              description={procedure.details.description}
              suggestions={suggestions.cpt}
              code={procedure.details.code}
              onChangeDescription={(value, isSuggestionClick, selectedIndex) =>
                handleOnChangeCpt({
                  field: 'description',
                  value,
                  isSuggestionClick,
                  selectedIndex
                })
              }
              onChangeCode={(value, isSuggestionClick, selectedIndex) =>
                handleOnChangeCpt({ field: 'code', value, isSuggestionClick, selectedIndex })
              }
              errors={errors}
            />
          </CptTextFields>
          <div className={classes.groups}>
            <div className={classes.input}>
              <TextFieldCurrency
                id='procedure-gross-amount'
                label='Gross Amount'
                value={procedure.details.gross_amount}
                onChange={event =>
                  handleOnChangeProcedureForm({
                    field: 'gross_amount',
                    value: event.target.value
                  })
                }
                error={errors.gross_amount}
              />
            </div>

            <div className={classes.input}>
              <TextFieldCurrency
                id='procedure-vat-amount'
                label='VAT Amount'
                value={procedure.details.vat_amount}
                onChange={event =>
                  handleOnChangeProcedureForm({
                    field: 'vat_amount',
                    value: event.target.value
                  })
                }
                error={errors.vat_amount}
              />
            </div>
            {showAmountPaidByMember && (
              <div className={classes.input}>
                <TextFieldCurrency
                  id='procedure-amount-paid-by-member'
                  label='Amount Paid by Member'
                  value={procedure.details.amount_paid_by_member}
                  onChange={event =>
                    handleOnChangeProcedureForm({
                      field: 'amount_paid_by_member',
                      value: event.target.value
                    })
                  }
                  error={errors.amount_paid_by_member}
                />
              </div>
            )}
          </div>
          {showPhilhealthField && (
            <div className={classes.groups}>
              <div className={classes.input}>
                <TextFieldCurrency
                  id='philhealth-amount-paid'
                  label='PhilHealth Amount Paid'
                  value={procedure.details.philhealth_amount_paid}
                  onChange={event =>
                    handleOnChangeProcedureForm({
                      field: 'philhealth_amount_paid',
                      value: event.target.value
                    })
                  }
                  error={errors.philhealth_amount_paid}
                />
              </div>

              <div className={classes.input}>
                <TextField
                  multiline
                  rows={3}
                  id='philhealth-remarks'
                  label='PhilHealth Remarks'
                  value={procedure.details.philhealth_remarks}
                  onChange={event =>
                    handleOnChangeProcedureForm({
                      field: 'philhealth_remarks',
                      value: event.target.value
                    })
                  }
                />
              </div>
            </div>
          )}

          <div className={`${classes.groups} ${classes.buttons}`}>
            {procedure.mode === 'add' && (
              <React.Fragment>
                <Button
                  id='procedure-save-button'
                  variant='contained'
                  color='primary'
                  className='button-positive'
                  onClick={handleOnSaveProcedure}
                >
                  Save
                </Button>
                <Button
                  id='procedure-cancel-button'
                  variant='contained'
                  color='secondary'
                  onClick={() => dispatch(setProcedure({ show: false }))}
                >
                  Cancel
                </Button>
              </React.Fragment>
            )}
            {procedure.mode !== 'add' && (
              <React.Fragment>
                <Button
                  id='procedure-update-button'
                  variant='contained'
                  color='primary'
                  className='button-positive'
                  onClick={() => handleOnUpdateProcedure(procedure.index)}
                >
                  Update
                </Button>
                <Button
                  id='procedure-delete-button'
                  variant='contained'
                  color='secondary'
                  onClick={() => setOpenDialog(true)}
                >
                  Delete
                </Button>
              </React.Fragment>
            )}
          </div>
        </Paper>
        <Alert
          open={openDialog}
          message='Are you sure you want to delete this procedure?'
          cancelLabel='No'
          okLabel='Yes'
          onCancel={() => setOpenDialog(false)}
          onOk={() => handleOnRemoveProcedure(procedure.index)}
        />
      </React.Fragment>
    )
  )
}
