import { SafeAccess } from 'Utilities/SafeAccess'
import Unit from '../Utilities/Unit'
import DefaultValue from '../Utilities/DefaultValue'
import { DigitFormatter, UpperWord } from 'Utilities/TextFormatter'

const Functions = {
  // Single Value
  mobile_ape_availees: ({ nodes, details }) => SafeAccess(details, 'custom_metadata.mobile_ape_availees', ''),
  billing_arangement: ({ nodes, details }) => {
    const data = SafeAccess(details, 'custom_metadata', null)
    const billingArrangement = {
      premium: 'Imputed in premiums',
      billback: 'Bill back with admin fee',
      rider_fee: '1-time rider fee to be upon enrollment',
      imputed_in_premiums: 'Imputed in premiums',
      billback_net: 'Bill back, net of APE cost with admin fee',
      fixed_rate_rider: 'Scheduled fixed rate for the entire rider',
      fixed_rate_personnel: 'Scheduled fixed rate per personnel deployed',
      soa_rate: 'Scheduled SOA based on fixed rate/hour/personnel deployed',
      soa_actual_cost: 'Scheduled SOA based on actual cost of personnel deployed'
    }
    // CC
    // const billingArrangement = {
    //   premium: "Imputed in premiums",
    //   fixed_rate_rider: "Scheduled fixed rate for the entire rider",
    //   fixed_rate_personnel: "Scheduled fixed rate per personnel deployed",
    //   soa_rate: "Scheduled SOA based on fixed rate/hour/personnel deployed",
    //   soa_actual_cost: "Scheduled SOA based on actual cost of personnel deployed",
    //   rider_fee: "1-time rider fee to be upon start of contract",
    // }
    // ECU
    // const billingArrangement = {
    //   imputed_in_premiums: 'Imputed in premiums',
    //   billback: 'Bill back with admin fee',
    //   billback_net: 'Bill back, net of APE cost with admin fee',
    //   rider_fee: '1-time rider fee to be upon enrollment'
    // }
    // MT
    // const billingArrangement = {
    //   premium: "Imputed in premiums",
    //   billback: "Bill back with admin fee",
    //   rider_fee: "1-time rider fee to be upon enrollment"
    // }
    if (!data) return ''
    if (!billingArrangement[data.billing_arangement]) return ''
    return [
      billingArrangement[data.billing_arangement],
      data.billback_fee ? `${data.billback_fee} ${Unit(data.billback_type)}` : '',
      data.rider_fee ? `${data.rider_fee} ${Unit(data.rider_fee_unit)}` : ''
    ]
      .filter(s => s !== '')
      .join('\r\n')
  },
  waiting_period: ({ nodes, details }) => {
    const data = SafeAccess(details, 'custom_metadata', null)
    if (!data || !data.waiting_period_type) return ''
    return [
      data.waiting_period_type === 'effectivity' ? 'Waiting period after effectivity of member' : '',
      data.waiting_period_type === 'date_of_hire' ? 'Waiting period after date of hire' : '',
      data.waiting_period ? `${data.waiting_period} ${Unit(data.waitin_period_unit)}` : ''
    ]
      .filter(s => s !== '')
      .join('\r\n')
  },
  limit: ({ nodes, details }) => {
    const data = SafeAccess(details, 'custom_metadata', null)
    if (!data) return ''
    return data.limit ? `${data.limit} ${Unit(data.limit_unit)}` : ''
  },
  limit_for_all: ({ nodes, details }) => {
    const data = SafeAccess(details, 'custom_metadata', null)
    if (!data) return ''
    return data.limit_for_all ? `${data.limit_for_all} ${Unit(data.limit_for_all_unit)}` : ''
  },
  availment_per_year: ({ nodes, details }) => {
    const data = SafeAccess(details, 'custom_metadata', null)
    if (!data) return ''
    return data.availment_per_year ? `${DigitFormatter(data.availment_per_year)}` : ''
  },
  voluntary_upgrading_type: ({ nodes, details }) => {
    const data = SafeAccess(details, 'custom_metadata', null)
    if (!data) return ''
    if (!data.voluntary_upgrading_type) return ''
    switch (data.voluntary_upgrading_type) {
      case 'paid_by_member':
        return 'Room and Board difference is paid by the Member'
      case 'first_hours_free':
        return data.voluntary_upgrading_amount
          ? `First ${data.voluntary_upgrading_amount} hours free`
          : 'First 0 hour free'
      case 'rate':
        return data.voluntary_upgrading_amount ? `Rate: ${data.voluntary_upgrading_amount} %` : 'Rate: 0%'
    }
  },
  involuntary_upgrading_type: ({ nodes, details }) => {
    const data = SafeAccess(details, 'custom_metadata', null)
    if (!data) return ''
    if (!data.involuntary_upgrading_type) return ''
    return [
      data.except_suite_room && data.except_suite_room === true ? 'Except Suite Room' : '',
      data.involuntary_upgrading_type === 'paid_by_member' ? 'Room and Board difference is paid by the Member' : '',
      data.involuntary_upgrading_type === 'first_hours_free'
        ? `First ${data.involuntary_upgrading_amount ? `${data.involuntary_upgrading_amount} hours` : '0 hour'} free`
        : ''
    ]
      .filter(s => s !== '')
      .join('\r\n')
  },

  // Empty
  empty: ({ nodes, details }) => ''
}

