import React from 'react'

// Material Components
import Button from '@material-ui/core/Button'
// Shared Components
import DropDown from 'SharedComponents/DropDown/DropDown'
import TextField from 'SharedComponents/TextField/CustomTextField'

// Styles
import classes from './DataPairs.module.scss'
import { Typography } from '@material-ui/core'

const DataPair = props => {
  const { index, sources, targets, source, target } = props
  const { onClickRemove, onChangeSource, onChangeTarget } = props

  return (
    <div className={classes.child}>
      <div className={classes.source}>
        {/* <DropDown items={sources} value={source} onChange={onChangeSource} /> */}
        <TextField
          className={classes.name}
          id={`input-source-${index}`}
          value={source}
          onChange={onChangeSource}
          autoFocus
          fullWidth
        />
      </div>
      <div className={classes.target}>
        <DropDown items={targets} value={target} onChange={onChangeTarget} />
      </div>
      <div className={classes.action}>
        <Button onClick={onClickRemove}>Remove</Button>
      </div>
    </div>
  )
}
export default function DataPairContainer (props) {
  const { headers = ['Source', 'Target'], fields = [], sources, targets } = props
  const { onClickAddNew, onClickRemove, onChangeSource, onChangeTarget } = props
  return (
    <div className={classes.root}>
      <div className={classes.header}>
        {headers.map((head, index) => (
          <Typography key={`heade-${index}`} variant='h6'>
            {head}
          </Typography>
        ))}
      </div>
      <div className={classes.children}>
        {fields.map((field, index) => (
          <DataPair
            key={`field-${index}`}
            index={index}
            sources={sources}
            targets={targets}
            source={field.source || ''}
            target={field.target || ''}
            onChangeSource={event => onChangeSource({ index, value: event.target.value })}
            onChangeTarget={event => onChangeTarget({ index, value: event.target.value })}
            onClickRemove={event => onClickRemove({ index })}
          />
        ))}
      </div>
      <div className={classes.action}>
        <Button onClick={onClickAddNew}>Add New</Button>
      </div>
    </div>
  )
}
