import React from 'react'

// Components
import Table from 'SharedComponents/Table/Table'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import TextField from 'SharedComponents/TextField/CustomTextField'
import Typography from '@material-ui/core/Typography'
import Link from '@material-ui/core/Link'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'

// Utilities
import { setPaginate } from './Utils/pagination'
import { sort } from './Utils/sortBy'
import { onQuery } from './Utils/query'
import { SafeAccess } from 'Utilities/SafeAccess'

// Styles
import classes from './Exclusions.module.scss'

let columns = [
  { name: 'Type', sortField: 'type', search: true },
  { name: 'Item', sortField: 'item_name', search: true }
]

export default function Benefits (props) {
  const { isMember, member, client } = props

  const [selectedPlan, setSelectedPlan] = React.useState('BASE')
  const [planTypes, setPlanTypes] = React.useState([])
  const [rows, setRows] = React.useState([])
  const [allExclusions, setAllExclusions] = React.useState([])
  const [exclusionList, setExclusionList] = React.useState({
    data: [],
    orderBy: '',
    order: 'desc',
    count: 0,
    page: 1,
    limit: 5,
    filter: {},
    search: {},
    plan_type: '',
    rowsPerPageOptions: [5, 10, 15]
  })

  const [debounce, setDebounce] = React.useState(null)

  React.useEffect(() => {
    if (isMember) {
      if (!member) return
      // getExclusions(member.benefit_tree)
      const _rows = getExclusions2(member.benefit_tree)

      setAllExclusions(_rows)
      setExclusionList(state => ({ ...state, data: _rows, count: _rows.length }))
      setRows(_rows.slice(0, exclusionList.limit))
      handleOnSetItem(selectedPlan)
    }
  }, [member, isMember, client])

  const getExclusions = nodes => {
    const { MCT } = nodes
    let arr_list = []

    if (Object.keys(nodes).length) {
      let plan_types = SafeAccess(MCT, `nodes.CT.nodes`, {})
      let plan_type_keys = Object.keys(plan_types)

      setPlanTypes(plan_type_keys)

      for (const type of plan_type_keys) {
        Object.keys(plan_types[type].nodes).forEach(i => {
          if (i === 'EX') {
            let child_node = Object.keys(plan_types[type].nodes[i].nodes)
              .map(c => plan_types[type].nodes[i].nodes[c])
              .filter(i => Object.keys(i.nodes).length)
              .map(outer_node => {
                if (Object.keys(outer_node.nodes).length) {
                  return Object.values(outer_node.nodes)
                    .filter(i => i.details.custom_metadata.is_excluded)
                    .map(inner_node => ({
                      item_name: inner_node.details.name,
                      plan_type: type,
                      type: outer_node.details.name,
                      code: inner_node.details.code,
                      parent_code: outer_node.details.code
                    }))
                }
              })
            arr_list = [].concat.apply([], child_node)
          }
        })
      }
    }
    let _rows = arr_list.filter(benefit => benefit.plan_type === selectedPlan)

    setAllExclusions(arr_list)
    setExclusionList(state => ({ ...state, data: _rows, count: _rows.length }))
    setRows(_rows.slice(0, exclusionList.limit))
  }

  const getExclusions2 = nodes => {
    const { MCT } = nodes
    const plan_types = SafeAccess(MCT, `nodes.CT.nodes`, {})

    const plan_types_keys = Object.keys(plan_types)
    setPlanTypes(plan_types_keys)

    let rows = []
    plan_types_keys.forEach(planTypeKey => {
      const ex_nodes = SafeAccess(plan_types, `${planTypeKey}.nodes.EX.nodes`, null)
      if (ex_nodes) {
        Object.keys(ex_nodes).map(exclusionKey => {
          const inner_node = ex_nodes[exclusionKey]
          Object.keys(inner_node.nodes).map(innerKey => {
            const { details } = inner_node.nodes[innerKey]
            if (details.custom_metadata.is_excluded) {
              rows.push({
                item_name: details.name,
                plan_type: planTypeKey,
                type: inner_node.details.name,
                code: details.code,
                parent_code: inner_node.details.code
              })
            }
          })
        })
      }
    })
    return rows
  }
  // TODO will park this for now
  // const handleOnSearchAll = args => {
  //   const { search, page } = args

  //   const queriedData = onQueryAll({
  //     search: search.toLowerCase(),
  //     data: allExclusions.filter(benefit => benefit.plan_type === selectedPlan)
  //   })

  //   setExclusionList(state => ({ ...state, page, count: queriedData.length, data: queriedData }))
  //   setRows(queriedData.slice(0, exclusionList.limit))
  // }
  const handleOnSearch = args => {
    const { search, page, range } = args
    const _data = allExclusions.filter(benefit => benefit.plan_type === selectedPlan)

    if (range !== 'all') {
      exclusionList.filter[range] = search
    } else {
      exclusionList.search[range] = search
    }

    const queriedData = onQuery({ filter: exclusionList.filter, search: exclusionList.search, data: _data })

    setExclusionList(state => ({ ...state, page, count: queriedData.length, data: queriedData }))
    setRows(queriedData.slice(0, exclusionList.limit))
  }

  const handleOnPaginate = args => {
    const { data, limit, page } = setPaginate({ args, exc: exclusionList })
    setExclusionList(state => ({ ...state, limit, page }))
    setRows(data)
  }

  const handleOnSetItem = item => {
    let _rows = allExclusions.filter(benefit => benefit.plan_type === item)

    setSelectedPlan(item)
    setExclusionList(state => ({
      ...state,
      data: _rows,
      count: _rows.length,
      page: 1,
      limit: state.rowsPerPageOptions[0]
    }))
    setRows(_rows.slice(0, exclusionList.rowsPerPageOptions[0]))

    // TODO
    // setExclusionList(state => ({ ...state, data: _rows, count: _rows.length, page: 1 }))
    // setRows(_rows.slice(0, exclusionList.limit))
  }

  const handleOnSort = args => {
    const { data: sortedData } = sort({ data: rows, field: args.orderBy, direction: args.order })

    setRows(sortedData.slice(0, exclusionList.limit))
    setExclusionList(state => ({ ...state, ...args }))
  }

  return (
    <div className={classes.root}>
      <div className={classes.header} />
      <div className={classes.body}>
        <div className={classes[`plan-types`]}>
          {planTypes.map((item, index) => (
            <Typography key={`plan-${index}`} className={classes.type}>
              <Link onClick={() => handleOnSetItem(item)}>
                <span>{item}</span>
              </Link>
            </Typography>
          ))}
        </div>
        <div className={classes.search}>
          <SearchTextComponent
            id='search-exclusion'
            onChange={e => {
              const search = e.target.value
              clearTimeout(debounce)
              setDebounce(
                setTimeout(() => {
                  handleOnSearch({ search, page: 1, range: 'all' })
                }, 500)
              )
            }}
            placeholder='Search'
          />
        </div>
        <div className={classes.table}>
          <Table
            TableContainerProps={{ style: { maxHeight: '400px', overflow: 'auto', position: 'relative' } }}
            TableProps={{ stickyHeader: true }}
            head={
              <TableRow>
                {columns.map(headCell => (
                  <TableCell key={headCell.sortField}>
                    <TableSortLabel
                      active={exclusionList.orderBy === headCell.sortField}
                      direction={exclusionList.order ? 'asc' : 'desc'}
                      onClick={event => handleOnSort({ orderBy: headCell.sortField, order: !exclusionList.order })}
                    >
                      {headCell.name}
                    </TableSortLabel>
                    {headCell.search && (
                      <TextField
                        id={`${headCell.sortField}-input`}
                        TextFieldStyle={{ margin: 0 }}
                        fullWidth
                        onChange={e => {
                          const search = e.target.value
                          clearTimeout(debounce)
                          setDebounce(
                            setTimeout(() => {
                              handleOnSearch({ search, page: 1, range: headCell.sortField })
                            }, 500)
                          )
                        }}
                      />
                    )}
                  </TableCell>
                ))}
              </TableRow>
            }
            body={
              rows.length ? (
                rows.map((row, index) => (
                  <TableRow id={`exlusions-tab-row-${index}`} key={`ex-${index}`}>
                    <TableCell id={`exlusions-tab-row-item-${index}-type`}>{row.type}</TableCell>
                    <TableCell id={`exlusions-tab-row-item-${index}-name`}>{row.item_name}</TableCell>
                  </TableRow>
                ))
              ) : (
                <TableRow>
                  <TableCell className={classes.handling} colSpan={2}>
                    No data found
                  </TableCell>
                </TableRow>
              )
            }
            pagination
            count={exclusionList.count}
            page={exclusionList.page}
            limit={exclusionList.limit}
            rowsPerPageOptions={exclusionList.rowsPerPageOptions}
            onChange={args => handleOnPaginate(args)}
          />
        </div>
      </div>
    </div>
  )
}
