import { SafeAccess } from 'Utilities/SafeAccess'
import Unit from '../Utilities/Unit'
import DefaultValue from '../Utilities/DefaultValue'
import { DigitFormatter, UpperWord } from 'Utilities/TextFormatter'

const Functions = {
  // Single Value
  name: ({ nodes, details }) => details.name,
  parent_name: ({ nodes, details }) => details.parent_name,
  title: ({ nodes, details }) => SafeAccess(details, 'custom_metadata.title', '-'),
  limit_type: ({ nodes, details }) => details.limits.map(limit => limit.type).join('\r\n'),
  age_limit: ({ nodes, details }) => SafeAccess(details, 'custom_metadata.age_limit', 0),
  max_availee: ({ nodes, details }) => SafeAccess(details, 'custom_metadata.max_availee', 0),
  number: ({ nodes, details }) => SafeAccess(details, 'custom_metadata.number', 0),
  required: ({ nodes, details }) => DefaultValue(SafeAccess(details, 'custom_metadata.required') || false),
  no_of_days: ({ nodes, details }) => SafeAccess(details, 'custom_metadata.no_of_days', ''),
  time_from: ({ nodes, details }) => SafeAccess(details, 'custom_metadata.time_from', ''),
  time_to: ({ nodes, details }) => SafeAccess(details, 'custom_metadata.time_to', ''),
  member_type: ({ nodes, details }) => SafeAccess(details, 'custom_metadata.member_type', ''),
  plan_type: ({ nodes, details }) => SafeAccess(details, 'custom_metadata.plan_type', ''),
  peso_limit_amt: ({ nodes, details }) => SafeAccess(details, 'custom_metadata.peso_limit_amt', ''),
  no_of_members: ({ nodes, details }) => SafeAccess(details, 'custom_metadata.no_of_members', 0),
  peso_limit: ({ nodes, details }) => SafeAccess(details, 'custom_metadata.peso_limit', ''),

  // Arrays
  payment: ({ nodes, details }) => SafeAccess(details, 'custom_metadata.payment', ''),
  availment: ({ nodes, details }) => SafeAccess(details, 'custom_metadata.availment', ''),
  limit: ({ nodes, details }) =>
    details.limits
      .map(limit => limit.limit && limit.limit_unit && `${DigitFormatter(limit.limit)} ${Unit(limit.limit_unit)}`)
      .join('\r\n'),
  rollover: ({ nodes, details }) => UpperWord(SafeAccess(details, 'custom_metadata.rollover', '')),
  refresh: ({ nodes, details }) => UpperWord(SafeAccess(details, 'custom_metadata.refresh', '')),
  eligible_availees: ({ nodes, details }) => SafeAccess(details, 'custom_metadata.eligible_availees', ''),
  days_deployed: ({ nodes, details }) => SafeAccess(details, 'custom_metadata.days_deployed', ''),
  eligibility: ({ nodes, details }) => SafeAccess(details, 'custom_metadata.eligibility', ''),

  // Empty
  empty: ({ nodes, details }) => ''
}

