import constants from './../../contants'

const initialValue = true

const rejectReducer = (state = initialValue, action) => {
  if (action.for !== constants.for.CLAIMS_ANALYSIS) return state

  switch (action.type) {
    case 'SET_REJECT_CONTROL':
      return (state = action.payload)

    default:
      return state
  }
}

export default rejectReducer
