import React from 'react'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import Button from '@material-ui/core/Button'

// Components
import Table from 'SharedComponents/Table/Table'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import TextField from 'SharedComponents/TextField/CustomTextField'

// Services
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Utilities
import { includesAny } from 'Utilities/ArrayCompare'
import lodash from 'lodash'

// Styles
import classes from './ClientMemberLookup.module.scss'

export default function ClientMemberLookup (props) {
  // Variables
  const { columns = [], allSearch = '', allSearchText = '', allSearchFields, mode = '' } = props
  // Services
  const { service } = props
  // Events
  // Callbacks
  const { onRecordSet } = props

  // Local Variables
  const [apiData, setApiData] = React.useState({
    rows: [],
    search: '',
    order: '',
    limit: 10
    // count: 0,
    // page: 1,
    // rowsPerPageOptions: [10, 15, 20]
  })

  const [finalRows, setFinalRows] = React.useState([])
  const [selected, setSelected] = React.useState(-1)
  const [debounce, setDebounce] = React.useState(null)
  const [colFilters, setColFilters] = React.useState({})

  React.useEffect(() => {
    setApiData(state => ({ ...state, search: allSearch }), fetchData(allSearch))
  }, [])

  React.useEffect(() => {
    const order = apiData.order.split(' ')
    let _rows = lodash.orderBy(apiData.rows, [order[0]], [order[1]])
    _rows = translateRows(_rows)
    _rows = filterData(_rows)
    setFinalRows(_rows)
  }, [apiData.rows, apiData.order, colFilters])

  const fetchData = search => {
    if (search === '') return
    LoadingService.loading(true)
    let fields = {}
    for (const col of columns) {
      fields[col.id] = true
    }
    const order = apiData.order === '' ? `${columns[0].id} asc` : apiData.order
    setApiData(state => ({ ...state, rows: [], order }))
    let fetches = []
    for (const field of allSearchFields) {
      fetches.push(
        new Promise((resolve, reject) => {
          service({
            where: { [field.id]: { value: search, match: field.match } },
            order,
            fields,
            limit: apiData.limit
          }).then(res => resolve(res))
        })
      )
    }
    Promise.all(fetches).then(responses => {
      for (const res of responses) {
        if (!res) return
        const { data } = res.data
        setApiData(state => ({ ...state, rows: [...state.rows, ...data[mode]] }))
      }
      LoadingService.loading(false)
    })
  }

  const translateRows = rows => {
    return rows.map(row => columns.map((col, colIdx) => col.value(row)))
  }

  const revertRow = row => {
    if (!row) return
    const _row = {}
    for (const [index, cell] of Object.entries(row)) {
      _row[columns[index].id] = cell
    }
    return _row
  }

  const filterData = rows => {
    let _rows = rows
    // _rows = _rows.filter(row => row.map((col, index) => includesAny(`${col}`, filters.all)).some(val => val))
    _rows = _rows.filter(
      row =>
        !row
          .map((col, index) => {
            if (!colFilters[index]) return true
            return includesAny(`${col}`, colFilters[index])
          })
          .some(val => val === false)
    )
    return _rows
  }

  const renderRows = rows => {
    if (!rows) return
    return rows.map((row, index) => (
      <TableRow
        id={`member-row-${index}`}
        key={`member-${index}`}
        hover
        role='checkbox'
        tabIndex={-1}
        className={selected && selected[0] === row[0] ? classes.selected : classes['default-override']}
      >
        {columns.map((col, colIdx) => (
          <TableCell key={`${col.id}-${colIdx}`} id={`${col.id}-${colIdx}`} onClick={() => handleItemClick(row)}>
            {row[colIdx]}
          </TableCell>
        ))}
      </TableRow>
    ))
  }

  const handleOnSort = id => {
    const [, direction] = apiData.order.split(' ')
    setApiData(state => ({ ...state, order: `${id} ${direction !== 'asc' ? 'asc' : 'desc'}` }))
  }

  const handleColumnFilter = ({ id, index, value }) => {
    setColFilters(state => ({ ...state, [index]: value }))
  }

  const handleItemClick = row => {
    if (!selected) setSelected(row)
    else if (row[0] === selected[0]) setSelected()
    else setSelected(row)
  }

  return (
    <div className={classes.root}>
      <div className={classes.search}>
        <SearchTextComponent
          id='search'
          value={apiData.search}
          onChange={event => {
            const search = event.target.value
            setApiData(state => ({ ...state, search }))
            clearTimeout(debounce)
            setDebounce(
              setTimeout(() => {
                fetchData(search)
              }, 500)
            )
          }}
          placeholder={allSearchText}
        />
      </div>
      <div className={classes.table}>
        <Table
          TableContainerProps={{ style: { height: '300px', overflow: 'auto', position: 'relative' } }}
          TableProps={{ stickyHeader: true }}
          head={
            <TableRow>
              {columns.map((cell, colIdx) => (
                <TableCell key={cell.id} id={cell.id}>
                  <TableSortLabel
                    active={apiData.order.split(' ')[0] === cell.id}
                    direction={apiData.order.split(' ')[1] === 'asc' ? 'asc' : 'desc'}
                    onClick={() => handleOnSort(cell.id)}
                  >
                    {cell.label}
                  </TableSortLabel>
                  <TextField
                    onChange={event => {
                      const search = event.target.value
                      clearTimeout(debounce)
                      setDebounce(
                        setTimeout(() => {
                          handleColumnFilter({
                            id: cell.id,
                            index: colIdx,
                            value: search
                          })
                        }, 250)
                      )
                    }}
                  />
                </TableCell>
              ))}

              <TableCell align='center' />
            </TableRow>
          }
          body={renderRows(finalRows)}
          pagination={false}
          // count={count}
          // page={page}
          // limit={limit}
          // rowsPerPageOptions={rowsPerPageOptions}
          // onChange={onPaginate}
        />
      </div>
      <div className={classes.footer}>
        <Button
          variant='contained'
          color='primary'
          id='select'
          className='button-positive'
          onClick={() => selected && onRecordSet(revertRow(selected))}
        >
          Select
        </Button>
      </div>
    </div>
  )
}
