export const onQuery = args => {
  const { filter, search, data } = args
  let mutable_data = []

  if (search.all) {
    const _search = search.all.toLowerCase()
    mutable_data = data.filter(
      i => i.item_name.toLowerCase().includes(_search) || i.type.toLowerCase().includes(_search)
    )
  } else {
    mutable_data = data
  }

  if (Object.keys(filter).length > 0) {
    const fields = Object.keys(filter)
    fields.forEach((field, index) => {
      const search = filter[field].toLowerCase()

      if (index === 0) {
        mutable_data = mutable_data.filter(item => item[field].toLowerCase().includes(search))
      } else {
        mutable_data = mutable_data.filter(item => item[field].toLowerCase().includes(search))
      }
    })
  }

  return mutable_data
}
