import Joi from '@hapi/joi'

// Services
import ExtractValidationResult from 'Services/ExtractValidationResult'

const schema = Joi.object({
  bundle_soa: Joi.number()
    .label('Total Bundle Soa Amount')
    .required(),
  batches_soa: Joi.number()
    .label('Total Batches Soa Amount')
    .valid(Joi.ref('bundle_soa'))
    .required(),
  bundle_rcs: Joi.number()
    .label('Total No. of Bundle RCS')
    .required(),
  batches_rcs: Joi.number()
    .label('Total No. of Batches RCS')
    .valid(Joi.ref('bundle_rcs'))
    .required()
}).options({ abortEarly: false })

const validate = data => ExtractValidationResult(schema.validate(data))

export default validate
