import constants from './../../contants'

export const setLimitType = payload => {
  return {
    for: constants.for.CLAIMS_ANALYSIS,
    type: 'SET_LIMIT_TYPE',
    payload
  }
}

export const resetLimitType = payload => {
  return {
    for: constants.for.CLAIMS_ANALYSIS,
    type: 'RESET_LIMIT_TYPE',
    payload
  }
}
