import XLSX from 'xlsx'

export default async function XLSXParser ({ file, onComplete }) {
  let headers = null
  const results = []

  var reader = new FileReader()
  reader.onload = function (e) {
    const workbook = XLSX.read(e.target.result, { type: 'binary' })

    const sheet = workbook.Sheets[workbook.SheetNames[0]]
    const data = XLSX.utils.sheet_to_json(sheet, { raw: false, header: 1 })
    headers = data.shift()

    for (let index = 0; index < data.length; index++) {
      const _item = {}
      headers.map(i => (_item[i] = ''))

      data[index].map((value, index) => (_item[headers[index]] = value))

      if (index === data.length - 1 && !data[index].length) continue

      results.push(_item)
    }

    onComplete({ headers, results })
  }

  reader.readAsBinaryString(file)
}
