import React from 'react'

import moment from 'moment'

// Material Components
import Button from '@material-ui/core/Button'
import FormControl from '@material-ui/core/FormControl'
import GetAppIcon from '@material-ui/icons/GetApp'
import MenuItem from '@material-ui/core/MenuItem'
import Paper from '@material-ui/core/Paper'
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'
import Select from '@material-ui/core/Select'
import Table from 'SharedComponents/Table/Table'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import Typography from '@material-ui/core/Typography'

// Shared Components
import DatePicker from 'SharedComponents/DatePicker/DatePicker'
import TextField from 'SharedComponents/TextField/CustomTextField'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'

// import ClaimsUtilLedgerReport from '../ClaimsUtilLedgerReport/ClaimsUtilLedgerReport'
import Utilization from '../ClaimsViewer/Components/Utilization/Utilization'

// Services
import ArrayToCSV from 'Services/ArrayToCSV'
// TODO
// Change ClaimsService when API for Treasury is available
import ClaimsServices from 'Services/Api/ClaimsServices'
import ClaimsReconServices from 'Services/Api/ClaimsReconServices'
import ClaimsAdjudicationServices from 'Services/Api/ClaimsAdjudicationServices'
import CustomerCareServices from 'Services/Api/CustomerCareServices'

import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'

// Utility
import { DateFormatter, LargeNumberFormatter, CurrencyFormatter } from 'Utilities/TextFormatter'
import { SafeAccess } from 'Utilities/SafeAccess'

// Styles
import classes from './style/UtilizationReport.module.scss'
import theme from './style/theme'

// Data
import legend from './data/legend'

// Config
import months from './config/months'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

const clientColumns = [
  { label: 'Type', data: 'type', search: true, match: 'any' },
  { label: 'Total Amount', data: 'total_amount', search: true, match: 'number' },
  { label: 'Number of Cases', data: 'cases', search: true, match: 'number' },
  { label: 'Availed By Principals', data: 'availed_by_principals', search: true, match: 'number' },
  { label: 'Availed By Dependents', data: 'availed_by_dependents', search: true, match: 'number' }
]

// const memberDataColumns = [
//   'loa_number',
//   'approval_code',
//   'claim_number',
//   'diagnosis',
//   'procedures.description',
//   'facility_id'
// ]
const memberDataColumns = ['claim_no', 'diagnosis', 'procedures', 'provider']

export default function UtilizationReport (props) {
  const [reports, setReports] = React.useState({
    data: [],
    search: '',
    range: { from: new Date().toISOString(), to: new Date().toISOString(), field: 'status_date' },
    filter: {},
    orderBy: 'type',
    order: true,
    page: 1,
    limit: 10,
    count: 0,
    rowsPerPageOptions: [10, 15, 20]
  })
  const [debounce, setDebounce] = React.useState(null)
  const [reportType, setReportType] = React.useState('client')

  /**
   * initial -> initialized on generate report
   * actual -> data displayed on the graph
   * initial is retained as reference for filtering
   *  graph data to refrain from making api calls
   */
  const [graphData, setGraphData] = React.useState({
    initial: [],
    actual: []
  })

  const [graph, setGraph] = React.useState({
    utilized: 0,
    ibnr: 0,
    total: 0
  })

  const [graphFilter, setGraphFilter] = React.useState('all')
  // const [refreshMemberReport, doRefreshMemberReport] = React.useState(0)
  const [client, setClient] = React.useState({ name: '--', account_no: '' })
  const [member, setMember] = React.useState({})
  const [utilizationLoa, setUtilizationLoa] = React.useState({})
  const [memberFetchStatus, setMemberFetchStatus] = React.useState('')

  React.useEffect(() => {
    PageTitleService('Utilization Report')
    BreadcrumbsService.setSelectedItems(['utilization'])
  }, [])

  React.useEffect(() => {
    fetchTable()
    // Watch filter items

    // [reports.search, reports.filter, reports.orderBy, reports.order, reports.limit, reports.page]
  }, [reports.filter, reports.orderBy, reports.order, reports.limit, reports.page])

  React.useEffect(() => {
    /**
     * Handle Graph Filtering
     * Also Handles Initial Data
     */
    let newData
    if (graphFilter === 'all') {
      // Returns all data after being processed for percentage count
      newData = graphData.initial.map(data => {
        const newChild = data.claims.map(child => ({ ...child, percentage: getPercentage(child.total_amount) }))
        return { ...data, claims: newChild }
      })
    } else {
      // Returns selected data after being processed for percentage count
      newData = graphData.initial.map(data => {
        let childContainer = []
        data.claims.map(child => {
          if (child.claim_type === graphFilter) {
            childContainer.push({ ...child, percentage: getPercentage(child.total_amount) })
          }
        })
        return { ...data, claims: childContainer }
      })
    }

    setGraphData(p => ({ ...p, actual: newData }))
  }, [graphFilter, graphData.initial])

  const fetchCSV = () => {
    // TODO:
    // Change service if api becomes available
    ClaimsServices.utilizationLegder({
      search: reports.search,
      range: reports.range,
      filter: reports.filter,
      sort: `${reports.orderBy}|${reports.order ? 'asc' : 'desc'}`,
      page: reports.page,
      limit: reports.count > 0 ? reports.count : reports.rowsPerPageOptions[0]
    }).then(res => {
      const { data } = res.data
      const { results = [] } = data
      const _reports = results.map(report => ({
        ...report,
        date_requested: DateFormatter(report.date_requested),
        check_date: DateFormatter(report.check_date),
        date_received: DateFormatter(report.date_received)
      }))

      const rows = _reports.map(report => {
        /**
         * Disabled: Change to dynamic mapping
         *  if ever needed to print cvs
         */
        // return columns.map(col => {
        //   if (col.type === 'array') console.log(formatData({ type: col.type, source: report, field: col.data }))
        //   return formatData({ type: col.type, source: report, field: col.data })
        // })
      })

      // const header = columns.map(c => c.label)

      // ArrayToCSV({ fileName: 'Treasury Transmittal Report', header, rows })
    })
  }

  const fetchReport = () => {
    if (reportType === 'member') {
      getMember(reports.search)
      // doRefreshMemberReport(refreshMemberReport + 1)
      return
    }

    if (reports.search === '') return
    LoadingService.loading(true)
    Promise.all([
      ClaimsServices.utilizationClient({
        search: reports.search,
        range: reports.range,
        filter: reports.filter,
        sort: `${reports.orderBy}|${reports.order ? 'asc' : 'desc'}`,
        page: reports.page,
        limit: reports.limit,
        type: graphFilter
      }),
      ClaimsReconServices.getClientUtilization({ client_account_no: reports.search }),
      CustomerCareServices.getClientUtilizationLoa({client_account_no: reports.search})]).then(res => {
      const [graphClientRes, clientUtilRes, clientUtilLoaRes] = res
      const { data: graph } = graphClientRes.data
      const { data: utilAmount } = clientUtilRes.data
      const { data: loaAmount } = clientUtilLoaRes.data

      // Total limit is just a placeholder, waiting for the endpoint // dirty fix
      setGraph(state => ({ ...state, total: graph.client.name !== 'Client Not found' ? 1000000 : 0, utilized: utilAmount.total_utilization_amount || 0, ibnr: loaAmount.total_ibnr_amount || 0 }))
      setGraphData(p => ({ ...p, initial: graph.results }))
      setClient(state => ({ ...state, name: graph.client.name, account_no: graph.client.account_number }))
      fetchTable()
    }).catch(err => console.error(`Error Fetching Data: ${err}`))
  }

  React.useEffect(() => {}, [graph])

  // Fetching table
  const fetchTable = () => {
    ClaimsServices.utilizationClientTable({
      search: reports.search,
      range: reports.range,
      filter: reports.filter,
      sort: `${reports.orderBy}|${reports.order ? 'asc' : 'desc'}`,
      page: reports.page,
      limit: reports.limit
    }).then(res => {
      const { results = [], page, count, limit } = res.data.data

      setReports(state => ({ ...state, data: results, count, page, limit }))
      LoadingService.loading(false)
    })
  }

  const formatData = ({ type = null, source, field }) => {
    let output = ''
    if (type === 'date') {
      output = DateFormatter(source[field])
    } else if (type === 'array') {
      const [root, property] = field.split('.')
      output = source[root] ? source[root].map(f => f[property]).join(`${'\r\n'}${'\r\n'}`) : ''
    } else {
      output = source[field] ? source[field] : ''
    }

    return output && output !== '' && output !== `${'\r\n'}${'\r\n'}` ? output : 'N/A'
  }

  const handleOnFilter = args => {
    const { field, value, match } = args

    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        setReports(state => ({ ...state, filter: { ...state.filter, [field]: { value, match } } }))
      }, 500)
    )
  }

  const handleOnPaginate = args => {
    setReports(state => ({ ...state, ...args }))
  }

  const handleSearchChange = event => {
    // /*
    //  * Debounce for searching through
    //  * SearchTextComponent
    //  */
    const search = event.target.value
    setReports(state => ({ ...state, search }))
    // clearTimeout(debounce)
    // setDebounce(
    // setTimeout(() => {
    //   }, 500)
    // )
  }

  const handleReportType = args => {
    setReportType(args.target.value)
  }

  const generateGraph = args => {
    /**
     * style={{ bottom: `${data.percentage === '100%' ? '80%' : data.percentage}` }}
     * -Prevents tooltip/popover description to display above the chart
     */
    return (
      <div className={classes['graph-container']}>
        <div className={classes['bar-chart-wrapper']}>
          {args.map((data, index) => (
            <div className={classes['bar-chart']}>
              <div
                className={classes.graph}
                style={{ height: `${data.percentage}`, background: theme[data.claim_type] }}
              >
                {''}
              </div>
              <div
                className={classes.tooltip}
                style={{ bottom: `${data.percentage === '100%' ? '80%' : data.percentage}` }}
              >
                {`${LargeNumberFormatter(data.total_amount / 1000).toString()}K`}
              </div>
            </div>
          ))}
        </div>
      </div>
    )
  }

  const getPercentage = (args = 0) => {
    // Returns argument on percentage scale
    const result = (args / 250000) * 100
    // Set maximum percentage to 100%
    return `${result > 100 ? 100 : result}%`
  }

  const handleGraphFilter = args => {
    // Triggers the filter watch function
    setGraphFilter(args)
  }

  const getMember = account_number => {
    CustomerCareServices.getMemberUtilizationLoa({ account_number: account_number }).then(res => {
      const { data } = res.data
      setUtilizationLoa(data)
    })
    setMemberFetchStatus('fetching')
    CustomerCareServices.getMember({ account_number: account_number })
      .then(res => {
        const { data } = res.data
        setMemberFetchStatus('success')
        setMember(data)
      })
      .catch(err => {
        const { data } = err.response
        // status === 404
        setMemberFetchStatus(`${data.status}`)
      })
  }

  return (
    <Paper className={classes.root}>
      <div className={classes.header}>
        <Typography variant='h6'>Report Type:</Typography>
        <div className={classes.content}>
          <div className={classes.filter}>
            <div className={classes.input}>
              <RadioGroup className={classes['radio-group']} value={reportType} onChange={handleReportType}>
                <div className={classes['radio-container']}>
                  <p>Corporate Client</p> <Radio color='primary' value='client' />
                </div>
                <div className={classes['radio-container']}>
                  <p> Member </p> <Radio color='primary' value='member' />
                </div>
              </RadioGroup>
            </div>
          </div>
        </div>
        <div className={classes.content}>
          <div className={classes.filter}>
            <SearchTextComponent
              id='search'
              value={reports.search}
              onChange={handleSearchChange}
              placeholder={reportType == 'client' ? `Search for Client ID or Client Name` : `Search for Member ID`}
            />
          </div>
        </div>
      </div>
      <div className={classes.header}>
        <Typography variant='h6'>Utilization Date Range</Typography>
        <div className={classes.content}>
          <div className={classes.filter}>
            <div className={classes.input}>
              <p>From: </p>
              <DatePicker
                id='ledger-filter-date-from'
                value={reports.range.from}
                onChange={event => {
                  setReports(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      from: DateFormatter(event)
                    }
                  }))
                }}
              />
            </div>
            <div className={classes.input}>
              <p>To: </p>
              <DatePicker
                id='ledger-filter-date-to'
                value={reports.range.to}
                onChange={event => {
                  setReports(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      to: DateFormatter(event)
                    }
                  }))
                }}
              />
            </div>
          </div>
        </div>
        <div className={classes.content}>
          <div className={classes.filter}>
            <Button
              className={`button-positive ${classes.submit}`}
              onClick={() => {
                fetchReport()
              }}
              variant='contained'
              color='primary'
            >
              Generate
            </Button>
          </div>
        </div>
      </div>
      <hr />
      {reportType && reportType === 'client' && (
        // TODO
        /**
         * Header-report:
         * -Waiting for data
         */
        <div className={classes.body}>
          <Typography variant='h6'>{client.name}</Typography>
          <div className={classes['header-report']}>
            <div className={classes['report-primary']}>
              <div className={classes.title}>Annual Benefit Limit</div>
              <Typography variant='h6'>{`PHP ${CurrencyFormatter(graph.total)}`}</Typography>
            </div>
            <div className={classes['report-details']}>
              {graph.total ? <div className={classes.graph}>
                <div className={classes.item} style={{ background: '#00a896', width: `${(graph.utilized / graph.total) * 100}%` }}>
                  {''}
                </div>
                <div className={classes.item} style={{ background: '#606c38', width: `${(graph.ibnr / graph.total) * 100}%` }}>
                  {''}
                </div>
                <div className={classes.item} style={{ background: '#e63946',
                  width: `${
                    ((graph.total - graph.ibnr - graph.utilized) / graph.total) * 100 < 0
                      ? 0
                      : ((graph.total - graph.ibnr - graph.utilized) / graph.total) * 100
                  }%` }}>
                  {''}
                </div>
              </div> : null}
              <div className={classes.legend}>
                <div className={classes.item}>
                  <div className={classes['header-data']}>
                    <div className={classes.mark} style={{ background: '#00a896' }}>
                      {''}
                    </div>
                    <div className={classes.text} style={{ color: '#00a896' }}>
                      utilized
                    </div>
                  </div>
                  <div className={classes.details}>{`PHP ${CurrencyFormatter(graph.utilized)}`}</div>
                </div>
                <div className={classes.item}>
                  <div className={classes['header-data']}>
                    <div className={classes.mark} style={{ background: '#606c38' }}>
                      {''}
                    </div>
                    <div className={classes.text} style={{ color: '#606c38' }}>
                      ibnr
                    </div>
                  </div>
                  <div className={classes.details}>{`PHP ${CurrencyFormatter(graph.ibnr)}`}</div>
                </div>
                <div className={classes.item}>
                  <div className={classes['header-data']}>
                    <div className={classes.mark} style={{ background: '#e63946' }}>
                      {''}
                    </div>
                    <div className={classes.text} style={{ color: 'e63946' }}>
                      available
                    </div>
                  </div>
                  <div className={classes.details}>{`PHP ${CurrencyFormatter(graph.total - graph.ibnr - graph.utilized)}`}</div>
                </div>
              </div>
            </div>
          </div>
          <Typography variant='h6'>Utilization Summary</Typography>
          <div className={classes.filter} style={{ textAlign: 'right' }}>
            <span>
              <FormControl>
                <Select
                  className={classes['select']}
                  value={graphFilter}
                  onChange={e => handleGraphFilter(e.target.value)}
                >
                  <MenuItem value={'all'}>
                    <Typography className={classes.input}>All Types</Typography>
                  </MenuItem>
                  {legend.map(data => (
                    <MenuItem value={data.type}>
                      <Typography className={classes.input}>{data.description}</Typography>
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </span>
          </div>
          <div className={classes['graph-legend']}>
            <div className={classes['legend-wrapper']}>
              {legend.map(data => (
                <div className={classes['legend-group']}>
                  <div className={classes['legend']} style={{ background: theme[data.type] }}>
                    {''}
                  </div>
                  <div className={classes['description']}>{data.description}</div>
                </div>
              ))}
            </div>
          </div>
          <div className={classes['graph-reports']}>
            <div className={classes['graph-plot']}>
              <div className={classes['plot-items']}>250k</div>
              <div className={classes['plot-items']}>150k</div>
              <div className={classes['plot-items']}>100k</div>
              <div className={classes['plot-items']}>50k</div>
              <div className={classes['plot-items']}>0</div>
            </div>
            {graphData.actual.map(data => (
              <div className={classes['graph-group']}>
                {generateGraph(data.claims)}
                <div className={classes['graph-category']}>
                  {months[data.month]} {graphData.actual && graphData.actual.length >= 12 ? data.year : ''}
                </div>
              </div>
            ))}
          </div>
          <div className={classes.table}>
            <Table
              TableContainerProps={{
                style: { maxHeight: '60vh', overflow: 'auto', position: 'relative', minHeight: '20vh' }
              }}
              TableProps={{ stickyHeader: true }}
              head={
                <TableRow>
                  {clientColumns.map((headCell, index) => (
                    <TableCell key={`table-header-${index}`}>
                      <TableSortLabel
                        active={reports.orderBy === headCell.data}
                        direction={reports.order ? 'asc' : 'desc'}
                        onClick={event => handleOnPaginate({ orderBy: headCell.data, order: !reports.order })}
                      >
                        {headCell.label}
                      </TableSortLabel>
                      {headCell.search && (
                        <div className={classes.input}>
                          {headCell.type && headCell.type === 'date' ? (
                            <DatePicker
                              noMargin
                              id={`${headCell.data}`}
                              onChange={event =>
                                handleOnFilter({
                                  field: headCell.data,
                                  value: DateFormatter(event),
                                  match: headCell.match
                                })
                              }
                            />
                          ) : (
                            <TextField
                              noMargin
                              onChange={event =>
                                handleOnFilter({
                                  field: headCell.data,
                                  value: event.target.value,
                                  match: headCell.match
                                })
                              }
                            />
                          )}
                        </div>
                      )}
                    </TableCell>
                  ))}
                </TableRow>
              }
              body={
                reports.data &&
                reports.data.map((claim, index) => (
                  <TableRow key={index}>
                    <TableCell>{claim.type}</TableCell>
                    <TableCell>{claim.total_amount.toFixed(2)}</TableCell>
                    <TableCell>{claim.cases}</TableCell>
                    <TableCell>{claim.availed_by_principals}</TableCell>
                    <TableCell>{claim.availed_by_dependents}</TableCell>
                  </TableRow>
                ))
              }
              pagination
              count={reports.count}
              page={reports.page}
              limit={reports.limit}
              rowsPerPageOptions={reports.rowsPerPageOptions}
              onChange={args => handleOnPaginate(args)}
            />
          </div>
        </div>
      )}

      {/* <ClaimsUtilLedgerReport
        refresh={refreshMemberReport}
        dateRange={{ from: reports.range.from, to: reports.range.to }}
        dataColumns={memberDataColumns}
        hideReport={reportType === 'client' ? 'true' : ''}
        hideHeader='true'
      /> */}

      {reportType && reportType === 'member' && (
        <div>
          <div className={classes.header}>
            <Typography variant='h6'>
              {SafeAccess(member, ['info', 'first_name'], '') +
                ' ' +
                SafeAccess(member, ['info', 'middle_name'], '') +
                ' ' +
                SafeAccess(member, ['info', 'last_name'], '')}
            </Typography>
            <Typography variant='h6'>{SafeAccess(member, ['info', 'brand_name'], '')}</Typography>
          </div>

          <div className={classes.content}>
            <table className={classes.memberinfo}>
              <tbody>
                <tr className={classes.memberinfoRow}>
                  <td className={classes.memberInfoLabel}>Type</td>
                  <td className={classes.memberInfoValue}>{SafeAccess(member, ['info', 'member_type'], '')}</td>

                  <td className={classes.memberInfoLabel}>Birthdate (Age)</td>
                  <td className={classes.memberInfoValue}>
                    {member &&
                      member.info &&
                      moment(SafeAccess(member, ['info', 'date_of_birth'], '')).format('MMMM DD, YYYY') +
                        ' (' +
                        moment().diff(SafeAccess(member, ['info', 'date_of_birth'], ''), 'years', false) +
                        ')'}
                  </td>

                  <td className={classes.memberInfoLabel}>Gender</td>
                  <td className={classes.memberInfoValue}>{SafeAccess(member, ['info', 'gender'], '')}</td>
                </tr>
                <tr className={classes.memberinfoRow}>
                  <td className={classes.memberInfoLabel}>Plant Type</td>
                  <td className={classes.memberInfoValue}>{SafeAccess(member, ['info', 'plan_type'], '')}</td>

                  <td className={classes.memberInfoLabel}>
                    {SafeAccess(member, ['benefit_limits', 1, 'type'], '')} Limit
                  </td>
                  <td className={classes.memberInfoValue}>
                    {SafeAccess(member, ['benefit_limits', 1, 'limit_unit'], '') +
                      ' ' +
                      SafeAccess(member, ['benefit_limits', 1, 'limit'], '')}
                  </td>

                  <td className={classes.memberInfoLabel}>Civil Status</td>
                  <td className={classes.memberInfoValue}>{SafeAccess(member, ['info', 'civil_status'], '')}</td>
                </tr>
                <tr className={classes.memberinfoRow}>
                  <td className={classes.memberInfoLabel}>R&amp;B</td>
                  <td className={classes.memberInfoValue}>{SafeAccess(member, ['info', 'plan_type'], '')}</td>

                  <td className={classes.memberInfoLabel}>
                    {SafeAccess(member, ['benefit_limits', 0, 'type'], '')} Limit
                  </td>
                  <td className={classes.memberInfoValue}>
                    {SafeAccess(member, ['benefit_limits', 0, 'limit_unit'], '') +
                      ' ' +
                      SafeAccess(member, ['benefit_limits', 0, 'limit'], '')}
                  </td>

                  <td className={classes.memberInfoLabel}>Effectivity Date</td>
                  <td className={classes.memberInfoValue}>
                    {moment(SafeAccess(member, ['info', 'effectivity_date'], '')).format('MMMM DD, YYYY')}
                  </td>
                </tr>
              </tbody>
            </table>
          </div>

          {/* TODO: replace with filler */}
          <br />

          <Utilization
            fetchService={ClaimsAdjudicationServices.getUtilizations}
            utilizationLoa={utilizationLoa}
            // isMember={isMember}
            member={member}
            // client={client}
            diagnosis={[]}
            dataColumns={memberDataColumns}
            hideSearchBar
            hideActionColumn
          />
        </div>
      )}
    </Paper>
  )
}
