import React from 'react'
import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper'
import moment from 'moment'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'

// Components
import CustomTextField from 'SharedComponents/TextField/CustomTextField'
import DatePicker from 'SharedComponents/DatePicker/DatePicker'
import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'
import TextFieldCurrency from 'SharedComponents/TextFieldCurrency/TextFieldCurrency'
import TextFieldNumber from 'SharedComponents/TextFieldNumber/TextFieldNumber'
import Alert from 'SharedComponents/Alert/Alert'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import BatchEncodingServices from 'Services/Api/BatchEncodingServices'

// Style
import classes from './NewBundlePage.module.scss'

// Validator
import NewBundleValidator from './NewBundleValidator'
export default function NewBundlePage (props) {
  const [disableControls, setDisableControls] = React.useState(false)
  const [openAlert, setOpenAlert] = React.useState({})
  const [newBundle, setNewBundle] = React.useState({
    bundle_number: '',
    total_rcs: 0,
    soa_number: '',
    facility_id: '',
    facility_name: '',
    total_soa_amt: 0,
    doctor_id: '',
    doctor_name: '',
    date_received: moment(),
    due_date_adjustment: 0,
    due_date: moment().add(30, 'days')
  })

  const [errors, setErrors] = React.useState({})

  const [suggestions, setSuggestions] = React.useState({
    medicalPartner: {
      id: [],
      name: [],
      data: []
    },

    doctorPartner: {
      id: [],
      name: [],
      data: []
    }
  })

  const [debounce, setDebounce] = React.useState({
    medicalPartner: null,
    doctorPartner: null
  })

  // Medical Partner (Facility) ////////////////////////////////////
  const getMedicalPartnerSuggestions = ({ value = '', by }) => {
    if (value.trim().length < 3) return

    clearTimeout(debounce.medicalPartner)
    setDebounce(state => ({
      ...state,
      medicalPartner: setTimeout(() => {
        if (value) {
          BatchEncodingServices.lookups({ on: 'medical-partner', params: { query: value, by } }).then(res => {
            const { data } = res.data
            let _items = []
            for (let d of data) {
              _items.push(d[by])
            }
            setSuggestions(state => ({ ...state, medicalPartner: { ...state.medicalPartner, [by]: _items, data } }))
          })
        }
      }, 500)
    }))
  }

  // Medical Partner (Doctor) ////////////////////////////////////
  const getDoctorPartnerSuggestions = ({ value = '', by }) => {
    if (value.trim().length < 3) return

    clearTimeout(debounce.doctorPartner)
    setDebounce(state => ({
      ...state,
      doctorPartner: setTimeout(() => {
        if (value) {
          BatchEncodingServices.lookups({ on: 'doctor-partner', params: { query: value, by } }).then(res => {
            const { data } = res.data
            let _items = []
            for (let d of data) {
              _items.push(d[by])
            }
            setSuggestions(state => ({ ...state, doctorPartner: { ...state.doctorPartner, [by]: _items, data } }))
          })
        }
      }, 500)
    }))
  }

  const handleOnChangeAutosuggest = args => {
    const { value, field } = args
    setNewBundle(state => ({ ...state, [field]: value }))
    handleOnChangeMedicalPartner(args)
  }

  const handleOnChangeMedicalPartner = ({ value, field, isSuggestionClick, selectedIndex }) => {
    setSuggestions(state => ({
      ...state,
      medicalPartner: {
        id: [],
        name: [],
        data: []
      },
      doctorPartner: {
        id: [],
        name: [],
        data: []
      }
    }))

    if (field === 'facility_id' || field === 'facility_name') {
      if (isSuggestionClick) {
        for (let i of suggestions.medicalPartner.data) {
          if (value === i.id || value === i.name) {
            // Compute due date based on date received and earliest term found
            // Fallback term is 30 days
            let term = 30
            if (i.earliest_term) {
              term = parseInt(i.earliest_term)
              if (isNaN(term) || term < 0) {
                term = 30
              }
            }
            let newDate = moment(newBundle.date_received).add(term, 'days')

            setNewBundle(state => ({
              ...state,
              facility_id: i.id,
              facility_name: i.name,
              due_date: newDate,
              due_date_adjustment: term
            }))
          }
        }
      } else {
        switch (field) {
          case 'facility_id':
            getMedicalPartnerSuggestions({ value, by: 'id' })
            break

          case 'facility_name':
            getMedicalPartnerSuggestions({ value, by: 'name' })
            break

          default:
            break
        }
      }
    } else if (field === 'doctor_id' || field === 'doctor_name') {
      if (isSuggestionClick) {
        if (!isNaN(selectedIndex)) {
          try {
            const i = suggestions.doctorPartner.data[selectedIndex]
            setNewBundle(state => ({ ...state, doctor_id: i.id, doctor_name: i.full_name }))
          } catch (ex) {}
        }
      } else {
        switch (field) {
          case 'doctor_id':
            getDoctorPartnerSuggestions({ value, by: 'id' })
            break

          case 'doctor_name':
            getDoctorPartnerSuggestions({ value, by: 'full_name' })
            break

          default:
            break
        }
      }
    }
  }

  React.useEffect(() => {
    PageTitleService(`Create New Bundle`)
    BreadcrumbsService.setSelectedItems(['dashboard', 'newBundle'])
    setNewBundle(state => ({ ...state, bundle_number: props.match.params.bundle_number }))
  }, [props.match.params.bundle_number])

  const validate = value => {
    const { error } = NewBundleValidator(value)
    setErrors(error || {})
    return !!error
  }
  const handleOnSave = () => {
    setDisableControls(true)
    setOpenAlert(state => ({
      ...state,
      show: false
    }))

    const data = {
      ...newBundle,
      date_received: newBundle.date_received.toISOString(),
      due_date: newBundle.due_date.toISOString()
    }

    const hasError = validate({
      bundle_number: data.bundle_number,
      total_rcs: data.total_rcs,
      date_received: data.date_received,
      due_date: data.due_date,
      soa_number: data.soa_number,
      total_soa_amt: data.total_soa_amt,
      facility_id: data.facility_id,
      facility_name: data.facility_name,
      doctor_id: data.doctor_id,
      doctor_name: data.doctor_name
    })

    if (hasError) {
      setDisableControls(false)
      return
    }
    // return validate({ total_rcs: data.total_rcs })

    LoadingService.loading(true)

    // Remove date due adjustment; it will cause an error on server
    delete data.due_date_adjustment

    BatchEncodingServices.updateBundle({
      data,
      bundle_number: newBundle.bundle_number
    }).then(res => {
      props.history.push(`/bundles/${newBundle.bundle_number}`)
    })
  }

  const cancel = () => props.history.push(`/`)

  return (
    <div className={classes.root}>
      <div className={classes['upload-container']}>
        <Link to={`/bundles/upload-soa`}>
          <Typography id='upload-soa-button' variant='subtitle2' className={`bold text-link ${classes.label}`}>
            + Upload SOA
          </Typography>
        </Link>
      </div>

      <Paper className={classes.form}>
        <Grid container spacing={3} className={classes.inputs}>
          <Grid item lg={3} md={4} sm={6} xs={12} className={classes.input}>
            <CustomTextField
              id='create-new-bundle-bundle-number'
              label='Bundle No.'
              value={newBundle.bundle_number}
              fullWidth
              disabled
              error={errors.bundle_number}
            />
          </Grid>

          <Grid item lg={3} md={4} sm={6} xs={12} className={classes.input}>
            <TextFieldNumber
              id='create-new-bundle-total-rcs'
              required
              label='Total No. of RCS'
              value={newBundle.total_rcs}
              fullWidth
              disabled={disableControls}
              onChange={e => setNewBundle(state => ({ ...state, total_rcs: e.target.value }))}
              error={errors.total_rcs}
            />
          </Grid>
        </Grid>

        <Grid container spacing={3} className={classes.inputs}>
          <Grid item lg={3} md={4} sm={6} xs={12} className={classes.input}>
            <DatePicker
              id='create-new-bundle-date-received'
              label='Date Received'
              value={newBundle.date_received}
              disabled={disableControls}
              onChange={date => {
                // Adjust due date accordingly to date received and term adjustment
                const due_date = moment(date).add(newBundle.due_date_adjustment, 'days')

                setNewBundle(state => ({ ...state, date_received: date, due_date: due_date }))
              }}
              error={errors.date_received}
            />
          </Grid>

          <Grid item lg={3} md={4} sm={6} xs={12} className={classes.input}>
            <DatePicker
              id='create-new-bundle-due-date'
              label='Due Date'
              disabled={disableControls}
              value={newBundle.due_date}
              onChange={date => setNewBundle(state => ({ ...state, due_date: date }))}
              error={errors.due_date}
            />
          </Grid>

          <Grid item lg={3} md={4} sm={6} xs={12} className={classes.input}>
            <CustomTextField
              id='create-new-bundle-soa-number'
              label='SOA No.'
              fullWidth
              disabled={disableControls}
              onChange={e => setNewBundle(state => ({ ...state, soa_number: e.target.value }))}
              error={errors.soa_number}
            />
          </Grid>

          <Grid item lg={3} md={4} sm={6} xs={12} className={classes.input}>
            <TextFieldCurrency
              id='create-new-bundle-total-soa'
              required
              label='Total SOA Amount'
              fullWidth
              value={newBundle.total_soa_amt}
              disabled={disableControls}
              onChange={e => setNewBundle(state => ({ ...state, total_soa_amt: e.target.value }))}
              error={errors.total_soa_amt}
            />
          </Grid>
        </Grid>

        <Grid container spacing={3} className={classes.inputs}>
          <Grid item lg={3} md={4} sm={6} xs={12} className={classes.input}>
            <AutoComplete
              id='create-new-bundle-facility-partner-id'
              required
              label='Medical Partner ID'
              value={newBundle.facility_id}
              disabled={disableControls}
              suggestions={suggestions.medicalPartner.id}
              onChange={(value, isSuggestionClick) =>
                handleOnChangeAutosuggest({ field: 'facility_id', value, isSuggestionClick })
              }
              error={errors.facility_id}
            />
          </Grid>

          <Grid item lg={3} md={4} sm={6} xs={12} className={classes.input}>
            <AutoComplete
              id='create-new-bundle-facility-partner-name'
              required
              label='Medical Partner Name (Facility)'
              value={newBundle.facility_name}
              disabled={disableControls}
              suggestions={suggestions.medicalPartner.name}
              onChange={(value, isSuggestionClick) =>
                handleOnChangeAutosuggest({ field: 'facility_name', value, isSuggestionClick })
              }
              error={errors.facility_name}
            />
          </Grid>

          <Grid item lg={3} md={4} sm={6} xs={12} className={classes.input}>
            <AutoComplete
              id='create-new-bundle-docotr-partner-id'
              label='Medical Partner ID'
              value={newBundle.doctor_id}
              disabled={disableControls}
              suggestions={suggestions.doctorPartner.id}
              onChange={(value, isSuggestionClick, selectedIndex) =>
                handleOnChangeAutosuggest({ field: 'doctor_id', value, isSuggestionClick, selectedIndex })
              }
              error={errors.doctor_id}
            />
          </Grid>

          <Grid item lg={3} md={4} sm={6} xs={12} className={classes.input}>
            <AutoComplete
              id='create-new-bundle-doctor-name'
              label='Medical Partner Name (Doctor)'
              value={newBundle.doctor_name}
              disabled={disableControls}
              suggestions={suggestions.doctorPartner.full_name}
              onChange={(value, isSuggestionClick, selectedIndex) =>
                handleOnChangeAutosuggest({ field: 'doctor_name', value, isSuggestionClick, selectedIndex })
              }
              error={errors.doctor_name}
            />
          </Grid>
        </Grid>

        <div className={classes.actions}>
          <Button variant='contained' color='primary' className='button-negative' onClick={cancel} id='cancel'>
            Cancel
          </Button>

          <Button
            variant='contained'
            color='primary'
            className={`${classes.save} button-positive`}
            onClick={() =>
              setOpenAlert(state => ({
                ...state,
                show: true,
                message: 'Are you sure you want to save this?'
              }))
            }
            disabled={disableControls}
            id='save'
          >
            Save
          </Button>

          <Alert
            open={openAlert.show}
            message={openAlert.message}
            cancelLabel='No'
            okLabel='Yes'
            onCancel={() => setOpenAlert(state => ({ ...state, show: false }))}
            onOk={handleOnSave}
          />
        </div>
      </Paper>
    </div>
  )
}
