// Received Path as Array
// Received Path as String

const SafeAccess = (value, path, def = undefined, delimeter = '.') => {
  if (typeof path === 'object') {
    return path.reduce((obj, key) => (obj && obj[key] !== 'undefined' ? obj[key] : undefined), value) || def
  } else if (typeof path === 'string') {
    return (
      path.split(delimeter).reduce((obj, key) => (obj && obj[key] !== 'undefined' ? obj[key] : undefined), value) || def
    )
  }
} // pass in your object structure as array elements

export { SafeAccess }
