/**
 * Performs transformation of specified data rows using the specified data mapping
 *
 * Data mapping given should contain the following keys:
 * <ul>
 *   <li>columns: for mapping columns from import file to Claims model</li>
 *   <li>fields: for mapping specific field values to Claims model</li>
 * </ul>
 *
 * Examples:
 * <code>
 *   {
 *     columns: SoaEtlController.COLUMN_TEMPLATES.DEFAULT,
 *     fields: {
 *               [SoaEtlController.MINIMUM_KEYS.BATCH_CLAIM_TYPE]: {}
 *             }
 *   }
 * </code>
 *
 * <code>
 *   {
 *     columns: {},
 *     fields: {
 *               [SoaEtlController.MINIMUM_KEYS.BATCH_CLAIM_TYPE]: {}
 *             }
 *   }
 * </code>
 *
 * @param dataRows data rows to be transformed
 * @param dataMapping data mapping information
 */
SoaEtlController.transform = function (dataRows, dataMapping, defaults = {}) {
  console.log('soa_etl', 'transform')

  // Initial check parameters
  if (!dataRows || !dataMapping || !dataMapping.columns || !dataMapping.fields[SoaEtlController.MINIMUM_KEYS.BATCH_CLAIM_TYPE]) throw { error: 'invalid parameters' }

  const prepData = SoaEtlController._transformPreviewDataToPrepData(dataRows, dataMapping, defaults)
  console.log(prepData)

  const bundle = SoaEtlController._transformPrepDataToHierarchicalData(prepData)
  console.log(JSON.stringify(bundle, null, 2))

  return bundle
}

/** 
 * Loads the specified bundle data unto the Claims server by invoking the appropriate API calls
 * for the bundle, batch and claims information found in the bundle data.
 * 
 * @param bundle bundle data from transform()
 */
SoaEtlController.loadToApi = function (bundle, successCallback) {

  let promise = new Promise(function (resolve, reject) {
    // TODO: refactor; callback hell

    // Invoke Create Bundle
    BatchEncodingServices.createBundle().then(res => {
      const { data } = res.data
      const bundleNumber = data
      console.log(bundleNumber)

      bundle.bundle_number = bundleNumber

      const bundleParam = { ...bundle }
      delete bundleParam.batches

      // Invoke Update Bundle
      BatchEncodingServices.updateBundle({ bundle_number: bundleNumber, data: bundleParam }).then(res => {

        // Create promise for each add batch operation (which contains add-claims operation)
        const addBatchPromises = bundle.batches.map(batch => {
          const batchParam = { ...batch }
          delete batchParam.claims

          // Create single promise for add batch
          const promise = new Promise((resolveBatchPromise, rejectBatchPromise) => {

            // Call create batch API
            BatchEncodingServices.createBatch({ data: batchParam, bundle_number: bundleNumber })
              .then(res => {
                const { data } = res.data
                batch.batch_number = data.batch_number

                async function addClaims() {
                  for (let i = 0; i < batch.claims.length; i++) {
                    const claim = batch.claims[i]
                    // Invoke create claim
                    await ClaimsEncodingServices.addClaim({ batch_number: batch.batch_number, data: claim })
                  }
                  resolveBatchPromise()
                }

                // Create claims for batch
                addClaims()

              })
              .catch(() => {
                rejectBatchPromise('Batch creation failed')
              })
          })

          return promise
        })

        // Note: VNI requirement, is to not perform automatic bundle submission 
        //       and consequentially, no automatic claims creation for claims under the
        //       automatically created batches. That is the function requirement. 
        //       The technical operation however, needs claims to be created but just 
        //       NOT visible as of yet to claims encoders (and up).
        // // Todo: refactor to wait for all batches to be created before submitting bundle
        // BatchEncodingServices.submitBundle({ bundle_number: bundleNumber }).then(res => {
        //   if (successCallback) { successCallback();} 
        //   resolve()
        //   // batch.claims.forEach(element => {
        //   //   let claim = element
        //   // })
        // }).catch(() => {
        //   reject('Bundle submission failed')
        // })


        // PromiseUtil.executeInSequence(addBatchPromises)
        Promise.all(addBatchPromises)
          .then(() => {
            console.log('success SOA loading unto API')
            //alert('success SOA loading unto API')
            resolve()
          })

      }).catch(() => {
        console.log('failed SOA loading unto API')
        //alert('failed SOA loading unto API')
        reject('Bundle creation failed')
      })

    })
  });

  return promise
}


/**
 * Performs transformation of "preview data rows" to "preparation data rows"
 *
 * Data mapping given should contain the following keys:
 * <ul>
 *   <li>columns: for mapping columns from import file to Claims model</li>
 *   <li>fields: for mapping specific field values to Claims model</li>
 * </ul>
 *
 * Examples:
 * <code>
 *   {
 *     columns: SoaEtlController.COLUMN_TEMPLATES.DEFAULT,
 *     fields: {
 *               [SoaEtlController.MINIMUM_KEYS.BATCH_CLAIM_TYPE]: {}
 *             }
 *   }
 * </code>
 *
 * <code>
 *   {
 *     columns: {},
 *     fields: {
 *               [SoaEtlController.MINIMUM_KEYS.BATCH_CLAIM_TYPE]: {}
 *             }
 *   }
 * </code>
 *
 * @param dataRows data rows to be transformed
 * @param dataMapping data mapping information
 *
 * @return mapped-out data rows
 */
SoaEtlController._transformPreviewDataToPrepData = function (dataRows, dataMapping, defaults = {}) {
  const outputEntries = []
  let prevEntry = {}
  dataRows.forEach(dataEntry => {
    const outputEntry = {}
    let hasSingleValue = false
    Object.keys(dataMapping.columns).forEach(columnKey => {
      let dataValue = dataEntry[columnKey]
      let outputKey = dataMapping.columns[columnKey]

      // If no value for the key
      if (!dataValue || dataValue.trim().length === 0) {
        dataValue = ''
        // Attempt to use previous row's value
        // dataValue = prevEntry[outputKey]
        // if (!dataValue) dataValue = ''
      } else {
        hasSingleValue = true
      }

      // Store upon output entry
      outputEntry[outputKey] = dataValue.trim()
    })

    if (hasSingleValue) {
      if (defaults) {
        Object.keys(defaults).forEach(key => {
          outputEntry[key] = defaults[key]
        })
      }

      Object.keys(dataMapping.fields).forEach(fieldKey => {
        let value = outputEntry[fieldKey]
        if (value && value.length > 0) outputEntry[fieldKey] = dataMapping.fields[fieldKey][value]
      })

      outputEntries.push(outputEntry)
    }

    // Keep track of previews row
    prevEntry = outputEntry
  })

  return outputEntries
}

/** 
 * Transforms Preparation Data into Hierarchical Data (bundle, batches and claims).
 * 
 * @param prepDataRows output from _transformPreviewDataToPrepData()
 * 
 * @return bundle object which includes underneath batches and claims objects
 */
SoaEtlController._transformPrepDataToHierarchicalData = function (prepDataRows) {
  let prevPrepEntry = {}

  let bundle = null
  let currentBatch = null
  let currentClaim = null
  let totalGrossAmount = 0

  prepDataRows.forEach(dataEntry => {

    const soaNumber = dataEntry[SoaEtlController.MINIMUM_KEYS.BUNDLE_SOA_NUM]
    if (!bundle) {
      let rcsTotal = dataEntry[SoaEtlController.MINIMUM_KEYS.BUNDLE_RCS_NUM]
      try {
        rcsTotal = parseInt(rcsTotal)
      } catch {
        throw 'Error: Invalid Total RCS (' + rcsTotal + ')'
      }

      // bundle = {
      //   soa: { number: soaNumber, amount: 0.0 },
      //   date: { received: dataEntry[SoaEtlController.MINIMUM_KEYS.BUNDLE_RCS_DATE] },
      //   facility: {
      //     id: dataEntry[SoaEtlController.MINIMUM_KEYS.BUNDLE_FACILITY_CODE],
      //     name: dataEntry[SoaEtlController.MINIMUM_KEYS.BUNDLE_FACILITY_NAME]
      //   },
      //   total_rcs: rcsTotal,
      //   batches: []
      // }
      const dateReceived = dataEntry[SoaEtlController.MINIMUM_KEYS.BUNDLE_RCS_DATE]
      bundle = {
        soa_number: soaNumber,
        total_soa_amt: 0.0,

        // TODO: uncomment
        date_received: moment(dataEntry[SoaEtlController.MINIMUM_KEYS.BUNDLE_RCS_DATE]).format('MM/DD/YYYY'),
        due_date: moment(dateReceived).add(30, 'days').format('MM/DD/YYYY'),

        facility_id: dataEntry[SoaEtlController.MINIMUM_KEYS.BUNDLE_FACILITY_CODE],
        facility_name: dataEntry[SoaEtlController.MINIMUM_KEYS.BUNDLE_FACILITY_NAME],
        total_rcs: rcsTotal,
        doctor_id: '',
        doctor_name: '',
        batches: []
      }
    }

    // Enforce there is only SOA number
    if (soaNumber !== '' && soaNumber !== bundle.soa_number) {
      throw 'Error: Another SOA Number found (' + soaNumber + ')'
    }

    // Fetch claim type and check if there is a change
    const claimType = dataEntry[SoaEtlController.MINIMUM_KEYS.BATCH_CLAIM_TYPE]
    if (!currentBatch || (claimType != '' && claimType != currentBatch.claim_type)) {
      // currentBatch = {
      //   claim: {
      //     type: claimType,
      //     source: dataEntry[SoaEtlController.MINIMUM_KEYS.BATCH_CLAIMS_SOURCE],
      //   },
      //   claims: []
      // }

      currentBatch = {
        claim_type: claimType,
        claim_source: dataEntry[SoaEtlController.MINIMUM_KEYS.BATCH_CLAIMS_SOURCE],
        doctor_id: '',
        doctor_name: '',
        total_rcs: 0,
        amount_billed: 0.0,
        notes: "SYSTEM GENERATED BATCH VIA SOA UPLOAD\n",
        claims: []
      }

      bundle.batches.push(currentBatch)
    }

    // Fetch approval code and check if there is a change
    const approvalCode = dataEntry[SoaEtlController.MINIMUM_KEYS.CLAIM_APPROVAL_CODE]
    if (!currentClaim || (approvalCode != '' && approvalCode != currentClaim[SoaEtlController.MINIMUM_KEYS.CLAIM_APPROVAL_CODE])) {

      // Increment total RCS for the parent batch
      currentBatch.total_rcs += 1

      const memberName = dataEntry[SoaEtlController.MINIMUM_KEYS.CLAIM_MEMBER_NAME]
      const nameParts = memberName.split(',')

      // currentClaim = {
      //   member: {
      //     account_number: dataEntry[SoaEtlController.MINIMUM_KEYS.CLAIM_MEMBER_NUM],
      //     last_name: nameParts[0].trim(),
      //     first_name: nameParts[1].trim(),
      //     middle_name: '',
      //   },
      //   approval_code: approvalCode,
      //   professional_fees: [],
      //   hospital_items: [],
      //   procedures: []
      // }

      currentClaim = {
        member_account_number: dataEntry[SoaEtlController.MINIMUM_KEYS.CLAIM_MEMBER_NUM],
        member_first_name: nameParts[1].trim(),
        member_middle_name: '',
        member_last_name: nameParts[0].trim(),

        facility_id: bundle.facility_id,
        facility_name: bundle.facility_name,

        approval_code: approvalCode,
        loa_number: '',

        diagnosis: [],
        professional_fees: [],
        hospital_items: [],
        procedures: [],

        date_from: dataEntry[SoaEtlController.MINIMUM_KEYS.CLAIM_DATE_ADMITTED],
        date_to: dataEntry[SoaEtlController.MINIMUM_KEYS.CLAIM_DATE_DISCHARGE],

        claim_sub_type: 'Surgical',
        claim_sub_sub_type: 'Physical',

        with_corporate_guarantee: '',
        loa_notes: '',

      }

      if (currentBatch.claim_type === 'In Patient') {
        currentClaim.room_type = 'WARD'
        currentClaim.room_rate = 0.0
        currentClaim.pro_id = 'N/A'
        currentClaim.pro_name = 'N/A'
      }

      currentBatch.claims.push(currentClaim)
    }

    // Check for diagnosis entries  
    const diagnosisName = dataEntry[SoaEtlController.MINIMUM_KEYS.CLAIM_DIAGNOSIS_NAME]
    const diagnosisCode = dataEntry[SoaEtlController.MINIMUM_KEYS.CLAIM_DIAGNOSIS_CODE]
    if (diagnosisName && diagnosisCode) {
      const flag = currentClaim.diagnosis.length == 0
      currentClaim.diagnosis.push({ code: diagnosisCode, description: diagnosisName, primary: flag })
    }

    let lineItem = null
    let lineAmount = null

    // Check for line item - professional fee 
    lineItem = dataEntry[SoaEtlController.MINIMUM_KEYS.CLAIM_DOCTOR_NAME]
    lineAmount = dataEntry[SoaEtlController.MINIMUM_KEYS.CLAIM_DOCTOR_AMOUNT]
    if (lineItem && lineAmount) {
      try {
        lineAmount = parseFloat(lineAmount)
      } catch {
        lineAmount = 0.0
      }
 
      bundle.total_soa_amt += lineAmount
      currentBatch.amount_billed += lineAmount
      
      currentClaim.professional_fees.push({
        doctor_name: lineItem, doctor_id: "unknown", gross_amount: lineAmount,
        specialization: "unknown", vat_amount: 0, amount_paid_by_member: 0,
        philhealth_amount_paid: 0, philhealth_remarks: "", amount_not_covered: 0, reason: "", amount_due_from_member: 0, reason_for_charging_member: ""
      })
    }

    if (currentBatch.claim_type !== 'Out Patient Procedure') {
      // Check for line item - hospital item 
      lineItem = dataEntry[SoaEtlController.MINIMUM_KEYS.CLAIM_HOSPITAL_ITEM_NAME]
      lineAmount = dataEntry[SoaEtlController.MINIMUM_KEYS.CLAIM_HOSPITAL_ITEM_AMOUNT]
      if (lineItem && lineAmount) {
        try {
          lineAmount = parseFloat(lineAmount)
        } catch {
          lineAmount = 0.0
        }

        bundle.total_soa_amt += lineAmount
        currentBatch.amount_billed += lineAmount

        currentClaim.hospital_items.push({
          name: lineItem, item_code: "unknown", gross_amount: lineAmount,
          vat_amount: 0, amount_paid_by_member: 0, philhealth_amount_paid: 0, philhealth_remarks: "", amount_not_covered: 0, reason: "", amount_due_from_member: 0, reason_for_charging_member: ""
        })
      }
    }

    if (currentBatch.claim_type !== 'Out Patient Consultation') {
        // Check for line item - procedure
      lineItem = dataEntry[SoaEtlController.MINIMUM_KEYS.CLAIM_PROCEDURE_NAME]
      lineAmount = dataEntry[SoaEtlController.MINIMUM_KEYS.CLAIM_PROCEDURE_AMOUNT]
      if (lineItem && lineAmount) {
        try {
          lineAmount = parseFloat(lineAmount)
        } catch {
          lineAmount = 0.0
        }

        bundle.total_soa_amt += lineAmount
        currentBatch.amount_billed += lineAmount

        currentClaim.professional_fees.push({
          description: lineItem, gross_amount: lineAmount,
          code: "unknown", vat_amount: 0, amount_paid_by_member: 0, philhealth_amount_paid: 0, philhealth_remarks: ""
        })
      }
    }

  })

  return bundle
}


