import React from 'react'

// Material Components
import Button from '@material-ui/core/Button'
import Paper from '@material-ui/core/Paper'
import Table from 'SharedComponents/Table/Table'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import Typography from '@material-ui/core/Typography'
import GetAppIcon from '@material-ui/icons/GetApp'
import PrintIcon from '@material-ui/icons/Print'

// Shared Components
import DatePicker from 'SharedComponents/DatePicker/DatePicker'
import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'
import { PdfViewer2Service } from 'SharedComponents/PdfViewer2/PdfViewer2'
import { CurrencyFormatter } from 'Utilities/TextFormatter'

// Components
import Footer from './Footer/Footer'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import CustomerCareServices from 'Services/Api/CustomerCareServices'
import ClaimsServices from 'Services/Api/ClaimsServices'
import UserManagementServices from 'Services/Api/UserManagementServices'
import ArrayToCSV from 'Services/ArrayToCSV'

// Utility
import { DateFormatter } from 'Utilities/TextFormatter'
import { staticHeaderFooter } from './helper/staticHeaderFooter'

// Styles
import classes from './TransmittalReport.module.scss'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { BatchEncoding as actions } from 'Pages/Protected/Claims/Store/actions'

const { initSuggestions, resetSuggestionObject, setSuggestionObject } = actions

const columns = [
  { label: 'NO.', sortField: 'number', sort: false, align: 'left', class: 'h_no' },
  { label: 'LOA NO.', sortField: 'latest.loa_number', sort: true, align: 'left', class: 'h_loa' },
  { label: 'DATE', sortField: 'status_date', sort: true, align: 'left', class: 'h_date' },
  { label: 'MEMBER', sortField: 'latest.member.last_name', sort: true, align: 'left', class: 'h_member' },
  { label: 'AMOUNT', sortField: 'total_amount', sort: false, align: 'right', class: 'h_amount' },
  { label: 'AR', sortField: 'total_due_from_member', sort: false, align: 'right', class: 'h_ar' }
]

export default function TransmittalReport (props) {
  const dispatch = useDispatch()

  const suggestions = useSelector(({ BatchEncoding: state }) => state.suggestions)

  const [claims, setClaims] = React.useState({
    raw: [],
    data: [],
    search: '',
    range: { from: new Date().toISOString(), to: new Date().toISOString(), field: 'status_date' },
    orderBy: 'loa_number',
    order: true,
    page: 1,
    limit: 10,
    count: 0,
    rowsPerPageOptions: [10, 15, 20]
  })

  const [client, setClient] = React.useState({
    registered_name: '',
    corporate_account_no: ''
  })

  const [returnClient, setReturnClient] = React.useState({})

  const [total, setTotal] = React.useState({
    amount: 0,
    ar_amount: 0
  })

  const [username, setUsername] = React.useState('')

  React.useEffect(() => {
    dispatch(initSuggestions())
    PageTitleService('Transmittal Report')
    BreadcrumbsService.setSelectedItems(['transmittal'])

    // TODO is this right?
    const _id = localStorage.getItem('user_id')
    UserManagementServices.getUserById(_id).then(res => {
      const { username = '' } = res.data
      setUsername(username)
    })
  }, [])

  const [debounce, setDebounce] = React.useState(null)

  const handleClientOnChange = ({ value, field, isSuggestionClick }) => {
    setClient(state => ({ ...state, [field]: value }))
    if (isSuggestionClick) {
      for (const i of suggestions.client.data) {
        if (value === i.registered_name || value === i.corporate_account_no) {
          dispatch(resetSuggestionObject({ field: 'client' }))
          setClient(state => ({
            ...state,
            registered_name: i.registered_name,
            corporate_account_no: i.corporate_account_no
          }))
          return
        }
      }
    } else {
      dispatch(resetSuggestionObject({ field: 'client' }))
      getClientSuggestions({ value, field })
    }
  }

  const getClientSuggestions = ({ value, field }) => {
    if (value.trim().length < 3) return
    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        if (value) {
          CustomerCareServices.getClientInfo({ value, field }).then(res => {
            const { data = [] } = res.data

            const item = []
            for (const d of data) {
              item.push(d[field])
            }

            dispatch(
              setSuggestionObject({
                field: 'client',
                suggestion: { [field]: item, data }
              })
            )
          })
        }
      }, 500)
    )
  }

  const fetchTransmittalItems = params => {
    if (client.registered_name === '' || client.corporate_account_no === '') {
      return setClaims(state => ({ ...state, data: [] }))
    }

    ClaimsServices.transmittalReport({
      range: params.range,
      sort: `${params.orderBy}|${params.order ? 'asc' : 'desc'}`,
      page: params.page,
      limit: params.limit,
      client
    }).then(res => {
      const { results = [], raw = [], count, limit, page, client = {} } = res.data.data
      const _totam_amout = raw.reduce((a, c) => a + c.total_amount, 0)
      const _ar_amount = raw.reduce((a, c) => a + c.total_due_from_member, 0)

      setReturnClient(client)
      setClaims(state => ({ ...state, data: results, raw, count, page, limit }))
      setTotal(state => ({
        ...state,
        amount: !_totam_amout <= 0 ? _totam_amout : 0,
        ar_amount: !_ar_amount <= 0 ? _ar_amount : 0
      }))
    })
  }

  const handleOnPaginate = args => {
    const params = {
      ...claims,
      ...args
    }

    setClaims(state => ({ ...state, ...args }))
    fetchTransmittalItems(params)
  }

  const handlePrintReport = params => {
    if (!claims.data.length) return

    PdfViewer2Service({
      name: 'Transmittal Report',
      fileName: 'transmittal_report',
      service: ClaimsServices.getTransmittalReportPdf({
        range: params.range,
        sort: `${params.orderBy}|${params.order ? 'asc' : 'desc'}`,
        client
      }),
      open: true
    })
  }

  const handleGenerateReport = () => {
    if (!claims.raw.length) return

    const header = columns.map(i => i.label)
    const rows = []

    claims.raw.forEach(item => {
      item.member = `${item.member.last_name}, ${item.member.first_name} ${item.member.middle_name}`
      item.date = DateFormatter(item.date)
      rows.push(Object.values(item))
    })

    const statics = staticHeaderFooter({ ...returnClient, ...total, username })

    ArrayToCSV({ fileName: `${client.registered_name} Transmittal Report`, header, rows, statics })
  }

  return (
    <Paper className={classes.root}>
      <div className={classes.header}>
        <div className={classes['search-header']}>
          <div className={classes['search-header-child']}>
            <div className={classes.column}>
              <Typography variant='h6'>Company:</Typography>
            </div>
            <div className={classes.column}>
              <div className={classes['input']}>
                <AutoComplete
                  placeholder='Search by company name'
                  id='company-name'
                  value={client.registered_name}
                  suggestions={suggestions.client.registered_name}
                  onChange={(value, isSuggestionClick) =>
                    handleClientOnChange({
                      field: 'registered_name',
                      value,
                      isSuggestionClick
                    })
                  }
                />
              </div>
            </div>
          </div>
          <div className={classes['search-header-child']}>
            <div className={classes.column}>
              <Typography variant='h6'>Account No.:</Typography>
            </div>
            <div className={classes.column}>
              <div className={classes['input']}>
                <AutoComplete
                  placeholder='Search by account number'
                  id='account-number'
                  value={client.corporate_account_no}
                  suggestions={suggestions.client.corporate_account_no}
                  onChange={(value, isSuggestionClick) =>
                    handleClientOnChange({
                      field: 'corporate_account_no',
                      value,
                      isSuggestionClick
                    })
                  }
                />
              </div>
            </div>
          </div>
        </div>
        <Typography variant='h6'>Date Range: </Typography>
        <div className={classes.content}>
          <div className={classes.filter}>
            <div className={classes.input}>
              <p>From: </p>
              <DatePicker
                id='filter-date-from'
                value={claims.range.from}
                onChange={event =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      from: DateFormatter(event)
                    }
                  }))
                }
              />
            </div>
            <div className={classes.input}>
              <p>To: </p>
              <DatePicker
                id='filter-date-to'
                value={claims.range.to}
                onChange={event =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      to: DateFormatter(event)
                    }
                  }))
                }
              />
            </div>
            <Button
              className={`button-positive ${classes.submit}`}
              onClick={() => fetchTransmittalItems(claims)}
              variant='contained'
              color='primary'
            >
              Filter
            </Button>
          </div>
          <div className={classes.action}>
            <Button id='print-report' onClick={() => handlePrintReport(claims)} disabled={!claims.data.length}>
              <PrintIcon fontSize='small' />
              <Typography variant='caption' className={`bold ${!claims.data.length ? classes.disabled : classes.typo}`}>
                Print Report
              </Typography>
            </Button>
            <Button id='generate-report' onClick={() => handleGenerateReport()} disabled={!claims.data.length}>
              <GetAppIcon fontSize='small' />
              <Typography variant='caption' className={`bold ${!claims.data.length ? classes.disabled : classes.typo}`}>
                Generate Report
              </Typography>
            </Button>
          </div>
        </div>
      </div>
      <div className={classes.body}>
        <div className={classes.table}>
          <Table
            TableContainerProps={{ style: { maxHeight: '60vh', overflow: 'auto', position: 'relative' } }}
            head={
              <TableRow>
                {columns.map((headCell, index) => (
                  <TableCell className={classes[headCell.class]} align={headCell.align} key={`table-header-${index}`}>
                    {headCell.sort ? (
                      <TableSortLabel
                        active={claims.orderBy === headCell.sortField}
                        direction={claims.order ? 'asc' : 'desc'}
                        onClick={event => handleOnPaginate({ orderBy: headCell.sortField, order: !claims.order })}
                      >
                        {headCell.label}
                      </TableSortLabel>
                    ) : (
                      headCell.label
                    )}
                  </TableCell>
                ))}
              </TableRow>
            }
            body={
              claims.data.length ? (
                claims.data.map((item, index) => (
                  <TableRow key={index}>
                    <TableCell align='left'>{item.no}</TableCell>
                    <TableCell className={classes.left} align='left'>
                      {item.loa_number}
                    </TableCell>
                    <TableCell className={classes.left} align='left'>
                      {DateFormatter(item.date)}
                    </TableCell>
                    <TableCell
                      className={classes.member}
                      align='left'
                    >{`${item.member.last_name}, ${item.member.first_name} ${item.member.middle_name}`}</TableCell>
                    <TableCell className={classes.right} align='right'>
                      {CurrencyFormatter(!item.total_amount <= 0 ? item.total_amount : 0)}
                    </TableCell>
                    <TableCell className={classes.right} align='right'>
                      {CurrencyFormatter(!item.total_due_from_member <= 0 ? item.total_due_from_member : 0)}
                    </TableCell>
                  </TableRow>
                ))
              ) : (
                <TableRow>
                  <TableCell className={classes.handling} colSpan={columns.length}>
                    No data found
                  </TableCell>
                </TableRow>
              )
            }
            pagination
            count={claims.count}
            page={claims.page}
            limit={claims.limit}
            rowsPerPageOptions={claims.rowsPerPageOptions}
            onChange={args => handleOnPaginate(args)}
          />
          <Footer username={username} total={total} />
        </div>
      </div>
    </Paper>
  )
}
