import React from 'react'

import ValueField from '../ValueField/ValueField'

import classes from './FloatingFields.module.scss'

export default function FloatingFields (props) {
  // Variables
  const { elements, node } = props
  // Events

  const [fields, setFields] = React.useState([])

  React.useEffect(() => {
    if (!elements) return
    const values = elements.map(elem => ({ ...elem, value: elem.data(node) }))
    setFields(values)
  }, [props])

  return (
    <div className={classes.root}>
      <div className={classes.wrapper}>
        {fields.map(
          field =>
            field.value && (
              <div key={field.id} className={classes[field.layout ? `w-${field.layout}` : 'w-100']}>
                <ValueField
                  key={`${field.id}`}
                  id={field.id}
                  title={field.title}
                  label={field.label}
                  value={field.value}
                />
              </div>
            )
        )}
      </div>
    </div>
  )
}
