import Select from '@material-ui/core/Select'

import React from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'
import MenuItem from '@material-ui/core/MenuItem'
import { Link } from 'react-router-dom'

import classes from './DropDown.module.scss'

/**
 * Props
 * id required
 * label
 * required
 * value
 * items array {value, text}
 * onChange
 *
 */
export default function DropDown (props) {
  const {
    id,
    label = '',
    required,
    items = [],
    readOnly,
    onChange,
    disableComponents = false,
    addNew = { show: false, value: 'add-new', text: 'Add New' },
    error,
    ...other
  } = props
  const { value = '' } = props

  const [focus, setFocus] = React.useState()
  const [_error, setError] = React.useState()

  React.useEffect(() => {
    if (typeof value === 'string') {
      if (!value.trim()) setError(error)
    }
  }, [error, value])

  const handleChange = event => {
    setError()
    if (typeof onChange === 'function') onChange(event)
  }

  return (
    <div className={classes.root}>
      <Typography variant='subtitle2' className={classes.label}>
        {label}
        {!readOnly && required ? ' *' : ''}
      </Typography>
      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {value}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper
              className={`${classes['select-container']} ${
                _error ? classes['has-error'] : focus && classes.focus
              } box-shadow-field ${disableComponents && classes.disabled} box-shadow-field`}
            >
              {items && (
                <Select
                  id={id}
                  onChange={handleChange}
                  onFocus={() => setFocus(true)}
                  onBlur={() => setFocus(false)}
                  classes={{ root: classes.select }}
                  input={<InputBase className={classes.input} />}
                  {...other}
                  disabled={disableComponents}
                >
                  {items.map((item, index) => (
                    <MenuItem key={`${id}-items${index}`} id={`${id}-items${index}`} value={item.value}>
                      {item.text}
                    </MenuItem>
                  ))}
                  {addNew.show && (
                    <MenuItem key={`${id}-items-add-new`} id={`${id}-items-add-new`} value={addNew.value}>
                      {addNew.text}
                    </MenuItem>
                  )}
                </Select>
              )}
            </Paper>

            {_error && <span className={classes.error}>{_error}</span>}
          </React.Fragment>
        )}
      </div>
    </div>
  )
}
