import React from 'react'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
// import TableSortLabel from '@material-ui/core/TableSortLabel'
import Typography from '@material-ui/core/Typography'
import { CurrencyFormatter, DateFormatter } from 'Utilities/TextFormatter'

// Components
import Table from 'SharedComponents/Table/Table'
import TextField from 'SharedComponents/TextField/CustomTextField'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'

// Services
import IcdCptStorageServices from 'Services/Storage/IcdCptStorageServices'
import { setPaginate } from './Utils/pagination'
import { mapData } from './Utils/mapData'
import { itemFilter } from './Utils/query'

// Styles
import classes from './Utilization.module.scss'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { ClaimsAnalysis as actions } from 'Pages/Protected/Claims/Store/actions'
const { setLimitType, resetLimitType } = actions

const headCellsComplete = [
  { name: 'Claim ID', sortField: 'claim_no', search: true },
  { name: 'Diagnosis', sortField: 'diagnosis', search: true },
  { name: 'Procedure', sortField: 'procedures', search: true },
  { name: 'Provider', sortField: 'provider', search: true },
  { name: 'Amount', sortField: 'amount', search: true },
  { name: 'Session', sortField: 'session', search: true },
  { name: 'Date', sortField: 'date', search: true },
  { name: 'Type', sortField: 'type', search: true },
  { name: 'Status', sortField: 'status', search: true }
]

const randomRange = (min, max) => {
  return Math.random() * (max - min) + min
}

export default function Utilization (props) {
  const dispatch = useDispatch()
  const title = useSelector(({ ClaimsAnalysis: state }) => state.title)

  const {
    fetchService,
    diagnosis,
    isMember = true,
    member = {},
    utilizationLoa = [],
    hideSearchBar = false,
    hideActionColumn = false,
    dataColumns
  } = props

  const [headCells, setHeadCells] = React.useState(headCellsComplete)
  const [rows, setRows] = React.useState([])
  const [allList, setAllList] = React.useState([])
  const [filteredRows, setFilteredRows] = React.useState({
    data: [],
    orderBy: '',
    order: 'desc',
    category: 'all',
    search: '',
    filter: {},
    count: 0,
    page: 1,
    limit: 10,
    rowsPerPageOptions: [10, 15, 20]
  })

  const [graph, setGraph] = React.useState({
    utilized: 0,
    ibnr: 0,
    total: 0
  })

  React.useEffect(() => {
    // Goal: Adjust columns to show based on data columns passed as props

    let filtered = null
    if (!dataColumns || dataColumns.length === 0) {
      filtered = headCellsComplete
    } else {
      filtered = headCellsComplete.filter(entry => dataColumns.indexOf(entry.sortField) > -1)
    }
    setHeadCells(filtered)
  }, [dataColumns])

  React.useEffect(() => {
    if (member) {
      let type
      let limit

      if (member.benefit_limits) {
        type = member.benefit_limits[0].type
        limit = member.benefit_limits[0].limit
      }

      if (type === 'ABL') {
        dispatch(setLimitType('Annual Benefit Limit'))

        setGraph(state => ({
          ...state,
          total: limit || randomRange
        }))

        fetchService({
          member_account_no: member.info.member_id,
          type: '',
          icd_code: ''
        }).then(res => {
          const { data } = res.data

          const list = mapData({ data: [...utilizationLoa.map(i => ({ ...i, type: 'IBNR' })), ...data.map(i => ({ ...i, status: 'Approved' }))] })

          setFilteredRows(state => ({ ...state, count: list.length, data: list }))
          setRows(list.slice(0, filteredRows.limit))
          setAllList(list)

          let ibnr = 0
          let utilized = 0

          utilizationLoa.map(item => (ibnr += item.total_amount))
          data.map(item => (utilized += item.grand_total))

          setGraph(state => ({
            ...state,
            ibnr,
            utilized
          }))
        })
      } else if (type === 'MBL') {
        dispatch(setLimitType('Maximum Benefit Limit'))

        setGraph(state => ({
          ...state,
          total: limit || randomRange
        }))

        let primary = (diagnosis && diagnosis.filter(icd => icd.primary === true)[0]) || {
          code: '',
          description: '',
          category_code: ''
        }

        IcdCptStorageServices.lookups({
          on: 'icd-10',
          params: { query: primary.code, by: 'code' }
        }).then(res => {
          const { data } = res.data
          const { diagnosis_code } = data[0] || []
          primary.category_code = diagnosis_code

          fetchService({
            member_account_no: member.info.member_id,
            type: 'mbl',
            icd_code: primary.category_code
          }).then(res => {
            const { data } = res.data

            // ICD Screening
            let _utilizationLoa = utilizationLoa.filter(item => {
              if (item.primary_icd10_code) {
                return item.primary_icd10_code.startsWith(primary.category_code)
              }
            })

            const list = mapData({ data: [..._utilizationLoa.map(i => ({ ...i, type: 'IBNR' })), ...data.map(i => ({ ...i, status: 'Approved' }))] })

            setFilteredRows(state => ({ ...state, count: list.length, data: list }))
            setRows(list.slice(0, filteredRows.limit))
            setAllList(list)

            let ibnr = 0
            let utilized = 0

            _utilizationLoa.map(item => (ibnr += item.total_amount))
            data.map(item => (utilized += item.grand_total))

            setGraph(state => ({
              ...state,
              ibnr,
              utilized
            }))
          })
        })
      }
    } else {
      dispatch(setLimitType('Member Not Found'))
    }
  }, [member, diagnosis])

  React.useEffect(() => {
    dispatch(resetLimitType())
  }, [])

  React.useEffect(() => {
    // console.log(list)
    const _data = itemFilter({ search: filteredRows.search, filter: filteredRows.filter, data: allList })

    setFilteredRows(state => ({ ...state, count: _data.length, data: _data, page: 1 }))
    setRows(_data.slice(0, filteredRows.limit))
  }, [filteredRows.search, filteredRows.filter])

  const [debounce, setDebounce] = React.useState(null)

  const handleSearchChange = args => {
    const { event, field, type } = args
    const search = event.target.value
    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        if (type === 'all') setFilteredRows(state => ({ ...state, search }))
        else setFilteredRows(state => ({ ...state, filter: { ...state.filter, [field]: search } }))
      }, 500)
    )
  }

  const handleOnPaginate = args => {
    const { data, limit, page } = setPaginate({ args, exc: filteredRows })
    setFilteredRows(state => ({ ...state, limit, page }))
    setRows(data)
  }

  const renderRows = ({ rows = [] }) => {
    return rows.map((row, index) => (
      <TableRow id={`utilization-row-${index}`} key={`claim-${index}`} hover role='checkbox' tabIndex={-1}>
        {headCells.map(headCell => {
          switch (headCell.sortField) {
            case 'claim_no':
              return (
                <TableCell key={`${index}-claim-no`} id={`utilization-claim_no-${index}`}>
                  <div className={classes.cell}>
                    <span>{row.claim_no}</span>
                    <span className={classes.label}>SWIPE</span>
                    <span>YES</span>
                  </div>
                </TableCell>
              )

            case 'diagnosis':
              return (
                <TableCell key={`${index}-diagnosis`} id={`utilization-diagnosis-${index}`}>
                  {row.diagnosis.map((row, index) => (
                    <div key={`icd-${index}`} className={classes.cell}>
                      <span>{row.diagnosis}</span>
                      <span className={classes.label}>ICD 10 CODE</span>
                      <span>{row.icd10_code}</span>
                    </div>
                  ))}
                </TableCell>
              )

            case 'procedures':
              return (
                <TableCell key={`${index}-procedures`} id={`utilization-procedures-${index}`}>
                  <div className={classes.cell}>
                    {row.procedures.map((row, index) => (
                      <div key={`procedure-${index}`} className={classes.cell}>
                        <span>{row.procedure_name}</span>
                        <span className={classes.label}>CPT CODE</span>
                        <span>{row.cpt_code}</span>
                      </div>
                    ))}
                  </div>
                </TableCell>
              )

            case 'provider':
              return (
                <TableCell key={`${index}-provider`} id={`utilization-partner_name-${index}`}>
                  <div className={classes.cell}>
                    <div className={classes.cell}>
                      {row.provider.facility.map((row, index) => (
                        <span key={`facility-${index}`}>{row}</span>
                      ))}
                      {row.provider.doctor.length !== 0 && <span className={classes.label}>MEDICAL DOCTOR</span>}
                      {row.provider.doctor.map((row, index) => (
                        <span key={`doctor-${index}`}>{`${row},`}</span>
                      ))}
                    </div>
                  </div>
                </TableCell>
              )

            case 'amount':
              return (
                <TableCell key={`${index}-amount`} align='right' id={`utilization-grand_total-${index}`}>
                  <span>{CurrencyFormatter(row.amount)}</span>
                </TableCell>
              )

            case 'session':
              return (
                <TableCell key={`${index}-session`} align='right' id={`utilization-session-${index}`}>
                  <span>{row.session}</span>
                </TableCell>
              )

            case 'date':
              return (
                <TableCell key={`${index}-date_admitted`} id={`utilization-date_admitted-${index}`}>
                  <span>{row.date}</span>
                </TableCell>
              )

            case 'type':
              return (
                <TableCell key={`${index}-type`} id={`utilization-type-${index}`}>
                  <span>{row.type}</span>
                </TableCell>
              )

            case 'status':
              return (
                <TableCell key={`${index}-status`} align='center' id={`utilization-status-${index}`}>
                  <span>{row.status}</span>
                </TableCell>
              )
          }
        })}

        {hideActionColumn === false && (
          <TableCell key={`${index}-icon`} align='center' id={`utilization-action-${index}`}>
            <FontAwesomeIcon icon='file-alt' size='lg' className={classes.icon} />
          </TableCell>
        )}
      </TableRow>
    ))
  }

  return (
    <div className={classes.root}>
      <div className={classes.header}>
        <div className={classes.graph}>
          <div className={classes['anual-benefit']}>
            <Typography variant='button'>{title}</Typography>
            <Typography id='utilization-graph-total' variant='h6'>{`PHP ${CurrencyFormatter(graph.total)}`}</Typography>
          </div>

          <div className={classes['line-graph']}>
            <div className={classes.container}>
              <div className={classes['line-container']}>
                <span
                  className={classes.item}
                  style={{ width: `${(graph.utilized / graph.total) * 100}%`, background: '#4fb083' }}
                />
                <span
                  className={classes.item}
                  style={{ width: `${(graph.ibnr / graph.total) * 100}%`, background: '#2a2b6e' }}
                />
                <span
                  className={classes.item}
                  style={{
                    width: `${
                      ((graph.total - graph.ibnr - graph.utilized) / graph.total) * 100 < 0
                        ? 0
                        : ((graph.total - graph.ibnr - graph.utilized) / graph.total) * 100
                    }%`,
                    background: '#f8b439'
                  }}
                />
              </div>
              <div className={classes['line-details']}>
                <div className={classes.item}>
                  <div className={classes.label}>
                    <span className={classes.color} style={{ background: '#4fb083' }} />
                    <Typography variant='button'>UTILIZED</Typography>
                  </div>
                  <Typography id='utilization-graph-utilized' variant='button' style={{ color: '#4fb083' }}>
                    {`PHP ${CurrencyFormatter(graph.utilized)}`}
                  </Typography>
                </div>
                <div className={classes.item}>
                  <div className={classes.label}>
                    <span className={classes.color} style={{ background: '#2a2b6e' }} />
                    <Typography variant='button'>IBNR</Typography>
                  </div>
                  <Typography id='utilization-graph-ibnr' variant='button' style={{ color: '#2a2b6e' }}>
                    {`PHP ${CurrencyFormatter(graph.ibnr)}`}
                  </Typography>
                </div>
                <div className={classes.item}>
                  <div className={classes.label}>
                    <span className={classes.color} style={{ background: '#f8b439' }} />
                    <Typography variant='button'>AVAILABLE</Typography>
                  </div>
                  <Typography id='utilization-graph-available' variant='button' style={{ color: '#f8b439' }}>
                    {`PHP ${CurrencyFormatter(graph.total - graph.ibnr - graph.utilized)}`}
                  </Typography>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className={classes.search} style={{ display: hideSearchBar === true ? 'none' : 'block' }}>
          <SearchTextComponent
            id='search-utilization'
            onChange={event => handleSearchChange({ event, field: 'all', type: 'all' })}
            placeholder='Search'
          />
        </div>
        <div style={{ display: hideSearchBar === false ? 'none' : 'block' }}>
          {/* TODO: replace with filler */}
          <br />
          <br />
        </div>
      </div>
      <div className={classes.table}>
        <Table
          TableContainerProps={{ style: { maxHeight: '500px', overflow: 'auto', position: 'relative' } }}
          TableProps={{ stickyHeader: true }}
          head={
            <TableRow>
              {headCells.map(headCell => (
                <TableCell key={headCell.sortField}>
                  {headCell.name}
                  {headCell.search && (
                    <TextField
                      id={`utilization-${headCell.sortField}-input`}
                      className={`${classes.search}`}
                      onChange={event => handleSearchChange({ event, field: headCell.sortField, type: 'column' })}
                    />
                  )}
                </TableCell>
              ))}

              {hideActionColumn === false && <TableCell align='center' />}
            </TableRow>
          }
          body={renderRows({ rows: rows })}
          pagination
          count={filteredRows.count}
          page={filteredRows.page}
          limit={filteredRows.limit}
          rowsPerPageOptions={filteredRows.rowsPerPageOptions}
          onChange={args => handleOnPaginate(args)}
        />
      </div>
    </div>
  )
}
