import instance from './instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
const axios = instance('/c/cm')

export default class ClaimsEncodingServices {
  static lookups ({ on, params }) {
    return axios({
      url: `/lookups/${on}`,
      method: 'GET',
      params: { limit: 10, ...params },
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  static storeLookups ({ on, data, params }) {
    return axios({
      url: `/lookups/${on}`,
      method: 'post',
      data,
      params,
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  static updateLookups ({ on, data, code, params }) {
    return axios({
      url: `/lookups/${code}/${on}`,
      method: 'put',
      data,
      params,
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }
}
