import React from 'react'
import EnhancedTable from 'SharedComponents/EnhancedTable/EnhancedTable'
import Alert from 'SharedComponents/Alert/Alert'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import Checkbox from '@material-ui/core/Checkbox'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
// import IconButton from '@material-ui/core/IconButton'
// import DeleteIcon from '@material-ui/icons/Delete'

// Services

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

// Styles
import classes from './ClaimList.module.scss'

const headCells = [
  { id: 'claim-number', numeric: false, disablePadding: false, label: 'Claim No.' },
  { id: 'loa-number', numeric: false, disablePadding: false, label: 'LOA No.' },
  { id: 'approval-number', numeric: false, disablePadding: false, label: 'Approval Code' },
  { id: 'member-name', numeric: false, disablePadding: false, label: 'Member Name' },
  { id: 'account-number', numeric: false, disablePadding: false, label: 'Account Number' },
  { id: 'availment-date', numeric: false, disablePadding: false, label: 'Availment Date' }
]
const cmHeadCells = [
  { id: 'claim-number', numeric: false, disablePadding: false, label: 'Claim No.' },
  { id: 'loa-number', numeric: false, disablePadding: false, label: 'LOA No.' },
  { id: 'approval-number', numeric: false, disablePadding: false, label: 'Approval Code' },
  { id: 'member-name', numeric: false, disablePadding: false, label: 'Member Name' },
  { id: 'client-account-name', numeric: false, disablePadding: false, label: 'Client Account Name' },
  { id: 'client-account-number', numeric: false, disablePadding: false, label: 'Client Account Number' },
  { id: 'availment-date', numeric: false, disablePadding: false, label: 'Availment Date' }
]

const prManagerheadCells = [
  { id: 'claim-number', numeric: false, disablePadding: false, label: 'Claim No.' },
  { id: 'loa-number', numeric: false, disablePadding: false, label: 'LOA No.' },
  { id: 'approval-number', numeric: false, disablePadding: false, label: 'Approval Code' },
  { id: 'member-name', numeric: false, disablePadding: false, label: 'Member Name' },
  { id: 'client-account-name', numeric: false, disablePadding: false, label: 'Client Account Name' },
  { id: 'availment-date', numeric: false, disablePadding: false, label: 'Availment Date' },
  { id: 'view-link', numeric: false, disablePadding: false, label: '' }
]

const rows = []

export default function ClaimList ({
  items,
  selectedClaimNumber,
  onItemClick,
  type,
  onConfirm,
  openDialog,
  onCancel,
  // forDeletion,
  actionItems,
  setActionItems,
  // setForDeletion,
  submittedBatch,
  setSelected,
  pagination,
  setPagination,
  isManager = false,
  selectStatus = true
}) {
  const [order, setOrder] = React.useState('asc')
  const [orderBy, setOrderBy] = React.useState('calories')
  const [claims, setClaims] = React.useState([])

  const getMessage = claims => {
    let message = 'Are you sure you want to delete this claim?'
    if (claims.claim_numbers.length > 1) {
      message = 'Are you sure you want to delete these claims?'
    }
    return message
  }

  React.useEffect(() => {
    const data = items.map(i => ({
      ...i,
      selected: false
    }))
    setClaims({
      data: data,
      loading: false,
      indeterminate: undefined,
      checked: undefined
    })
    setActionItems({ claim_numbers: [] })
    setSelected([])
  }, [items, selectStatus])

  const handleRequestSort = (event, property) => {
    const isDesc = orderBy === property && order === 'desc'
    setOrder(isDesc ? 'asc' : 'desc')
    setOrderBy(property)
  }

  const handleClick = ({ claims, index }) => {
    const _claims = {
      ...claims,
      data: claims.data.map((i, _index) => {
        if (_index === index) i.selected = !i.selected
        return i
      })
    }
    setClaims(_claims)
    changeSelectAllState({ data: _claims })
  }
  const handleSelectAllClick = event => {
    let _claims = claims

    if (_claims.data) {
      _claims.data.map(i => {
        i.selected = _claims.indeterminate ? true : !_claims.checked
        return i
      })
    }

    setClaims(_claims)
    changeSelectAllState({ data: _claims })
  }

  const changeSelectAllState = ({ data }) => {
    let _claims = data || claims
    let selected_count = 0
    let claimNumbers = []
    if (_claims.data) {
      for (let c of _claims.data) {
        if (c.selected) {
          selected_count++
          claimNumbers.push(c.claim_number)
        }
      }
    }

    let indeterminate = selected_count ? selected_count < _claims.data.length : false
    let checked = selected_count ? selected_count === _claims.data.length : false
    _claims = { ..._claims, indeterminate, checked }
    setClaims(_claims)

    setActionItems({ claim_numbers: claimNumbers })
    setSelected(state => [...claimNumbers])
  }
  const handleItemClick = index => onItemClick(index)

  return (
    <React.Fragment>
      <EnhancedTable
        headCells={type === 'Clinic Management' ? cmHeadCells : !isManager ? headCells : prManagerheadCells}
        handleRequestSort={handleRequestSort}
        handleSelectAllClick={handleSelectAllClick}
        pagination={pagination}
        rowsLength={rows.length}
        checkAll
        indeterminate={!!claims.indeterminate}
        checked={!!claims.checked}
        allowCheck
        allowCheckDisabled={submittedBatch}
        selectStatus={selectStatus}
      >
        <TableBody>
          {claims.data &&
            claims.data.map((row, index) => (
              <TableRow
                className={`${
                  selectedClaimNumber === row.claim_number ? classes.selected : classes['default-override']
                }`}
                selected={selectedClaimNumber === row.claim_number}
                role='checkbox'
                tabIndex={-1}
                key={row.claim_number}
              >
                {selectStatus ? (
                  <TableCell padding='checkbox'>
                    <Checkbox
                      disabled={submittedBatch}
                      checked={row.selected}
                      color='default'
                      onClick={event => handleClick({ index, claims })}
                    />
                  </TableCell>
                ) : null}

                <TableCell id={`claim-number-${index}`} onClick={() => handleItemClick(index)}>
                  {row.claim_number || ''}
                </TableCell>
                <TableCell id={`loa-number-${index}`} onClick={() => handleItemClick(index)}>
                  {row.loa_number || ''}
                </TableCell>
                <TableCell id={`approval-number-${index}`} onClick={() => handleItemClick(index)}>
                  {row.approval_code || ''}
                </TableCell>
                <TableCell id={`member-name-${index}`} onClick={() => handleItemClick(index)}>{`${
                  row.member ? row.member.first_name : ''
                } ${row.member ? row.member.last_name : ''}`}</TableCell>
                {type === 'Clinic Management' || type === 'PPE' ? (
                  <React.Fragment>
                    {type === 'Clinic Management' && (
                      <TableCell id={`client-account-name-${index}`} onClick={() => handleItemClick(index)}>
                        {row.client.account_name}
                      </TableCell>
                    )}
                    <TableCell id={`client-account-number-${index}`} onClick={() => handleItemClick(index)}>
                      {row.client.account_number}
                    </TableCell>
                  </React.Fragment>
                ) : !isManager ? (
                  <TableCell id={`account-number-${index}`} onClick={() => handleItemClick(index)}>
                    {row.member.account_number}
                  </TableCell>
                ) : (
                  <TableCell id={`account-name-${index}`} onClick={() => handleItemClick(index)}>
                    {row.client.account_name}
                  </TableCell>
                )}
                <TableCell id={`availment-date-${index}`} onClick={() => handleItemClick(index)}>
                  {`${DateFormatter(row.date.from)}`}
                  {type === 'In Patient' && ` - ${DateFormatter(row.date.to)}`}
                </TableCell>
                {isManager && (
                  <TableCell id={`availment-date-${index}`} onClick={() => handleItemClick(index)}>
                    <Link
                      to={`/payment-request/${row.bundle_number}/submitted/${row.batch_number}/claim/${row.claim_number}`}
                    >
                      <Typography variant='caption' className={`text-link underline`} id={`submmited-pr-${index}`}>
                        View Claim
                      </Typography>
                    </Link>
                  </TableCell>
                )}
              </TableRow>
            ))}
        </TableBody>
      </EnhancedTable>
      {!isManager ? (
        <Alert
          open={openDialog}
          message={getMessage(actionItems)}
          cancelLabel='No'
          okLabel='Yes'
          onCancel={onCancel}
          onOk={onConfirm}
        />
      ) : null}
    </React.Fragment>
  )
}
