import React from 'react'

// Components

import Table from 'SharedComponents/Table/Table'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import TextField from 'SharedComponents/TextField/CustomTextField'
import Typography from '@material-ui/core/Typography'

import SideBar from './Components/SideBar/SideBar'
import FloatingFields from './Components/FloatingFields/FloatingFields'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'

// Utilities
import { includesAny } from 'Utilities/ArrayCompare'
import { SafeAccess } from 'Utilities/SafeAccess'

// Mapper
import BenefitTable from './Mapper/BenefitTable'
import BenefitHeaders from './Mapper/BenefitHeaders'
import BenefitFooters from './Mapper/BenefitFooters'

// Styles
import classes from './Benefits.module.scss'

export default function Benefits (props) {
  // Events

  // Variables
  const { isMember, member, client } = props

  // Collections
  const [columns, setColumns] = React.useState([])
  const [rows, setRows] = React.useState([])

  // Table Variables
  const [table, setTable] = React.useState({
    count: 0,
    orderBy: 0,
    order: 'asc',
    limit: 5,
    filters: { all: '', fields: {} },
    page: 1,
    rowsPerPageOptions: [5, 10, 15, 20]
  })

  // Local Variables
  const [contractId, setContractId] = React.useState('')
  const [planType, setPlanType] = React.useState('')
  const [planPackage, setPlanPackage] = React.useState('')
  const [benefitCategory, setBenefitCategory] = React.useState('')
  const [lastRoomAndBoard, setLastRoomAndBoard] = React.useState('')

  // Nodes
  const [roomAndBoard, setRoomAndBoard] = React.useState({})
  const [coverages, setCoverages] = React.useState({})
  const [riders, setRiders] = React.useState({})

  const [rootNode, setRootNode] = React.useState({})
  const [benefitNode, setBenefitNode] = React.useState({})

  const [headers, setHeaders] = React.useState([])
  const [footers, setFooters] = React.useState([])

  React.useEffect(() => {
    if (isMember) {
      if (!member) return
      if (!member.latest_contract) return
      let { plan_type } = member.info
      const { MCT } = member.benefit_tree
      plan_type = plan_type
        .toUpperCase()
        .split(' ')
        .join('')

      setContractId(member.latest_contract._id)
      setPlanType(plan_type)
      setRootNode(MCT)

      const rnb = SafeAccess(MCT, `nodes.CT.nodes.${plan_type}.nodes.RB`)
      setRoomAndBoard(rnb)
      setCoverages(SafeAccess(MCT, `nodes.CT.nodes.${plan_type}.nodes.CV.nodes`))
      setRiders(SafeAccess(MCT, `nodes.CT.nodes.${plan_type}.nodes.RI.nodes`))

      const _keys = Object.keys(rnb.nodes).sort()
      const _name = _keys[_keys.length - 1]
      setLastRoomAndBoard(_name)
      setPlanPackage('RB')
      setBenefitCategory(_name)
    } else {
    }
  }, [isMember, member, client])

  React.useEffect(() => {
    if (planType === '' || planPackage === '' || benefitCategory === '') return
    setBenefitNode(SafeAccess(rootNode, `nodes.CT.nodes.${planType}.nodes.${planPackage}.nodes.${benefitCategory}`))
  }, [rootNode, planType, planPackage, benefitCategory])

  React.useEffect(() => {
    if (!benefitNode) return
    const _rows = getRows()
    setRows(_rows)
    setColumns(BenefitTable[planPackage === 'RB' ? 'RB' : benefitCategory] || [])
    setTable(state => ({ ...state, count: _rows.length }))

    setHeaders(getHeaders())
    setFooters(getFooters())
  }, [benefitNode])

  const getRows = () => {
    let _columns = BenefitTable[benefitCategory]
    const { nodes } = benefitNode

    let parent_keys = []
    if (benefitCategory === lastRoomAndBoard) {
      if (!benefitNode) return []

      if (Object.keys(benefitNode).length <= 0) {
        return []
      }
    } else if (lastRoomAndBoard === '') {
      return []
    } else {
      if (!nodes) return []

      if (Object.keys(nodes).length <= 0) return []

      parent_keys = Object.keys(nodes).sort()
    }

    let _rows = []
    switch (benefitCategory) {
      case lastRoomAndBoard:
        _columns = BenefitTable['RB']
        _rows = [_columns.map(col => col.data(benefitNode))]
        break
      case 'EM':
      case 'PV':
      case 'SD':
      case 'ADD':
        _rows = parent_keys.map((key, index) => _columns.map(col => col.data(nodes[key])))
        break
      default:
        _rows = parent_keys
          .map((pkey, index) =>
            Object.keys(nodes[pkey].nodes)
              .sort()
              .map(key => {
                const sub_node = nodes[pkey].nodes[key]
                sub_node.details.parent_name = nodes[pkey].details.name
                return _columns.map(col => col.data(sub_node))
              })
          )
          .reduce((acc, curr) => [...acc, ...curr], [])

        break
    }

    return _rows
  }

  const renderRows = () => {
    if (!rows) return
    const { all, fields } = table.filters

    let _rows = rows

    const ascending = (a, b) => a[table.orderBy].length - b[table.orderBy].length

    const descending = (a, b) => b[table.orderBy].length - a[table.orderBy].length

    _rows = table.order === 'asc' ? rows.sort(ascending) : rows.sort(descending)

    _rows = _rows.filter(row => row.map((col, index) => includesAny(`${col}`, all)).some(val => val))

    _rows = _rows.filter(
      row =>
        !row
          .map((col, index) => {
            if (!fields[index]) return true
            return includesAny(`${col}`, fields[index])
          })
          .some(val => val === false)
    )

    return _rows.slice((table.page - 1) * table.limit, table.page * table.limit).map((row, index) => (
      <TableRow key={`limits-row-${index}`}>
        {columns.map((col, index) => (
          <TableCell key={`limits-row-${index}-column-${col.id}`}>{row[index]}</TableCell>
        ))}
      </TableRow>
    ))
  }

  const getHeaders = () => {
    if (planPackage === 'RB') return BenefitHeaders['RB']
    return BenefitHeaders[benefitCategory]
  }

  const getFooters = () => {
    if (planPackage === 'RB') return BenefitFooters['RB']
    return BenefitFooters[benefitCategory]
  }

  const handleSidebarItemClick = args => {
    const { planPackage, benefitCategory } = args
    setPlanPackage(planPackage)
    setBenefitCategory(benefitCategory)
  }

  const handleTableSort = ({ id, index }) => {
    const order = table.order === 'asc' ? 'desc' : 'asc'
    setTable(state => ({ ...state, orderBy: index, order }))
  }

  const handleTableFilter = ({ id, value, index }) => {
    setTable(state => ({
      ...table,
      filters: { ...state.filters, fields: { ...state.filters.fields, [index]: value } }
    }))
  }

  const handleOnChangeSearchBox = value => {
    setTable(state => ({ ...table, filters: { ...state.filters, all: value } }))
  }

  const handleOnPaginate = args => {
    setTable(state => ({ ...state, ...args }))
  }

  return (
    <div className={classes.root}>
      <div className={classes.wrapper}>
        <div className={classes['left-side']}>
          <Typography className={classes.title}>Member Benefits</Typography>
          <SideBar
            contractId={contractId}
            nodes={[
              {
                id: 'room_and_board',
                label: 'Room and Board',
                type: 'item',
                planPackage: 'RB',
                benefitCategory: lastRoomAndBoard
              },
              {
                id: 'coverage',
                label: 'Coverage',
                type: 'list',
                planPackage: 'CV',
                benefits: coverages
              },
              {
                id: 'riders',
                label: 'Riders',
                type: 'list',
                planPackage: 'RI',
                benefits: riders
              }
            ]}
            onItemClick={handleSidebarItemClick}
          />
        </div>
        <div className={classes['right-side']}>
          <div className={classes.header}>
            <FloatingFields elements={headers} node={planPackage === 'RB' ? roomAndBoard : benefitNode} />
          </div>
          {planPackage !== 'RB' && (
            <div className={classes['table-header']}>
              <div className={classes.search}>
                <SearchTextComponent onChange={event => handleOnChangeSearchBox(event.target.value)} />
              </div>
              <div className={classes.contract}>
                <span>Contract Type</span>
                <span>---</span>
              </div>
            </div>
          )}

          <div className={classes.table}>
            <Table
              TableContainerProps={{ style: { maxHeight: '400px', overflow: 'auto', position: 'relative' } }}
              TableProps={{ stickyHeader: true }}
              head={
                <TableRow>
                  {columns.map((cell, index) => (
                    <TableCell key={cell.data}>
                      <TableSortLabel
                        active={table.orderBy === cell.id || table.orderBy === index}
                        direction={table.order === 'asc' ? 'asc' : 'desc'}
                        onClick={event => handleTableSort({ id: cell.id, index })}
                      >
                        {cell.name}
                      </TableSortLabel>
                      {cell.search && (
                        <TextField
                          id={`${cell.id}-input`}
                          className={`${classes.search}`}
                          onChange={event => handleTableFilter({ value: event.target.value, id: cell.id, index })}
                        />
                      )}
                    </TableCell>
                  ))}

                  <TableCell align='center' />
                </TableRow>
              }
              body={renderRows()}
              pagination
              count={table.count}
              page={table.page}
              limit={table.limit}
              rowsPerPageOptions={table.rowsPerPageOptions}
              onChange={args => handleOnPaginate(args)}
            />
          </div>
          <div className={classes.footer}>
            <FloatingFields elements={footers} node={planPackage === 'RB' ? roomAndBoard : benefitNode} />
          </div>
        </div>
      </div>
    </div>
  )
}
