export const itemFilter = args => {
  const { search, filter, data = [] } = args
  let _data = data

  if (search) {
    const _search = search.toLowerCase()
    _data = _data.filter(i => {
      return i.claim_no.toLowerCase().includes(_search) ||
        i.diagnosis.some(i => i.diagnosis.toLowerCase().includes(_search) || i.icd10_code.toLowerCase().includes(_search)) ||
        i.procedures.some(i => i.procedure_name.toLowerCase().includes(_search) || i.cpt_code.toLowerCase().includes(_search)) ||
        i.provider.doctor.some(i => i.toLowerCase().includes(_search)) ||
        i.provider.facility.some(i => i.toLowerCase().includes(_search)) ||
        i.amount.toString().toLowerCase().includes(_search) ||
        i.session.toString().toLowerCase().includes(_search) ||
        i.date.toLowerCase().includes(_search) ||
        i.type.toLowerCase().includes(_search) ||
        i.status.toLowerCase().includes(_search)
    })
  }

  if (Object.keys(filter).length > 0) {
    const _fields = Object.keys(filter)

    for (const f of _fields) {
      let value = filter[f].toLowerCase()
      if (value && f === 'diagnosis') {
        _data = _data.filter(i => {
          return i[f].some(i => i.diagnosis.toLowerCase().includes(value) || i.icd10_code.toLowerCase().includes(value))
        })
      } else if (value && f === 'procedures') {
        _data = _data.filter(i => {
          return i[f].some(i => i.procedure_name.toLowerCase().includes(value) || i.cpt_code.toLowerCase().includes(value))
        })
      } else if (value && f === 'provider') {
        _data = _data.filter(i => {
          return i[f].doctor.some(i => i.toLowerCase().includes(value)) || i[f].facility.some(i => i.toLowerCase().includes(value))
        })
      } else if (value) {
        _data = _data.filter(i => {
          return i[f].toString().toLowerCase().includes(value)
        })
      }
    }
  }
  return _data
}
