import FileSaver from 'file-saver'
import { CurrencyFormatter } from 'Utilities/TextFormatter'

export default function Download ({ fileName = 'CSV', header = [], rows = [], extension = 'csv', statics = [] }) {
  const valueSeperator = ','
  const newLine = '\r\n'
  const [c_header = [], c_footer = []] = statics

  const _rows = [
    ...c_header,
    header.reduce((joined, currentValue) => [joined, `"${currentValue}"`].join(valueSeperator), '').slice(1),
    newLine,
    ...rows.map(row =>
      [
        row.reduce((joined, currentValue) => [joined, `"${currentValue}"`].join(valueSeperator), '').slice(1),
        newLine
      ].join('')
    ),
    ...c_footer
  ]
  const blob = new Blob(_rows, { type: 'data:text/csv;charset=utf-8' })
  FileSaver.saveAs(blob, `${fileName}.${extension}`)
}
