import instance from './instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
const axios = instance('/x/cc')

export default class CustomerCareServices {
  static getMember ({ account_number }) {
    return axios({
      url: `/member`,
      method: 'GET',
      headers: { [ExcludeErrorConstants['all']]: 1 },
      params: { account_number }
    })
  }

  static getClient ({ account_number }) {
    return axios({
      url: `/client`,
      method: 'GET',
      headers: { [ExcludeErrorConstants['all']]: 1 },
      params: { account_number }
    })
  }

  static getMemberUtilizationLoa ({ account_number }) {
    return axios({
      url: `/members/${account_number}/utilization-loa`,
      method: 'GET',
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  // PRO User lookup
  static proUsersLookup ({ value, field }) {
    return axios({
      url: '/users/pro',
      method: 'GET',
      params: { [field]: value }
    })
  }

  static getMemberContract ({ mct_id }) {
    return axios({
      url: `/member/contract`,
      method: 'GET',
      headers: { [ExcludeErrorConstants['all']]: 1, Accept: 'application/pdf' },
      responseType: 'blob',
      params: { mct_id }
    })
  }

  static getClientContract ({ mct_id }) {
    return axios({
      url: `/client/contract`,
      method: 'GET',
      headers: { [ExcludeErrorConstants['all']]: 1, Accept: 'application/pdf' },
      responseType: 'blob',
      params: { mct_id }
    })
  }

  static getClientInfo ({ value, field }) {
    return axios({
      url: `/client-info`,
      method: 'GET',
      headers: { [ExcludeErrorConstants['all']]: 1 },
      params: { [field]: value }
    })
  }

  static getClients ({ where, fields, order, limit, skip, offset }) {
    return axios({
      url: `/clients`,
      method: 'GET',
      headers: { [ExcludeErrorConstants['all']]: 1 },
      params: { where, fields, order, limit, skip, offset }
    })
  }

  static getMembers ({ where, fields, order, limit, skip, offset }) {
    return axios({
      url: `/members`,
      method: 'GET',
      headers: { [ExcludeErrorConstants['all']]: 1 },
      params: { where, fields, order, limit, skip, offset }
    })
  }

  static getClientUtilizationLoa ({ client_account_no }) {
    return axios({
      url: `/client/${client_account_no}/utilization-loa`,
      method: 'GET',
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }
}
