export default function (unit) {
  if (!unit) return ''

  switch (unit.toLowerCase()) {
    case 'percentage':
    case 'percent':
      return '%'
    case 'amount_limit':
      return 'Amount Limit'
    case 'max_session':
      return 'Max Session'
    case 'max_amount':
      return 'Max Amount'
    case 'percent_mbl':
      return 'Percent of MBL'
    case 'percent_abl':
      return 'Percent of ABL'
    case 'no_visits':
      return 'No. of Visits'
    default:
      return unit
  }
}
