import constants from './../../contants'

export const resetRecon = payload => {
  return {
    for: constants.for.CLAIMS_ANALYSIS,
    type: 'RESET_RECON_NOTES',
    payload
  }
}

export const setReconNotes = payload => {
  return {
    for: constants.for.CLAIMS_ANALYSIS,
    type: 'SET_RECON_NOTES',
    payload
  }
}
